import { css } from 'lit-element';

export const basePollWidgetStyles = css`
  .widget-header {
    color: var(--poll-header-text-color);
    background-color: var(--poll-header-primary-color);
    background-image: linear-gradient(
      90deg,
      var(--poll-header-secondary-color) 0%,
      var(--poll-header-primary-color) 100%
    );
  }

  .widget-option.selected livelike-progress {
    --progress-meter-background: linear-gradient(
      90deg,
      var(--poll-selected-progress-secondary-color) 0%,
      var(--poll-selected-progress-primary-color) 100%
    );
  }

  .widget-option {
    border: 2px solid transparent;
    transition: border ease 0.3s;
  }

  .widget-select[disabled] > .widget-option {
    border: 2px solid var(--poll-disabled-border-color);
  }
  .widget-select[disabled] livelike-progress {
    --progress-meter-background: var(--poll-disabled-progress-color);
  }
  .widget-option.selected {
    border: 2px solid var(--poll-selected-border-color);
    transition: border ease 0.3s;
  }
`;