import { _$$ } from './internal/globals';
import { fetchData, saveLocalStorage, errDocLink } from '../utils';
import { IUserProfile } from '../types';

/**
 * generateProfile
 * Generates a new user profile.
 * If LiveLike has been initialized with the init function, generateProfile can be
 * called with no arguments. If it is called before init, the clientId must be passed.
 * @param {{clientId?: string}} [opts]
 * @returns {Promise<IUserProfile>} user profile object
 */

export const generateProfile = (args?: {
  clientId?: string;
  nickName?: string;
}): Promise<IUserProfile> =>
  new Promise((resolve, reject) => {
    let opts: any = { method: 'POST' };
    const clientId = args && args.clientId ? args.clientId : null;
    if (!clientId && !_$$.profileUrl)
      return errDocLink(
        'Either run LiveLike.init() or pass clientId argument.'
      );
    const name = args && args.nickName ? args.nickName : null;
    if (name) {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      opts.body = JSON.stringify({ nickname: name });
      opts.headers = headers;
    }
    const key = clientId ? 'livelike-user-auth-' + clientId : _$$.lsKey;
    const gen = (url: string) =>
      fetch(url, opts)
        .then(r => r.json())
        .then(response => {
          saveLocalStorage(key, response);
          resolve(response);
        })
        .catch(reject);

    clientId && !_$$.profileUrl
      ? fetchData.app(clientId).then(app => gen(app.profile_url))
      : gen(_$$.profileUrl);
  });
