import { html } from 'lit-element';
import { Widget } from '../base/Widget';
import { richPostStyles } from './styles/richPostStyles';
import '../../components/livelike-timer';

export class RichPostWidget extends Widget {
  static get properties() {
    return {
      content: { type: String },
    };
  }

  static get styles() {
    return [super.styles];
  }

  firstUpdated() {
    this.initPost();
    this.mode === 'timeline'
      ? this.showTimelineVotes()
      : this.waitForVotes(this.widgetPayload).then(this.shouldExpire);
  }

  initPost = () => {
    const sel = this.shadowRoot ? this.shadowRoot : this;
    const sec = sel.querySelector('#rich-post-body');
    sec.innerHTML = this.content;
    sec
      .querySelectorAll('img')
      .forEach(v => (v.style = 'width: 100%; height: auto;'));
  };

  render() {
    return html`
      ${richPostStyles}
      <article id="widget-article" class="widget rich-post-widget">
        <header id="widgetHeader" class="widget-header">
          <h1 class="widget-question" style="padding:1rem;"></h1>
        </header>
        <livelike-timer id="livelike-timer"></livelike-timer>
        <section id="rich-post-body" class="rich-post-body"></section>
      </article>
    `;
  }
}

customElements.define('livelike-rich-post', RichPostWidget);
