import { LitElement, html } from 'lit-element';
import { messageAction } from '../scripts/internal/pubnubHandler';
import { userProfile } from '../scripts/internal/globals';
import { messageItemStyles } from './styles/messageItemStyles';
import { messageReactionStyles } from './styles/messageReactionStyles';
import {
  request,
  eventBus,
  appendLocalStorage,
  formatDateTime,
  outsideClick,
} from '../utils';
import {
  renderMiniReactions,
  renderReactionButton,
  renderReactions,
} from './reaction-items';
import { moreIcon } from '../icons';
import './livelike-modal';
import './chat-moderation';

const modalType = {
  BLOCK: 'block',
  REPORT: 'report',
  ERROR: 'error',
};

class MessageItem extends LitElement {
  static get properties() {
    return {
      // Props
      item: { type: Object },
      elId: { type: String },
      dataId: { type: String },
      blockList: { type: Array },
      programId: { type: String },
      deletedMessages: { type: Array },
      langCode: { type: String },
      timeFormat: { type: Object },
      timestamps: { type: Boolean },
      messagemenus: { type: Boolean },
      debug: { type: Boolean },
      channel: { type: String },
      chatReactionPacks: { type: String },
      // State
      animate: { type: Number },
      clickedMessage: { type: String },
      reactionPack: { type: Array },
      selectedReactions: { type: String },
    };
  }

  constructor() {
    super();
    this.item = null;
    this.elId = null;
    this.dataId = null;
    this.debug = false;
    this.blockList = [];
    this.programId = null;
    this.deletedMessages = null;
    this.langCode = null;
    this.timeFormat = null;
    this.timestamps = null;
    this.messagemenus = false;
    this.channel = null;
    this.chatReactionPacks = [];
    //
    this.animate = 0;
    this.clickedMessage = null;
    this.reactionPack = [];
    this.selectedReactions = null;
  }
  createRenderRoot() {
    return this;
  }
  firstUpdated() {}

  updated(changedProps) {
    changedProps.forEach((prevProp, name) => {
      name === 'dataId' &&
        this.dataId &&
        eventBus.on(`${this.dataId}-outsideClicked`, () =>
          this.toggleReactions(this.selectedReactions)
        );
      if (
        name === 'chatReactionPacks' &&
        this.chatReactionPacks &&
        this.chatReactionPacks.length > 0
      ) {
        this.reactionPack =
          this.chatReactionPacks && this.chatReactionPacks[0]
            ? this.chatReactionPacks[0].emojis
            : [];
      }
    });
  }
  // static get styles() {
  //   return optionStyles;
  // }
  toggleModeration = messageId => {
    if (this.clickedMessage === messageId) {
      this.animate = 0;
      setTimeout(() => (this.clickedMessage = null), 300);
    } else {
      this.clickedMessage = messageId;
      setTimeout(() => (this.animate = 1), 50);
    }
  };

  findBlockedId = userId => this.blockList.find(id => id === userId);

  buildModal = (res, type) => this.displayModal(res ? type : modalType.ERROR);

  blockUser = item => {
    const blocked = this.findBlockedId(item.sender_id);
    if (!blocked) {
      let newBlockList = this.blockList.slice();
      newBlockList.splice(0, 0, item.sender_id);
      this.blockList = newBlockList;
      appendLocalStorage('blockList', newBlockList);
      this.sendReport(item).then(res => this.buildModal(res, modalType.BLOCK));
    }
  };

  reportUser = item => {
    this.toggleModeration(item.id);
    this.sendReport(item).then(res => this.buildModal(res, modalType.REPORT));
  };

  sendReport = item =>
    request.post({
      path: `programs/${this.programId}/report/`,
      data: {
        channel: this.channel,
        user_id: item.sender_id,
        nickname: item.sender_nickname,
        message_id: item.id,
        message: item.message,
      },
    });

  displayModal = messageType => {
    const modal = document.createElement('livelike-modal');
    modal.messageType = messageType;
    const ml = document
      .querySelector('livelike-chat')
      .shadowRoot.querySelector('livelike-chat-message-list');
    ml.shadowRoot.append(modal);
    this.clickedMessage = null;
    setTimeout(() => modal.remove(), 4000);
  };

  renderModeration = item => html`
    <chat-moderation
      .item=${item}
      .animate=${this.animate}
      .blockUser=${this.blockUser}
      .reportUser=${this.reportUser}
      .findBlockedId=${this.findBlockedId}
      .toggleModeration=${this.toggleModeration}
    ></chat-moderation>
  `;

  renderOptions = (activeClass, item) =>
    html`
      <button
        aria-label="Message options"
        class="more-icon ${activeClass}"
        @click=${() => this.toggleModeration(item.id)}
      >
        ${moreIcon}
      </button>
    `;

  findDeletedMessage = id =>
    this.deletedMessages.find(item => item.messageId === id);

  renderTimestamp = m => {
    const time = m.timetoken && m.timetoken.slice(0, m.timetoken.length - 4);
    const formatted = formatDateTime(
      Number(time),
      this.timeFormat,
      this.langCode
    );
    return html`
      <time
        aria-label=${formatted}
        class="message-timestamp"
        datetime="${time}"
      >
        ${formatted}
      </time>
    `;
  };

  renderDebugTimestamp = m => {
    const time = m.timetoken.slice(0, m.timetoken.length - 4);
    const DEBUG_pdt = m.program_date_time
      ? new Date(m.program_date_time).toISOString()
      : '-';
    const DEBUG_servertime = new Date(Number(time)).toISOString();

    return html`
      <div style="display:flex;flex-direction:column;">
        <span style="color:#C7704B;" class="message-timestamp">
          Program Date Time: ${DEBUG_pdt}
        </span>
        <span style="color:#C7704B;" class="message-timestamp">
          Server Time: ${DEBUG_servertime}
        </span>
      </div>
    `;
  };
  /////////////// REACTIONS ///////////////

  reactionAction = (item, reaction) => {
    const { timetoken, reactions, id } = item;
    let found;

    reactions[reaction.id] &&
      (found = reactions[reaction.id].find(r => r.uuid === userProfile.id));

    const cb = r => this.toggleReactions(id);

    found
      ? messageAction.remove(this.channel, timetoken, found.actionTimetoken, cb)
      : messageAction.add(
          this.channel,
          timetoken,
          { type: 'rc', value: reaction.id },
          cb
        );
  };

  toggleReactions = messageId => {
    if (this.selectedReactions === messageId) {
      this.animate = 0;
      setTimeout(() => {
        this.selectedReactions = null;
        outsideClick.remove();
      }, 300);
    } else {
      this.selectedReactions = messageId;
      setTimeout(() => {
        this.animate = 1;
        outsideClick.create(this.dataId);
      }, 50);
    }
  };

  //////////////////

  render() {
    const blocked = this.findBlockedId(this.item.sender_id);
    const isSingleOpen = this.clickedMessage === this.item.id;
    const activeClass = isSingleOpen ? 'active' : '';
    const deletedClass = this.findDeletedMessage(this.item.id) ? 'deleted' : '';

    return !blocked
      ? html`
          ${messageItemStyles} ${messageReactionStyles}
          <div class="message-item-container ${activeClass}">
            <div class="message-wrapper" tabindex="0">
              ${this.reactionPack && this.reactionPack.length > 0
                ? renderMiniReactions(this.item, this.reactionPack)
                : null}
              <div aria-live="polite" role="log">
                <div
                  class="sender-name"
                  aria-label=${this.item.sender_nickname}
                >
                  ${this.item.sender_nickname}
                </div>
                <div
                  aria-label=${this.item.message}
                  id="senderMessage-${this.item.id}"
                  class="sender-message ${deletedClass}"
                >
                  ${this.item.newMessage}
                </div>
                ${this.timestamps ? this.renderTimestamp(this.item) : null}
                ${this.debug ? this.renderDebugTimestamp(this.item) : null}
              </div>
              ${this.reactionPack && this.reactionPack.length > 0
                ? renderReactionButton(this.item, this.toggleReactions)
                : null}
            </div>
            ${this.selectedReactions === this.item.id
              ? renderReactions(
                  this.item,
                  this.reactionPack,
                  this.animate,
                  userProfile.id,
                  this.reactionAction
                )
              : null}
            ${this.programId && this.messagemenus
              ? this.renderOptions(activeClass, this.item)
              : null}
          </div>
          ${isSingleOpen && this.programId
            ? this.renderModeration(this.item)
            : null}
        `
      : null;
  }
}

customElements.define('livelike-message-item', MessageItem);
