import { Widget } from '../base/Widget';
import { Voteable } from '../base/Voteable';
import { followUpBaseStyles } from './styles/followUpBaseStyles';
import { predictionBaseStyles } from './styles/predictionBaseStyles';
import { textSelectStyles } from '../sharedStyles/textSelectStyles';

export class FollowUpBase extends Voteable(Widget) {
  static get styles() {
    return [
      super.styles,
      textSelectStyles,
      predictionBaseStyles,
      followUpBaseStyles,
    ];
  }
  static get properties() {
    return {
      el: { type: Object },
      question: { type: String },
      options: { type: Array },
      vote: { type: Object },
    };
  }

  firstUpdated() {
    if (this.mode === 'timeline') {
      this.showTimelineVotes();
    } else if(this.mode === 'pop-up' || this.mode === 'timeline'){
      if (this.timeout || this.widgetPayload.timeout) {
        const t = this.attachTimer(this.widgetPayload);
        this.voteResults(t.expireInMs)
          .then(() => this.displayPoints())
          .then(() => this.displayBadge())
          .then(() => {
            this.shouldExpire();
            t.timer.remove();
            this.insertReactions();
          });
      }
    }
  }
}
