import '@webcomponents/webcomponentsjs/webcomponents-bundle';
import * as Scripts from './index';
import { getProfile } from './getProfile';
import { fetchData, callbackEvent } from '../utils';
// import { polyfill } from '../utils/polyfill';
import { pubnubConnect, addListener } from './internal/pubnubHandler';
import { _$$, _config, programs, userProfile } from './internal/globals';
import { ChatContainer } from '../ChatContainer';
import { WidgetContainer } from '../WidgetContainer';
import {
  ProgramDateTimeSyncStrategy,
  HlsJsSyncStrategy,
} from '../videosync/SyncStrategy';
import {
  MessageEvents,
  AllMessageEvents,
  ReactionEvents,
} from '../constants/ChatEvents';
import { WidgetsCreated } from '../constants/WidgetEvents';
/**
 * Init function arguments
 * @param {string} [accessToken=null]
 * @param {string} clientId
 * @param {string} [videoEl]
 * @param {object} [syncStrategy]
 */
export const init = ({
  accessToken = null,
  clientId,
  syncStrategy,
  programId,
  endpoint,
  logger,
  nickName,
}) =>
  new Promise(resolve => {
    if (_$$.ready && _$$.clientId && userProfile) {
      console.error('LiveLike EngagementSDK has already been initialized!');
    }
    const LISTENER = {
      message: msg => {
        // console.log('msg', msg);
        const evt = msg.message.event;
        const payload = msg.message.payload;
        // Checks list of possible message events
        if (AllMessageEvents.some(e => e === evt)) {
          payload.timetoken = msg.timetoken;
          callbackEvent(payload, msg.channel, MessageEvents[evt]);
        }
        // Checks list of possible widget events
        WidgetsCreated.some(e => e === evt) &&
          callbackEvent(payload, msg.channel, 'widgetcreated');
      },
      messageAction: msg =>
        callbackEvent(msg.data, msg.channel, ReactionEvents[msg.event]),
    };
    // polyfill();
    endpoint && (_config.endpoint = endpoint);
    _$$.ready = false;
    _$$.logger = logger;
    _$$.clientId = clientId;
    _$$.programId = programId;
    _$$.accessToken = accessToken;
    _$$.syncStrategy = syncStrategy;
    _$$.lsKey = 'livelike-user-auth-' + clientId;
    fetchData.app(clientId).then(app => {
      _$$.stickerPacksUrl = app.sticker_packs_url;
      _$$.profileUrl = app.profile_url;
      _$$.programUrlTemplate = app.program_detail_url_template;
      _$$.chatUrlTemplate = app.chat_room_detail_url_template;
      getProfile({ accessToken, nickName }).then(profile => {
        ChatContainer; // Can't just import these at top of page with TypeScript.
        WidgetContainer;
        const pnConfig = {
          subscribeKey: app.pubnub_subscribe_key,
          publishKey: app.pubnub_publish_key,
          origin: app.pubnub_origin,
          authToken: profile.access_token,
        };

        const complete = () => {
          _$$.ready = true;
          _config.ready = true;
          resolve(profile);
        };

        const connect = () => {
          pubnubConnect(pnConfig).then(() => {
            if (_$$.pn._listenerManager._listeners.length === 0) {
              // If there are no listeners in pubnub instance, add LISTENER.
              addListener(LISTENER);
            } else {
              // If there are listeners, it could be listeners attached by user, or Widget, so check listeners.
              let matchCount = 0;
              // Loop over the listeners: Array.
              _$$.pn._listenerManager._listeners.forEach(listener => {
                // Loop through each property(fn): Function of the listener: Object.
                for (let fn in listener) {
                  // Check if function prop in pubnub instance is the same as in our LISTENER.
                  // JSON.stringify() does not work on function object properties, so String() must be used.
                  // Our LISTENER has the two - 'message' and 'messageAction' - function properties.
                  // So increment count for each identical signature
                  String(obj[fn]) === String(LISTENER[fn]) && matchCount++;
                }
                // If it contains two identical signatures, the count will be two, and it means at least one
                // of the current listeners in the pubnub instance is identical to our LISTENER. So only
                // attach a new LISTENER if it doesn't already exist in the pubnub instance.
                matchCount < 2 && addListener(LISTENER);
              });
            }
            complete();
          });
        };

        const changedProps = () => {
          // Loops through our pnConfig object and checks if any value is different
          // from the value of the same prop in the pubnub instance _config object.
          for (let val in pnConfig) {
            if (_$$.pn._config[val] !== pnConfig[val]) return true;
          }
          return false;
        };

        // If no pubnub instance exists, connect. If pubnub instance exists, but changed
        // props, connect. If pubnub instance exists, but all props are the same, complete.
        !_$$.pn ? connect() : !changedProps() ? connect() : complete();
      });
    });
  });

const LiveLike = {
  programs,
  userProfile,
  ...Scripts,
  _config,
};

// for browser
window.LiveLike = LiveLike;
window.ProgramDateTimeSyncStrategy = ProgramDateTimeSyncStrategy;
window.HlsJsSyncStrategy = HlsJsSyncStrategy;
// for node
export default LiveLike;
export { ProgramDateTimeSyncStrategy, HlsJsSyncStrategy };

