import { html } from 'lit-element';
import { QuizWidgetBase } from './QuizWidgetBase';
import { imageVotingWidgetStyles } from '../sharedStyles/imageVotingWidgetStyles';
import '../../components/livelike-option';
import '../../components/livelike-timer';
import '../../components/livelike-select';
import '../../components/livelike-progress';
import { RESULTS } from '../../constants/WidgetEvents';

export class ImageQuizWidget extends QuizWidgetBase {
  resultsEventName = RESULTS.IMAGE_QUIZ;

  static get styles() {
    return [super.styles, imageVotingWidgetStyles];
  }

  render() {
    return html`
      <article id="widget-article" class="widget ${this.articleClass()}">
        <header id="widgetHeader" class="widget-header">
          <h1 class="widget-question">${this.question}</h1>
        </header>
        <livelike-timer id="livelike-timer"></livelike-timer>
        <livelike-select class="widget-select" .disabled=${this.disabled}>
          ${this.choices.map(
            choice => html`
              <livelike-option
                class="widget-option ${this.selectedClass(
                  choice
                )} ${this.correctClass(choice)}"
                .value=${choice.id}
                .selected=${this.isSelected(choice)}
                @click=${() => this.voteAction(choice)}
              >
                <div class="container">
                  <div class="image-description-wrapper">
                    <div class="widget-option-description image-description">
                      ${choice.description}
                    </div>
                    <livelike-progress
                      class="widget-option-progress"
                      value=${this.votePercentage(choice.answer_count)}
                    ></livelike-progress>
                  </div>

                  <div
                    class="widget-option-percentage"
                    style="visibility: ${this.phase === 'voting'
                      ? 'hidden'
                      : 'visible'}"
                  >
                    ${this.votePercentage(choice.answer_count) + '%'}
                  </div>
                </div>
                <div class="widget-figure">
                  <img
                    class="widget-option-image"
                    src=${choice.image_url}
                    alt=${choice.description}
                  />
                </div>
              </livelike-option>
            `
          )}
        </livelike-select>
      </article>
    `;
  }
}

customElements.define('livelike-image-quiz', ImageQuizWidget);
