import { callbackEvent, getChannel } from '../utils';
import { publishMessage, generateUUID } from './internal/pubnubHandler';
import { programs, userProfile } from './internal/globals';

/**
 * sendMessage
 * Send user message to channel.
 * @param {{message: string, roomId: string, timestamp: string}}
 */
export const sendMessage = ({ message, roomId, timestamp }) => {

  const messageObj = {
    event: 'message-created',
    payload: {
      message,
      id: generateUUID(),
      sender_id: userProfile.id,
      sender_nickname: userProfile.nickname,
      sender_image_url:  programs[roomId]?.image_url || null,
      sender_profile_url: userProfile.url,
      badge_image_url: userProfile.badges?.[0]?.file || null,
      created_at: new Date().toISOString(),
      program_date_time: timestamp || null,
    },
  };

  const publish = channel =>
    publishMessage({ message: messageObj, channel })
      .then(() => callbackEvent(messageObj.payload, channel, 'messagesent'))
      .catch(() => callbackEvent('Message failed to send.', channel, 'messagefailed'));

  const channel = programs.channels[roomId];
  channel ? publish(channel) : getChannel(roomId).then(channel => publish(channel));
};
