import { html } from 'lit-element';
import { PredictionBase } from './PredictionBase';
import { imageVotingWidgetStyles } from '../sharedStyles/imageVotingWidgetStyles';
import '../../components/livelike-option';
import '../../components/livelike-timer';
import '../../components/livelike-select';

export class ImagePredictionWidget extends PredictionBase {
  static get styles() {
    return [super.styles, imageVotingWidgetStyles];
  }

  render() {
   return html`
      <article id="widget-article" class="widget phase-${this.phase}">
        <header id="widgetHeader" class="widget-header">
          <h1 class="widget-question">${this.question}</h1>
        </header>
        <livelike-timer id="livelike-timer"></livelike-timer>
        <livelike-select
          class="widget-select prediction-options"
          .disabled=${this.disabled}
        >
          ${this.options.map(
            option => html`
              <livelike-option
                class="widget-option ${this.selectedClass(option)}"
                .value=${option.id}
                .selected=${this.isSelected(option)}
                @click=${() => this.voteAction(option)}
              >
                <div class="container">
                  <div>
                    <div class="widget-option-description image-description">
                      ${option.description}
                    </div>
                  </div>
                  <div class="widget-option-percentage"></div>
                </div>
                <div class="widget-figure">
                  <img
                    class="widget-option-image"
                    src=${option.image_url}
                    alt=${option.description}
                  />
                </div>
              </livelike-option>
            `
          )}
        </livelike-select>
        <div class="widget-confirmation">
          ${this.confirmation_message}
        </div>
      </article>
    `;
  }
}

customElements.define('livelike-image-prediction', ImagePredictionWidget);
