import { html } from 'lit-element';
import { Widget } from '../base/Widget';
import { alertWidgetStyles } from './styles/alertWidgetStyles';

export class AlertWidget extends Widget {
  static get properties() {
    const props = Object.assign({}, super.properties, {
      title: { type: String },
      text: { type: String },
      image_url: { type: String },
      link_url: { type: String },
      link_label: { type: String },
    });
    return props;
  }

  firstUpdated() {
    this.mode === 'timeline'
      ? this.showTimelineVotes()
      : this.waitForVotes(this.widgetPayload).then(this.shouldExpire);
  }

  static get styles() {
    return [super.styles, alertWidgetStyles];
  }

  get hasCaptionAndMedia() {
    return !!this.text && !!this.image_url;
  }

  get hasOnlyMedia() {
    return !this.text && !!this.image_url;
  }

  get hasOnlyCaption() {
    return !!this.text && !this.image_url;
  }

  render() {
    return html`
      <article id="widget-article" class="widget">
        <header
          id="widgetHeader"
          class="widget-header ${this.title ? '' : 'no-title'}"
        >
          <h1 class="widget-question">${this.title ? this.title : ''}</h1>
        </header>

        <livelike-timer id="livelike-timer"></livelike-timer>

        <div class="widget-alert-body">
          ${this.hasCaptionAndMedia
            ? html`
                <figure class="widget-captioned-media">
                  ${this.text &&
                    html`
                      <figcaption class="widget-caption media-caption">
                        ${this.text}
                      </figcaption>
                    `}
                  ${this.image_url &&
                    html`
                      <img
                        class="widget-media"
                        src=${this.image_url}
                        alt=${this.text}
                      />
                    `}
                </figure>
              `
            : this.hasOnlyMedia
            ? html`
                <div class="widget-media">
                  <img src=${this.image_url} />
                </div>
              `
            : this.hasOnlyCaption
            ? html`
                <div class="widget-caption-container">
                  <span class="widget-caption">${this.text}</span>
                </div>
              `
            : null}
          ${this.link_url &&
            html`
              <a class="widget-link" href=${this.link_url} target="_blank"
                >${this.link_label}</a
              >
            `}
        </div>
      </article>
    `;
  }
}

customElements.define('livelike-alert', AlertWidget);
