import { html } from 'lit-element';
import { CheerMeterBase } from './CheerMeterBase';
import '../../components/livelike-timer';
import '../../components/livelike-select';
import { vsIcon } from '../../icons/';
import { widgetState as ws } from '../../constants/WidgetStates';
import { RESULTS } from '../../constants/WidgetEvents';
export class CheerMeterWidget extends CheerMeterBase {
  resultsEventName = RESULTS.CHEER_METER;

  render() {
    const isSelected = this.selectedOption && this.selectedOption.id;
    const getActive = id => isSelected && this.selectedOption.id === id;
    const activeClass = id => (getActive(id) ? ws.ACTIVE : ws.INACTIVE);
    return html`
      <article id="widget-article" class="widget ${this.articleClass()}">
        <header id="widgetHeader" class="widget-header">
          <h1 class="widget-question">${this.question}</h1>
        </header>
        <livelike-timer id="livelike-timer"></livelike-timer>

        <div class="cheer-versus ${isSelected ? ws.INACTIVE : ws.ACTIVE}">
            ${vsIcon}
        </div>

        <livelike-select class="widget-select" .disabled=${this.disabled}>
          <div class="cheer-description-body">
            ${this.options.map((option, idx) => {
              return html`
                <div
                  class="cheer-description-container"
                  style="width:${this.votePercentage(option.vote_count, 50)}%"
                >
                  <div class="cheer-description-wrap description${idx}">
                    <div>${option.description}</div>
                  </div>
                </div>
              `;
            })}
          </div>

          <div class="cheer-image-body">
            ${this.options.map((option, idx) => {
              const isActive = isSelected ? activeClass(option.id) : '';
              const isDisabled = this.disabled ? 'disabled' : 'enabled';
              return html`
                <div
                  class="cheer-image-container ${isActive} ${isDisabled}"
                  @click=${() =>
                    this.disabled ? null : this.cheerAction(option)}
                >
                  <div
                    id="image${idx}"
                    class="cheer-image-wrap image${idx} ${isActive} ${isDisabled}"
                  >
                    <img
                      class="cheer-image image${idx} ${isActive} ${isDisabled}"
                      src=${option.image_url}
                    />
                  </div>
                </div>
              `;
            })}
          </div>
        </livelike-select>

        <div class="cheer-score ${isSelected ? ws.ACTIVE : ws.INACTIVE}">
          MY SCORE
          <span class="cheer-score-number">${this.voteCount}</span>
        </div>
      </article>
    `;
  }
}

customElements.define('livelike-cheer-meter', CheerMeterWidget);
