import { LitElement, html } from 'lit-element';
import { chatAutoConnect } from './scripts/internal/chatAutoConnect';
import { ContentSession } from './ContentSession';
import { chatStyles } from './chatStyles';
import { messageList } from './scripts/messageList';
import { userProfile, _$$ } from './scripts/internal/globals';
import {
  getStickerMessage,
  getImageMessage,
} from './scripts/internal/convertMessages';
import {
  eventBus,
  eventDispatcher,
  immuteAppend,
  errDocLink,
  getLang,
  isArray,
  pageVisHandler,
  selfChildErr,
} from './utils';
import './components/livelike-chat-message-list';
import './components/livelike-chat-composer';

export class ChatContainer extends LitElement {
  static get properties() {
    return {
      dataId: { type: String },
      debug: { type: Boolean },
      channel: { type: String },
      loaded: { type: Boolean },
      profile: { type: Object },
      stickers: { type: Array },
      clientid: { type: String },
      langCode: { type: String },
      blockList: { type: Array },
      timeFormat: { type: Object },
      messageList: { type: Array },
      timestamps: { type: Boolean },
      placeHolder: { type: String },
      stickerPacks: { type: Array },
      initialLoad: { type: Boolean },
      messagemenus: { type: Boolean },
      chatSession: { type: Function },
      deletedMessages: { type: Array },
      messagesLoaded: { type: Boolean },
      roomid: { type: String, reflect: true },
      programid: { type: String, reflect: true },
      chatReactionPacks: { type: Array },
    };
  }

  constructor() {
    super();
    this.dataId = null;
    this.debug = false;
    this.channel = null;
    this.loaded = false;
    this.profile = userProfile;
    this.stickers = [];
    this.langCode = 'en-US';
    this.blockList = [];
    this.timeFormat = {
      month: 'short',
      day: 'numeric',
      hour: 'numeric',
      minute: 'numeric',
    };
    this.messageList = ['Waiting'];
    this.timestamps = false;
    this.placeHolder = 'Add to the conversation';
    this.stickerPacks = [];
    this.initialLoad = false;
    this.messagemenus = false;
    this.chatSession = null;
    this.deletedMessages = [];
    this.messagesLoaded = false;
    this.reactionPack = [];
    this.syncStrategy = null;
    this.chatReactionPacks = [];
    // document.addEventListener('visibilitychange', changePageVis, false);
    this.addEventListener('messagehistory', () => (this.messagesLoaded = true));
    eventBus.on('updateProfile', e => (this.profile = e.profile));
  }
  // createRenderRoot() {
  //   return this;
  // }
  firstUpdated() {
    this.parentElement &&
      this.parentElement.localName === this.localName &&
      selfChildErr(this);
    pageVisHandler();
    getLang(this).then(lang => lang && (this.langCode = lang));
  }

  updated(changedProps) {
    changedProps.forEach((prevProp, name) => {
      if (name === 'channel' && this.channel) {
        eventBus.on(this.channel, this.demultiplex);
        eventBus.off(prevProp, this.demultiplex);
      }

      name === 'loaded' && prevProp === false && this.getMessages();

      const programId =
        name === 'programid' && this.programid && prevProp !== this.programid;

      const roomId =
        name === 'roomid' &&
        this.roomid &&
        prevProp !== this.roomid &&
        this.roomid !== this.elId;

      if (roomId || programId) {
        if (prevProp !== undefined && prevProp !== null) {
          eventDispatcher(this, 'roomexited', { roomId: this.elId });
        }
        this.connectChat('updated');
      }
    });
  }

  connectChat = () => {
    if (!this.programid && !this.roomid && !_$$.programId) {
      return errDocLink(
        'Program id / Room id missing from chat element.',
        'program-ids'
      );
    }
    if (_$$.ready && userProfile && userProfile.id) {
      this.loaded = false;
      this.messagesLoaded = false;
      chatAutoConnect(this, userProfile).then(() => {
        this.loaded = true;
        this.initialLoad = true;
        userProfile.blocklist && (this.blockList = userProfile.blockList);
        this.chatSession = ContentSession(this);
        eventDispatcher(this, 'roomentered', { room: this, roomId: this.elId });
      });
    } else {
      setTimeout(this.connectChat, 1000);
    }
  };

  demultiplex = e => {
    e.event === 'messagesent' && this.messageSent(e);
    e.event === 'messagereceived' && this.messageReceived(e);
    e.event === 'messagedeleted' && this.messageDeleted(e);
    (e.event === 'reactionremoved' || e.event === 'reactionadded') &&
      this.updateReaction(e);
    eventDispatcher(this, e.event, { message: e.message, roomId: this.elId });
  };

  getMessages = () =>
    messageList(this.elId).then(messages => {
      messages && messages.length === 0
        ? (this.messageList = ['No Messages'])
        : (this.messageList = messages);
      eventDispatcher(this, 'messagehistory', { messages: this.messageList });
    });

  updateReaction = e => {
    const { messageTimetoken, actionTimetoken, uuid, value } = e.message;
    if (this.messageList && isArray(this.messageList)) {
      const updatedList = [...this.messageList];
      const idx = updatedList.findIndex(m => m.timetoken === messageTimetoken);
      if (idx !== -1) {
        let reaction = updatedList[idx].reactions[value];
        if (e.event === 'reactionadded') {
          !reaction && (updatedList[idx].reactions[value] = []);
          updatedList[idx].reactions[value].push({ uuid, actionTimetoken });
        }
        if (e.event === 'reactionremoved' && reaction) {
          const rIdx = reaction.findIndex(
            r => r.actionTimetoken === actionTimetoken
          );
          rIdx !== -1 && reaction.splice(rIdx, 1);
        }
        this.messageList = updatedList;
      }
    }
  };

  messageReceived = e =>
    this.chatSession && this.chatSession.enqueueItem(e.message, this.updateEl);

  messageSent = () =>
    setTimeout(() => eventBus.emit(`${this.dataId}-scrollDown`), 250);

  messageDeleted = e => {
    const idx = this.messageList.findIndex(
      v => String(e.message.id) === String(v.id)
    );
    if (idx !== -1) {
      const deleted = this.messageList[idx];
      deleted.message = 'This message has been deleted';
      this.deletedMessages = immuteAppend(this.deletedMessages, deleted);
      this.requestUpdate();
    }
  };

  insertMessage = message =>
    (this.messageList = immuteAppend(this.messageList, message));

  updateEl = () => {
    if (this.chatSession && this.chatSession.hasNextItem()) {
      if (this.chatSession.isItemReady()) {
        const m = this.chatSession.dequeueItem();
        if (m) {
          m.reactions = {};
          let newMessage = m.image_url
            ? getImageMessage(m)
            : getStickerMessage(m.message, this.stickers);
          const message = { ...m, newMessage };
          this.messageList && this.messageList[0] === 'No Messages'
            ? (this.messageList = [message])
            : this.insertMessage(message);
        }
      } else {
        setTimeout(this.updateEl, 1000);
      }
    }
  };

  static get styles() {
    return [chatStyles];
  }

  renderMessageList = () => {
    return this.messageList && this.messageList[0] === 'No Messages'
      ? html`
          <slot name="message-list-empty" slot="message-list-empty"></slot>
        `
      : html`
          <livelike-chat-message-list
            .messageList=${this.messageList}
            .deletedMessages=${this.deletedMessages}
            .timeFormat=${this.timeFormat}
            .langCode=${this.langCode}
            .timestamps=${this.timestamps}
            .blockList=${this.blockList}
            .stickers=${this.stickers}
            .programId=${this.programid}
            .elId=${this.elId}
            .dataId=${this.dataId}
            .messagemenus=${this.messagemenus}
            .debug=${this.debug}
            .messagesLoaded=${this.messagesLoaded}
            .channel=${this.channel}
            .chatReactionPacks=${this.chatReactionPacks}
          >
            <slot
              name="message-list-loading"
              slot="message-list-loading"
            ></slot>
          </livelike-chat-message-list>
        `;
  };

  render() {
    return html`
      <style>
        :host {
          display: flex;
          flex-direction: column;
        }
        livelike-chat {
          display: flex;
          flex-direction: column;
        }
      </style>
      ${this.initialLoad &&
      userProfile &&
      userProfile.id &&
      (this.programid || this.roomid)
        ? html`
            ${this.renderMessageList()}
            <livelike-chat-composer
              .elId=${this.elId}
              .dataId=${this.dataId}
              .stickerPacks=${this.stickerPacks}
              .placeHolder=${this.placeHolder}
              .syncStrategy=${this.syncStrategy}
            ></livelike-chat-composer>
          `
        : html`
            <slot name="chat-element-loading"></slot>
          `}
    `;
  }
}

customElements.define('livelike-chat', ChatContainer);

