import { component, html } from 'haunted';
import { bylinesStyles } from './styles/bylinesStyles';
import { formatDateTime } from '../utils';

function BylinesWrapper({
  hasAuthor,
  hasTimestamp,
  publishedTime,
  timeFormat,
  langCode,
  name,
}) {

  const getTimestamp = () => {
    const formatted = formatDateTime(publishedTime, timeFormat, langCode)
    // return formatted.date + formatted.time;
    return formatted
  }

  const renderTimestamp = () => html`
    <div class="widget-timestamp-container">
      <time class="widget-timestamp-time" datetime="${publishedTime}">
        ${getTimestamp()}
      </time>
    </div>
  `;

  const renderAuthor = () => html`
    <div class="widget-author-container">
      <span class="widget-author-name">${name}</span>
    </div>
  `;

  return html`
    ${bylinesStyles}
    <div class="bylines-wrapper">
      ${hasAuthor ? renderAuthor() : null}
      ${hasAuthor && hasTimestamp
        ? html`
            &nbsp;&ndash;&nbsp;
          `
        : null}
      ${hasTimestamp ? renderTimestamp() : null}
    </div>
  `;
}

customElements.define(
  'bylines-wrapper',
  component(BylinesWrapper, {
    props: {
      author: { type: String },
      timeline: { type: String },
      publishedTime: { type: String },
      name: { type: String },
      timeFormat: { type: String},
      hasTimestamp: { type: Boolean}
    },
    useShadowDOM: false,
  })
);

