import * as Scripts from '../index';
import { getSyncStrategy } from './getSyncStrategy';
import { _$$, _config, programs, userProfile } from './globals';
import { generateUUID } from './pubnubHandler';
import { fetchData } from '../../utils';

// Shared data between both Chat and Widget Element
export const connectionBase = async (el, type) => {
  let prog,
    prid,
    roomId = el.roomid;

  const programId = el.programid || _$$.programId;
  programId && (prog = await fetchData.program(programId));

  if (prog) {
    programs.rewards = prog.rewards_type;
    type === 'chatEl' && !roomId && (prid = prog.default_chat_room.id);
  }

  const elId = type === 'widgetEl' ? programId : roomId || prid;
  el.elId = elId;

  const dataId = generateUUID();
  el.dataId = dataId;

  !programs[elId] && (programs[elId] = {});

  el.loaded = false;
  el.programId = programId;
  el.syncStrategy = await getSyncStrategy(_$$.syncStrategy);

  window.LiveLike = {
    programs,
    userProfile,
    ...Scripts,
    _config,
    // _$$,
  };

  return { dataId, roomId, prog };
};
