import { html } from 'lit-element';

export const emojiSliderStyles = html`
  <style>
    .widget-select {
      background-color: var(--widget-background-color);
      border-radius: var(--widget-select-border-radius);
      transform: translateY(-2.25px);
      padding: 0 1rem;
    }

    form {
      margin-block-end: 0;
      --x: calc(var(--val) / 100 * (100% - 1em));
      position: relative;
      color: var(--image-slider-track-color-secondary);
      /* fallback for no CSS vars */
      color: HSL(
        calc((1 - var(--val) / 100) * 22 + var(--val) / 100 * 49),
        calc((1 - var(--val) / 100) * 100% + var(--val) / 100 * 100%),
        calc((1 - var(--val) / 100) * 54% + var(--val) / 100 * 50%)
      );
    }

    input[id='emoji-slider'] {
      display: block;
      width: 100%;
      height: 5em;
      background: none;
      cursor: pointer;
    }

    input[id='emoji-slider']:focus {
      outline: none;
    }

    input[id='emoji-slider'],
    input[id='emoji-slider']::-webkit-slider-thumb,
    input[id='emoji-slider']::-webkit-slider-runnable-track {
      -webkit-appearance: none;
    }

    input[id='emoji-slider']::-webkit-slider-runnable-track {
      border: none;
      width: 100%;
      height: var(--image-slider-track-height);
      border-radius: var(--image-slider-track-border-radius);
      background: var(--image-slider-track-color-secondary);
      /* works for no CSS vars here */
      /* background-image: var(--image-slider-track-background); */
      background-image: linear-gradient(
        90deg,
        var(--image-slider-track-color-primary),
        var(--image-slider-track-color-secondary) var(--x),
        transparent 0
      );
    }
    input[id='emoji-slider']::-moz-range-track {
      border: none;
      width: 100%;
      height: var(--image-slider-track-height);
      border-radius: var(--image-slider-track-border-radius);
      background: var(--image-slider-track-color-secondary);
      /* works for no CSS vars here */
      /* background-image: var(--image-slider-track-background);
      background-image: linear-gradient(
        90deg,
        #ff6a13,
        currentcolor var(--x),
        transparent 0
      ); */
      background-image: linear-gradient(
        90deg,
        var(--image-slider-track-color-primary),
        var(--image-slider-track-color-secondary) var(--x),
        transparent 0
      );
    }
    input[id='emoji-slider']::-ms-track {
      border: none;
      width: 100%;
      height: var(--image-slider-track-height);
      border-radius: var(--image-slider-track-border-radius);
      background: var(--image-slider-track-color-secondary);
      /* works for no CSS vars here */
      /* background-image: var(--image-slider-track-background);
      background-image: linear-gradient(
        90deg,
        #ff6a13,
        currentcolor var(--x),
        transparent 0
      ); */

      color: transparent;
    }

    input[id='emoji-slider']::-ms-fill-lower {
      border-radius: 0.25em 0 0 0.25em;
      background-image: linear-gradient(90deg, var(--image-slider-track-color-primary), var(--image-slider-track-color-secondary));
      /* no var fallback */
      background-image: linear-gradient(90deg, var(--image-slider-track-color-primary), var(--c, var(--image-slider-track-color-secondary)));
    }

    input[id='emoji-slider']::-webkit-slider-thumb {
      border: none;
      width: 1em;
      height: 1em;
      border-radius: 50%;
      /* some visual fallback if no emoji */
      box-shadow: inset 0 0 0 2px #eee;
      /* background: #222; */
      margin-top: -0.25em;
    }
    input[id='emoji-slider']::-moz-range-thumb {
      border: none;
      width: 1em;
      height: 1em;
      border-radius: 50%;
      /* some visual fallback if no emoji */
      /* box-shadow: inset 0 0 0 2px #eee; */
      /* background: #222; */
    }
    input[id='emoji-slider']::-ms-thumb {
      border: none;
      width: 1em;
      height: 1em;
      border-radius: 50%;
      /* some visual fallback if no emoji */
      /* box-shadow: inset 0 0 0 2px #eee; */
      /* background: #222; */
      margin-top: 0;
    }

    .slider-image {
      height: 50px;
      z-index: 10;
    }

    output[for='emoji-slider'] {
      /* for browsers that don't support vars,
      * so won't place the emoji correctly even if
      * they display it and not show a rectangle */
      display: none;
      position: absolute;
      bottom: 0.1em;
      left: var(--x);
      pointer-events: none;
    }

    /* if CSS variables are supported */
    @supports (margin: var(--m, 1em)) {

      output[for='emoji-slider'] {
        display: flex;
      }
    }

    .slider-result-container {
      position: relative;
      z-index:1000;
    }

    .slider-result-mark{
      background: rgb(233,0,0);
      background: -moz-linear-gradient(129deg, rgba(233,0,0,1) 33%, rgba(99,0,70,1) 100%);
      background: -webkit-linear-gradient(129deg, rgba(233,0,0,1) 33%, rgba(99,0,70,1) 100%);
      background: linear-gradient(129deg, rgba(233,0,0,1) 33%, rgba(99,0,70,1) 100%);
      width: 1rem;
      height: 1rem;
      bottom: 1.1rem;
      position: absolute;
      background: #d23a3a;
      border: 3px solid white;
      border-radius: 1rem;
      bottom: 1.5rem;
      z-index: 1000;
    }
  </style>
`;
