import { css } from 'lit-element';

export const widgetBaseStyles = css`
  :host([hidden]) {
    display: none;
  }

  .widget {
    color: var(--widget-color);
    width: var(--widget-width);
    margin: var(--widget-margin);
    position: var(--widget-position);
    border: var(--widget-border);
    border-radius: var(--widget-border-radius);
  }

  .widget-header {
    background-color: black;
    display: flex;
    border-radius: var(--widget-header-border-radius);
    padding-right: 1rem;
  }

  .widget-question {
    flex-grow: 1;
    font-size: inherit;
    margin: 0;
    padding: 0.5rem 0rem 0.5rem 1rem;
    overflow-wrap: break-word;
    width: 50%;
  }

  .widget-dismiss {
    background: none;
    border: none;
    padding-right: 0;
    margin-right: -0.5rem;
  }

  .widget-dismiss:hover {
    cursor: pointer;
  }

  livelike-timer {
    width: 100%;
    position: absolute;
    transform: translateY(-3px);
  }

  points-display {
    display: flex;
    justify-content: center;
  }
`;
