import { css } from 'lit-element';

export const appStyles = css`

    :host {
      /* COLORS */

      /* Body */
      --widget-margin: var(--theme-widget-margin);
      --widget-width: var(--theme-widget-width);
      --widget-color: var(--theme-widget-color, #fff);
      --widget-position: var(--theme-widget-position, relative);
      --widget-border: var(--theme-widget-border);
      --widget-border-radius: var(--theme-widget-border-radius, 6px);

      /* Background */
      --widget-background-color: var(--theme-widget-background-color, #333);
      --correct-primary-color: var(--theme-correct-primary-color, #81ca00);
      --correct-secondary-color: var(--theme-correct-secondary-color, #429321);
      --correct-border-color: var(--theme-correct-border-color, #81ca00);
      --correct-selected-border-color: var(--theme-correct-selected-border-color, #81ca00);
      --correct-progress-primary-color: var(--theme-correct-progress-primary-color, #81ca00);
      --correct-progress-secondary-color: var(--theme-correct-progress-secondary-color, #429321);
      --correct-selected-progress-primary-color: var(--theme-correct-selected-progress-primary-color, #81ca00);
      --correct-selected-progress-secondary-color: var(--theme-correct-selected-progress-secondary-color, #429321);
      --incorrect-primary-color: var(--theme-incorrect-primary-color, #dc0028);
      --incorrect-secondary-color: var(--theme-incorrect-secondary-color, #d0021b);
      --incorrect-selected-border-color: var(--theme-incorrect-selected-border-color, #dc0028);
      --incorrect-selected-progress-primary-color: var(--theme-incorrect-selected-progress-primary-color, #dc0028);
      --incorrect-selected-progress-secondary-color: var(--theme-incorrect-selected-progress-secondary-color, #d0021b);
      --correct-option-text-color: var(--theme-correct-option-text-color);
      --correct-selected-option-text-color: var(--theme-correct-selected-option-text-color);
      --incorrect-selected-option-text-color: var(--theme-incorrect-selected-option-text-color);

      --option-selected-background: var(--theme-option-selected-background);

      /* WIDGETS */
      --timer-height: var(--theme-timer-height, 3px);
      --timer-background-color: var(--theme-timer-background-color, #fff);

      --alert-header-background: var(--theme-alert-header-background);
      --alert-header-text-color: var(--theme-alert-header-text-color);
      --alert-primary-color: var(--theme-alert-primary-color, #9f041b);
      --alert-secondary-color: var(--theme-alert-secondary-color, #f5515f);
      --alert-badge-border-radius: var(--theme-alert-badge-border-radius, 6px);
      --alert-link-background: var(--theme-alert-link-background, rgba(255, 255, 255, 0.06));
      --alert-link-text-color: var(--theme-alert-link-text-color, inherit);

      --prediction-header-text-color: var(--theme-prediction-header-text-color);
      --prediction-header-primary-color: var(--theme-prediction-header-primary-color, #0096c8);
      --prediction-header-secondary-color: var(--theme-prediction-header-secondary-color, #003264);
      --prediction-selected-border-color: var(--theme-prediction-selected-border-color, #0096c8);

      --prediction-confirmation-background: var(--theme-prediction-confirmation-background, rgba(0, 0, 0, 0.6));
      --prediction-confirmation-border-radius: var(--theme-prediction-confirmation-border-radius, 6px);
      --prediction-confirmation-display: var(--theme-prediction-confirmation-display, flex);

      --poll-header-text-color: var(--theme-poll-header-text-color);
      --poll-header-primary-color: var(--theme-poll-header-primary-color, #C86DD7);
      --poll-header-secondary-color: var(--theme-poll-header-secondary-color, #3023AE);
      --poll-disabled-border-color: var(--theme-poll-disabled-border-color);
      --poll-disabled-progress-color: var(--theme-poll-disabled-progress-color, rgba(255, 255, 255, 0.12));
      --poll-selected-border-color: var(--theme-poll-selected-border-color, #C86DD7);
      --poll-selected-progress-primary-color: var(--theme-poll-selected-progress-primary-color, #C86DD7);
      --poll-selected-progress-secondary-color: var(--theme-poll-selected-progress-secondary-color, #3023AE);

      --quiz-header-text-color: var(--theme-quiz-header-text-color);
      --quiz-header-primary-color: var(--theme-quiz-header-primary-color, #FFB400);
      --quiz-header-secondary-color: var(--theme-quiz-header-secondary-color, #F76B1C);
      --quiz-selected-border-color: var(--theme-quiz-selected-border-color, #FFB400);
      --quiz-selected-progress-primary-color: var(--theme-quiz-selected-progress-primary-color, #FFB400);
      --quiz-selected-progress-secondary-color: var(--theme-quiz-selected-progress-secondary-color, #F76B1C);

      --social-embed-primary-color: var(--theme-social-embed-primary-color, #111);
      --social-embed-secondary-color: var(--theme-social-embed-secondary-color, #111);
      --social-embed-padding: var(--theme-social-embed-padding);
      --social-embed-background-color: var(--theme-social-embed-background-color, #333);
      --social-embed-border-radius: var(--theme-social-embed-border-radius);
      --social-embed-arrow-color: var(--theme-social-embed-arrow-color, #fff);
      --social-embed-arrow-shadow: var(--theme-social-embed-arrow-shadow, drop-shadow( 3px 3px 2px rgba(20, 20, 20, .7)));

      --rich-post-background-color: var(--theme-rich-post-background-color, #333);
      --rich-post-margin: var(--theme-rich-post-margin);
      --rich-post-padding: var(--theme-rich-post-padding, 1.25rem 1rem 1rem);

      --image-slider-header-text-color: var(--theme-image-slider-header-text-color);
      --image-slider-primary-color: var(--theme-image-slider-primary-color, #111);
      --image-slider-secondary-color: var(--theme-image-slider-secondary-color, #111);
      --image-slider-track-height: var(--theme-image-slider-track-height, 1.125rem);
      --image-slider-track-border-radius: var(--theme-image-slider-track-border-radius, 1.5rem);
      --image-slider-track-color-primary: var(--theme-image-slider-track-color-primary, #ff6a13);
      --image-slider-track-color-secondary: var(--theme-image-slider-track-color-secondary, #888);

      --cheer-meter-header-text-color: var(--theme-cheer-meter-header-text-color);
      --cheer-meter-side-one-header-color: var(--theme-cheer-meter-side-one-header-color, linear-gradient(to left, #3023ae, #53a0fd));
      --cheer-meter-side-one-text-color: var(--theme-cheer-meter-side-one-text-color, inherit);
      --cheer-meter-side-two-header-color: var(--theme-cheer-meter-side-two-header-color, linear-gradient(to right, #9f041b, #f5515f));
      --cheer-meter-side-two-text-color: var(--theme-cheer-meter-side-two-text-color, inherit);

      --widget-header-border-radius: var(--theme-widget-header-border-radius, 6px 6px 0 0);
      --widget-select-border-radius: var(--theme-widget-select-border-radius, 0 0 6px 6px);
      --widget-option-border-radius: var(--theme-widget-option-border-radius, 6px);

      /* Progress */
      --progress-height: var(--theme-progress-height, 100%);
      --progress-width: var(--theme-progress-width, 100%);
      --progress-box-sizing: var(--theme-progress-box-sizing);
      --progress-position: var(--theme-progress-position, relative);
      --progress-padding: var(--theme-progress-padding);

      /* Progress Meter */
      --progress-meter-border-radius: var(--theme-progress-meter-border-radius, 3px);
      --progress-meter-height: var(--theme-progress-meter-height, 100%);
      --progress-meter-width: var(--theme-progress-meter-width, 0);
      --progress-meter-position: var(--theme-progress-meter-position, absolute);
      --progress-meter-background: var(--theme-progress-meter-background, rgba(255, 255, 255, 0.12));

      /* Widget Options */
      --widget-option-border-bottom: var(--theme-widget-option-border-bottom);
      --widget-option-percentage-font-size: var(--theme-widget-option-percentage-font-size, 1rem);
      --widget-option-percentage-font-weight: var(--theme-widget-option-percentage-font-weight, bold);
      --widget-option-description-font-size: var(--theme-widget-option-description-font-size);
      --widget-option-description-font-weight: var(--theme-widget-option-description-font-weight);
      --widget-option-description-margin: var(--theme-widget-option-description-margin);

      /* Widget Reactions */
      --widget-reaction-container-margin: var(--theme-widget-reaction-container-margin);
      --widget-reaction-button-background: var(--theme-widget-reaction-button-background, rgba(255, 255, 255, 0.1));
      --widget-reaction-button-hover-background: var(--theme-widget-reaction-button-hover-background, rgba(255, 255, 255, 0.2));
      --widget-reaction-button-border: var(--theme-widget-reaction-button-border);
      --widget-reaction-button-border-radius: var(--theme-widget-reaction-button-border-radius, 0.5rem);
      --widget-reaction-button-padding: var(--theme-widget-reaction-button-padding, 0.125rem 0.25rem);
      --widget-reaction-button-color: var(--theme-widget-reaction-button-color, rgba(150, 150, 150, 0.5));
      --widget-reaction-button-hover-color: var(--theme-widget-reaction-button-hover-color, rgba(150, 150, 150, 1));
      --widget-reaction-wrap-background: var(--theme-widget-reaction-wrap-background, #282828);
      --widget-reaction-wrap-box-shadow: var(--theme-widget-reaction-wrap-box-shadow, -5px 10px 20px rgba(0, 0, 0, 0.4));
      --widget-reaction-item-background: var(--theme-widget-reaction-item-background, rgba(0, 0, 0, 0));
      --widget-reaction-item-hover-background: var(--theme-widget-reaction-item-hover-background, rgba(255, 255, 255, 0.2));
      --widget-reaction-item-container-margin: var(--theme-widget-reaction-item-container-margin, 0 0 0 0.5rem);
      --widget-reaction-item-container-transform: var(--theme-widget-reaction-item-container-transform, translate(-8px, -18px));
      --widget-reaction-count-color: var(--theme-widget-reaction-count-color, #fff);

      /* Points Display */
      --points-display-progress-width: var(--theme-points-progress-display-width, 100px);
      --points-display-progress-height: var(--theme-points-progress-display-height, 1rem);
      --points-display-progress-background: var(--theme-points-display-progress-background, #007bff !important);
      --points-display-progress-number-color: var(--theme-points-display-progress-number-color, #777);
      --points-coin-display: var(--theme-points-coin-display);

      /* Badge Display */
      --badge-display-border-radius: var(--theme-badge-display-border-radius, 6px);

      /* Load More Button */
      --load-more-button-display: var(--theme-load-more-button-display, flex);
      --load-more-button-justify-content: var(--theme-load-more-button-justify-content, center);
      --load-more-button-width: var(--theme-load-more-button-width, 100%);
      --load-more-button-font-size: var(--theme-load-more-button-font-size, 1rem);
      --load-more-button-height: var(--theme-load-more-button-height, 40px);
      --load-more-button-border: var(--theme-load-more-button-border, none);
      --load-more-button-border-radius: var(--theme-load-more-button-border-radius, 0.25rem);
      --load-more-button-background: var(--theme-load-more-button-background, #086fac);
      --load-more-button-color: var(--theme-load-more-button-color, #fff);
      --load-more-button-padding: var(--theme-load-more-button-padding);
      --load-more-button-margin: var(--theme-load-more-button-margin, 1rem 0 0 0);

      /* Author and Timestamp */
      --bylines-wrapper-display: var(--theme-bylines-wrapper-display, flex);
      --bylines-wrapper-justify-content: var(--theme-bylines-wrapper-justify-content, center);
      --bylines-wrapper-margin: var(--theme-bylines-wrapper-margin, 0.5rem);
      --widget-author-name-color: var(--theme-widget-author-name-color, #000);
      --widget-author-name-font-size: var(--theme-widget-author-name-font-size);
      --widget-author-name-margin: var(--theme-widget-author-name-margin, 0);
      --widget-author-image-height: var(--theme-widget-author-image-height, 30px);
      --widget-author-image-border-radius: var(--theme-widget-author-image-border-radius, 0.3rem);
      --widget-timestamp-container-display: var(--theme-widget-timestamp-container-display, flex);
      --widget-timestamp-container-align-items: var(--theme-widget-timestamp-container-align-items, center);
      --widget-timestamp-container-height: var(--theme-widget-timestamp-container-height, 100%);
      --widget-timestamp-time-color: var(--theme-widget-timestamp-time-color, #000);


      /* VARIABLES */
      --spacer: var(--theme-spacer, 1rem);

      /* GLOBALS */
      display: block;
    }
`;
