import { css } from 'lit-element';

export const predictionBaseStyles = css`
  .widget-header {
    color: var(--prediction-header-text-color);
    background-color: var(--prediction-header-primary-color);
    background-image: linear-gradient(
      90deg,
      var(--prediction-header-secondary-color) 0%,
      var(--prediction-header-primary-color) 100%
    );
  }
`;
