import { html } from 'lit-element';
import { component, useState, useEffect } from 'haunted';
import { outsideClick, eventBus } from '../utils';
import { stickerDisplayStyles } from './styles/stickerDisplayStyles';
import { emojiIcon, closeIcon } from '../icons';

const animate = {
  SHOW: 'show',
  HIDE: 'hide',
};

function StickerDisplay({ stickerPacks, dataId }) {
  const initialStickers = stickerPacks && stickerPacks[0].stickers;
  const initialId = stickerPacks && stickerPacks[0].id;
  const stickerEvt = `${dataId}-sendSticker`;
  const ocEvt = `${dataId}-outsideClicked`;

  const [toggle, setToggle] = useState(false);
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [animation, setAnimation] = useState(animate.HIDE);
  const [stickers, setStickers] = useState(initialStickers);
  const [selectedPack, setSelectedPack] = useState(initialId);

  useEffect(() => {
    drawerOpen ? outsideClick.create(dataId) : outsideClick.remove();
  }, [drawerOpen]);

  useEffect(() => {
    toggle ? openDrawer() : closeDrawer();
  }, [toggle]);

  useEffect(() => {
    eventBus.on(ocEvt, () => setToggle(false));
    return () => eventBus.off(ocEvt, () => setToggle(false));
  }, []);

  const toggleDrawer = () => setToggle(!toggle);

  const addSticker = sticker => {
    toggleDrawer(); // Does not fire if after eventBus. Find out why.
    const stickerMessage = `:${sticker.shortcode}:`;
    eventBus.emit(stickerEvt, { event: stickerMessage });
  };

  const openDrawer = () => {
    setDrawerOpen(true);
    setTimeout(() => setAnimation(animate.SHOW), 100);
  };

  const closeDrawer = () => {
    setAnimation(animate.HIDE);
    setTimeout(() => setDrawerOpen(false), 300);
  };

  const changePack = pack => {
    setStickers(pack.stickers);
    setSelectedPack(pack.id);
  };

  const stickerBody = () =>
    stickers &&
    stickers.map(sticker => {
      return html`
        <button
          tabindex="0"
          type="button"
          class="sticker-wrap"
          aria-label="Sticker"
          @click=${() => addSticker(sticker)}
        >
          <div class="sticker-wrap-image">
            <img
              class="sticker-image"
              src=${sticker.file}
              alt="Sticker ${sticker.shortcode}"
            />
          </div>
        </button>
      `;
    });

  const stickerNav = () =>
    stickerPacks &&
    stickerPacks.map(pack => {
      const isActive = pack.id === selectedPack ? 'active' : '';
      return html`
        <button
          tabindex="0"
          type="button"
          aria-label="Sticker pack ${pack.name}"
          class="sticker-nav-item ${isActive}"
          @click=${() => changePack(pack)}
        >
          <img src=${pack.file} alt="${pack.name}" />
        </button>
      `;
    });

  return html`
    ${stickerDisplayStyles}
    <button
      tabindex="0"
      type="button"
      class="sticker-icon"
      aria-label="Open Stickers"
      @click=${toggleDrawer}
    >
      ${emojiIcon}
    </button>
    ${drawerOpen
      ? html`
          <div
            class="sticker-container ${animation}"
            aria-label="Sticker picker"
          >
            <header class="sticker-container-header">
              STICKERS
              <button
                tabindex="0"
                type="button"
                class="sticker-close-icon"
                aria-label="Close Stickers"
                @click=${toggleDrawer}
              >
                ${closeIcon}
              </button>
            </header>

            <div class="sticker-body">
              ${stickerBody()}
            </div>

            <nav class="sticker-nav-container">
              <div class="sticker-nav-wrap">
                ${stickerNav()}
              </div>
            </nav>
          </div>
        `
      : null}
  `;
}
customElements.define('sticker-display', component(StickerDisplay));
