import { html } from 'lit-element';
import { FollowUpBase } from './FollowUpBase';
import { imageVotingWidgetStyles } from '../sharedStyles/imageVotingWidgetStyles';
import '../../components/livelike-option';
import '../../components/livelike-select';
import '../../components/livelike-progress';
import '../../components/livelike-timer';

export class ImagePredictionFollowUpWidget extends FollowUpBase {
  static get styles() {
    return [super.styles, imageVotingWidgetStyles];
  }

  render() {
    return html`
      <article id="widget-article" class="widget">
        <header id="widgetHeader" class="widget-header">
          <h1 class="widget-question">${this.question}</h1>
        </header>
        <livelike-timer id="livelike-timer"></livelike-timer>
        <livelike-select class="widget-select" disabled>
          ${this.options.map(
            option => html`
              <livelike-option
                class="widget-option
                ${this.selectedClass(option)}
                ${this.correctClass(option)}"
                .selected=${this.isSelected(option)}
              >
                <div class="container">
                  <div class="image-description-wrapper">
                    <div class="widget-option-description image-description">
                      ${option.description}
                    </div>
                    <livelike-progress
                      class="widget-option-progress"
                      value=${this.votePercentage(option.vote_count)}
                    ></livelike-progress>
                  </div>
                  <div class="widget-option-percentage">
                    ${this.votePercentage(option.vote_count)}%
                  </div>
                </div>
                <div class="widget-figure">
                  <img
                    class="widget-option-image"
                    src=${option.image_url}
                    alt=${option.description}
                  />
                </div>
              </livelike-option>
            `
          )}
        </livelike-select>
      </article>
    `;
  }
}

customElements.define(
  'livelike-image-prediction-follow-up',
  ImagePredictionFollowUpWidget
);
