import { Widget } from '../base/Widget';
import { Voteable } from '../base/Voteable';
import { throttle, request } from '../../utils';
import { cheerMeterStyles } from './styles/cheerMeterStyles';

export class CheerMeterBase extends Voteable(Widget) {
  static get properties() {
    const props = Object.assign({}, super.properties, {
      widgetPayload: { type: Object },
      options: { type: Array },
      question: { type: String },
      vote: { type: Number },
      voteCount: { type: Number },
      count: { type: Number },
    });
    return props;
  }

  constructor() {
    super();
    this.voteCount = 0;
    this.count = 0;
  }

  firstUpdated() {
    this.initialTimelineLoad
      ? this.showTimelineVotes()
      : this.waitForVotes(this.widgetPayload)
          .then(() => this.voteResults())
          .then(() => this.displayPoints())
          .then(() => this.displayBadge())
          .then(this.shouldExpire);
  }

  cheerAction = option => {
    if (this.selectedOption && this.selectedOption.id) {
      this.voteCount = this.voteCount + 1;
      this.count = this.count + 1;
      this.voteCount > 0 && this.sendVote(option);
    } else {
      this.selectOption(option);
    }
  };

  createVote = (option, data) => {
    this.count = 0;
    request
      .post({ url: option.vote_url, data })
      .then(payload => (this.vote = payload));
  };

  sendVote = throttle(option => {
    this.createVote(option, { vote_count: this.count });
  }, 3000);

  static get styles() {
    return [super.styles, cheerMeterStyles];
  }
}
