import { html } from 'lit-element';
import { SliderBase } from './SliderBase';
import { emojiSliderStyles } from './styles/emojiSliderStyles';
import '../../components/livelike-option';
import '../../components/livelike-timer';
import '../../components/livelike-select';
import '../../components/livelike-progress';
import { phase } from '../../constants/WidgetStates';
import { RESULTS } from '../../constants/WidgetEvents';

export class EmojiSlider extends SliderBase {
  resultsEventName = RESULTS.EMOJI_SLIDER;

  render() {
    return html`
      ${emojiSliderStyles}
      <article id="widget-article" class="widget ${this.articleClass()}">
        <header id="widgetHeader" class="widget-header">
          <h1 class="widget-question">${this.question}</h1>
        </header>
        <livelike-timer id="livelike-timer"></livelike-timer>
        <livelike-select class="widget-select" .disabled=${this.disabled}>
          <form style="--val: 56 ;">
            <input type="range" id="emoji-slider" />
            <output for="emoji-slider">
              <img class="slider-image" id="emoji-output" />
            </output>
          </form>

          ${this.phase !== phase.VOTING
            ? html`
                <div class="slider-result-container">
                  <div
                    class="slider-result-mark"
                    style="left: ${this.average_magnitude * 100}%"
                  ></div>
                </div>
              `
            : null}
        </livelike-select>
      </article>
    `;
  }
}

customElements.define('livelike-emoji-slider', EmojiSlider);
