import { html } from 'lit-element';
import { emojiIcon } from '../icons';

const getReactionCount = (item, id) =>
  item.reactions && item.reactions[id] ? item.reactions[id].length : 0;

export const renderReactionButton = (item, fn) => html`
  <div class="reaction-button-container">
    <button
      class="reaction-button"
      aria-label="Message reactions"
      @click=${() => fn(item.id)}
    >
      ${emojiIcon}
    </button>
  </div>
`;

export const renderMiniReactions = (item, reactionPack) => {
  const hasReactions = item.reactions ? Object.keys(item.reactions).length : 0;
  const totalReactions = Object.values(item.reactions).reduce(
    (a, c) => a.concat(c),
    []
  ).length;
  return hasReactions > 0
    ? html`
        <div class="mini-reactions">
          ${reactionPack.map(reaction =>
            getReactionCount(item, reaction.id) > 0
              ? html`
                  <img
                    class="mini-reaction-image"
                    src=${reaction.file}
                    alt="Reaction ${reaction.name}"
                  />
                `
              : null
          )}
          ${totalReactions > 0
            ? html`
                <span
                  aria-label="Total reaction number"
                  class="mini-reaction-count"
                >
                  ${totalReactions}
                </span>
              `
            : null}
        </div>
      `
    : null;
};

export const renderReactions = (
  item,
  reactionPack,
  animate,
  userId,
  fn
) => html`
  <div class="reaction-item-container" style="opacity: ${animate}">
    <div class="reaction-item-wrap">
      ${reactionPack.map(reaction => {
        const count = getReactionCount(item, reaction.id);
        const isSelected = item =>
          item.reactions[reaction.id] &&
          item.reactions[reaction.id].find(r => r.uuid === userId);
        const selected = isSelected(item) ? 'selected' : '';
        return html`
          <button
            aria-label="Reaction ${reaction.name}"
            class="reaction-item ${selected}"
            @click=${() => fn(item, reaction)}
          >
            <img class="reaction-image" src=${reaction.file} alt="" />
            ${count > 0
              ? html`
                  <span aria-label="Reaction number" class="reaction-count"
                    >${count}</span
                  >
                `
              : null}
          </button>
        `;
      })}
    </div>
  </div>
`;
