import { LitElement, html } from 'lit-element';
import { progressStyles } from './styles/progressStyles';

export class Progress extends LitElement {
  static get properties() {
    return {
      value: { type: Number }
    };
  }

  constructor() {
    super();

    this.value = 0;
  }

  static get styles() {
    return progressStyles;
  }

  render() {
    return html`
      <div class="progress">
        <div class="progress-meter" style="width: ${this.value}%"></div>
      </div>
    `;
  }
}

customElements.define('livelike-progress', Progress);
