import { html } from 'lit-element';

const wordItem = (idx, word) => html`
  <div class="word-item" key="word${idx}">${word}&nbsp;</div>
`;
const imgItem = (idx, height, sticker) => html`
  <div key="sticker${idx}">
    <img class="word-item" height=${height} src=${sticker.file} alt="" />
  </div>
`;

export const getStickerMessage = (message, stickers) => {
  let height = '50px';
  const STICKER_REG = /:([\w]+):/g;
  const replaced = message.replace(STICKER_REG, w => ` ${w} `);
  const messageArr = replaced.split(' ');
  const cleanedArr = messageArr.filter(Boolean);
  cleanedArr && cleanedArr.length > 1 && (height = '30px');
  return messageArr.map((word, idx) => {
    let combined = [];
    const matched = word.match(STICKER_REG);
    if (matched) {
      let sticker = stickers.find(s => s.shortcode === word.slice(1, -1));
      sticker
        ? combined.push(imgItem(idx, height, sticker))
        : combined.push(wordItem(idx, word));
    } else {
      combined.push(wordItem(idx, word));
    }
    return combined;
  });
};

export const getImageMessage = m =>
  html`
    <div key="image-message-container">
      <img
        class="image-message"
        width=${m.image_width}
        src=${m.image_url}
        alt="Chat image"
      />
    </div>
  `;

export const convertMessages = async ({ messageList, stickers }) =>
  messageList.map(m => {
    let newMessage = m.image_url
      ? getImageMessage(m)
      : getStickerMessage(m.message, stickers);
    return {
      ...m,
      newMessage,
    };
  });
