import { html } from 'lit-element';

export const textInputStyles = html`
  <style>
    :host {
      display: block;
    }
    /* ie and edge */
    livelike-chat-composer {
      display: block;
    }
    .input-container {
      display: flex;
      position: relative;
      padding: var(--input-container-padding);
      margin: var(--message-input-container-margin);
      border-top: 2px solid var(--input-container-background);
      background: var(--input-container-background);
    }

    .message-input {
      border: 0;
      background: var(--message-input-background);
      border: var(--message-input-border);
      box-sizing: border-box;
      border-radius: var(--message-input-border-radius);
      color: var(--message-input-color);
      font-family: var(--message-input-font-family);
      font-size: var(--message-input-font-size);
      line-height: 1.5rem;
      min-height: 2.5rem;
      overflow: hidden;
      padding: var(--message-input-padding);
      resize: none;
      width: var(--message-input-container-width);
    }
    .message-input:focus {
      outline: none;
      border: 1px solid rgba(255, 255, 255, 0.2);
    }
    .message-input::placeholder {
      /* color: $color-gray10; */
      color: var(--message-input-placeholder-color);
    }

    .send-button {
      background: none;
      border: none;
      padding: 0;
      margin-left: 1rem;
      display: flex;
      align-items: center;
      transition: all 0.3s ease-out;
    }

    .send-icon {
      border: none;
      background: none;
    }
    .send-icon svg {
      fill: var(--input-send-icon-color);
    }

    .send-icon:hover svg, .send-icon:focus svg {
      filter: var(--input-send-icon-hover-color);
      transition: all 0.3s ease-out;
    }

    .send-icon:hover {
      cursor: pointer;
    }
  </style>
`;
