import { LitElement, html } from 'lit-element';
//widgets
import './widgets/alerts/AlertWidget';
import './widgets/slider/EmojiSlider';
import './widgets/social/SocialEmbedWidget';
import './widgets/richpost/RichPost';
import './widgets/cheermeter/CheerMeterWidget';
import './widgets/polls/ImagePollWidget';
import './widgets/polls/TextPollWidget';
import './widgets/quizzes/ImageQuizWidget';
import './widgets/quizzes/TextQuizWidget';
import './widgets/predictions/ImagePredictionWidget';
import './widgets/predictions/ImagePredictionFollowUpWidget';
import './widgets/predictions/TextPredictionWidget';
import './widgets/predictions/TextPredictionFollowUpWidget';
// components
import './components/bylines-wrapper';
import './components/loadmore-widgets';
import { widgetAutoConnect } from './scripts/internal/widgetAutoConnect';
import { appStyles } from './appStyles';
import { _$$ } from './scripts/internal/globals';
import { WidgetLifecycles } from './constants/WidgetEvents';
import { WidgetController } from './WidgetController';
import { ContentSession } from './ContentSession';
// import { createWidgetAdapter } from './Adapter';
import { WidgetAdapter } from './Adapter';
import {
  errDocLink,
  getLang,
  eventBus,
  pageVisHandler,
  selfChildErr,
} from './utils';

export class WidgetContainer extends LitElement {
  static properties = {
    programid: { type: String },
    channel: { type: String },
    dataId: { type: String },
    loaded: { type: Boolean },
    mode: { type: String },
    authors: { type: Boolean },
    timestamps: { type: Boolean },
    timeFormat: { type: Object },
    langCode: { type: String },
    limit: { type: Number },
    nextPage: { type: String },
    reactions: { type: Boolean },
    widgetTimelineUrl: { type: String },
    widgetSession: { type: Object },
    widgetController: { type: Object },
    timelineLoaded: { type: Boolean },
    adapter: { type: Object },
    widgetAdapter: { type: Object },
  };

  constructor() {
    super();
    this.loaded = false;
    this.channel = null;
    this.nextPage = null;
    this.dataId = null;
    this.adapter = null;
    this.widgetAdapter = null;
    this.mode = 'pop-up';
    this.timeFormat = {
      month: 'long',
      day: 'numeric',
      year: 'numeric',
      hour: 'numeric',
      minute: 'numeric',
    };
    this.langCode = 'en-US';
    this.reactions = false;
    this.authors = null;
    this.timestamps = null;
    this.widgetSession = null;
    this.widgetController = null;
    this.timelineLoaded = false;
    this.widgetTimelineUrl = null;

    // document.addEventListener('visibilitychange', changePageVis, false);
    // this.addEventListener('WIDGETTEST', function(e){console.log('WidgetContainer.js -> WIDGETTEST', e)}, true)
    // WidgetLifecycles.forEach(ev => this.addEventListener(ev, ()=>{}, true))
    WidgetLifecycles.forEach(ev => this.addEventListener(ev, e => {}, true));
  }

  firstUpdated() {
    const ln = this.parentElement && this.parentElement.localName;
    (ln === 'livelike-widgets' || ln === 'livelike-widget-zone') &&
      selfChildErr(this);
    pageVisHandler();
    // this.widgetAdapter = createWidgetAdapter(this.adapter)
    // console.log('connect this.wdiogetAdapter', this.widgetAdapter)
    // this.widgetController = WidgetController(this, this.widgetAdapter);
    this.widgetAdapter = new WidgetAdapter();
    this.widgetController = new WidgetController(this, this.widgetAdapter);
    getLang(this).then(lang => lang && (this.langCode = lang));
  }

  updated(changedProps) {
    changedProps.forEach((prevProp, name) => {
      if (name === 'adapter' && this.adapter) {
        this.widgetAdapter = new WidgetAdapter(this.adapter);
        // console.log('connect this.wdiogetAdapter', this.widgetAdapter)
        window.WCTEST = WidgetController;
        this.widgetController = new WidgetController(this, this.widgetAdapter);
      }
      // this.widgetController = new WidgetController(this, this.widgetAdapter);
      const programId =
        name === 'programid' && this.programid && prevProp !== this.programid;
      const mode = name === 'mode' && prevProp !== undefined;

      if (name === 'channel' && this.channel) {
        eventBus.on(this.channel, this.widgetCreated);
        eventBus.off(prevProp, this.widgetCreated);
      }
      if (programId || mode) {
        if (this.mode === 'timeline' || prevProp === 'timeline') {
          while (this.firstChild) {
            this.removeChild(this.firstChild);
          }
        }
        this.connectWidget();
      }
    });
  }

  connectWidget = () => {
    if (!this.programid && !_$$.programId) {
      return errDocLink(
        'Program id missing from widget element.',
        'program-ids'
      );
    }
    if (_$$.ready && this.widgetAdapter) {
      this.loaded = false;
      widgetAutoConnect(this).then(() => {
        this.widgetSession = ContentSession(this);
        // console.log('connect this.wdiogetAdapter', this.widgetAdapter)
        // this.widgetController = WidgetController(this, this.widgetAdapter);
        this.mode === 'timeline'
          ? this.widgetController
              .getTimeline(this.widgetTimelineUrl)
              .then(({ loaded, nextPage }) => {
                this.nextPage = nextPage;
                this.loaded = loaded;
              })
          : (this.loaded = true);
      });
    } else {
      setTimeout(this.connectWidget, 1000);
    }
  };

  widgetCreated = e =>
    this.widgetSession.enqueueItem(
      e.message,
      this.widgetController.updateWidget
    );

  static get styles() {
    return appStyles;
  }
  // createRenderRoot() {
  //   return this;
  // }
  render() {
    return html`
      <style>
        :host {
          display: block;
        }
        livelike-widgets {
          display: block;
        }
        livelike-widget-zone {
          display: block;
        }
      </style>
      ${this.loaded
        ? html`
            <slot></slot>
            ${this.mode === 'timeline' && this.nextPage
              ? html`
                  <loadmore-widgets .nextPage=${this.nextPage} .el=${this}>
                  </loadmore-widgets>
                `
              : null}
          `
        : html`
            <slot name="widgets-loading"></slot>
          `}
    `;
  }
}

customElements.define('widget-container', WidgetContainer);
customElements.define('livelike-widgets', class extends WidgetContainer {});
customElements.define('livelike-widget-zone', class extends WidgetContainer {});
