import { _$$, userProfile } from './internal/globals';
import {
  fetchData,
  saveLocalStorage,
  eventBus,
  typeErr,
  errDocLink,
  request,
} from '../utils';

// Updated profile response does not contain access_token, so add it back in.
export const updateProfile = async ({ nickName, clientId, accessToken }) => {
  if (!nickName) return errDocLink('Missing nickName argument');
  if (typeof nickName !== 'string') return typeErr('nickName', 'String');

  let profileUrl = _$$.profileUrl,
    lsKey = _$$.lsKey,
    token = accessToken ? accessToken : userProfile.access_token;
  if (!profileUrl && !lsKey) {
    if (clientId && accessToken) {
      const app = await fetchData.app(clientId);
      profileUrl = app.profile_url;
      lsKey = 'livelike-user-auth-' + clientId;
    } else {
      return errDocLink(
        'Missing clientId and accessToken. Either run LiveLike.init() or pass arguments.'
      );
    }
  }
  const profile = await request.patch({
    url: profileUrl,
    data: { nickname: nickName },
    accessToken
  });
  profile.access_token = token;
  saveLocalStorage(lsKey, profile);
  if (_$$.profileUrl && _$$.lsKey) {
    userProfile.nickname = profile.nickname;
    userProfile.gaming && (profile.gaming = userProfile.gaming); //reappend gaming that's lost due to moving flow of profile into to pure events, no props to detach profile from chat container element.
    eventBus.emit('updateProfile', { profile: profile });
  }
  return profile;
};
