import { programs } from './internal/globals';
import { getMessages } from './internal/pubnubHandler';
import { fetchData, separateMessagesRemoveDeleted } from '../utils';
import { IMessageListOptions } from '../types';

export const messageList = async (
  roomId: string,
  opts?: IMessageListOptions
): Promise<void | Array<object>> => {
  let channel = programs.channels[roomId];
  !channel && (channel = await fetchData.chatRoom(roomId));
  try {
    const messageResponse = await getMessages(channel, opts);
    return separateMessagesRemoveDeleted(messageResponse.channels[channel]);
  } catch (err) {
    return console.error(err);
  }
};

export const getMessageList = (el: any, limit = 50, reverse = false) =>
  new Promise(res => {
    const roomId = el.roomid ? el.roomid : el.elId;
    const opts = { limit, reverse };
    messageList(roomId, opts).then(list => res(list));
  });
