import { css } from 'lit-element';

export const baseQuizWidgetStyles = css`
.widget-header {
  color: var(--quiz-header-text-color);
  background-color: var(--quiz-header-primary-color);
  background-image: linear-gradient(
    90deg,
    var(--quiz-header-secondary-color) 0%,
    var(--quiz-header-primary-color) 100%
  );
}

.widget-option.selected livelike-progress {
  --progress-meter-background: linear-gradient(
    90deg,
    var(--quiz-selected-progress-secondary-color) 0%,
    var(--quiz-selected-progress-primary-color) 100%
  );
}

.widget-option {
  border: 2px solid transparent;
  transition: border ease 0.3s;
}
.widget-option.selected {
  border: 2px solid var(--quiz-selected-border-color);
  transition: border ease 0.3s;
}

.widget-option.selected.result-correct {
  border: 2px solid var(--correct-selected-border-color);
  transition: border ease 0.3s;
  color: var(--correct-selected-option-text-color);
}
.widget-option.selected.result-correct livelike-progress {
  --progress-meter-background: linear-gradient(
    90deg,
    var(--correct-selected-progress-secondary-color) 0%,
    var(--correct-selected-progress-primary-color) 100%
  );
}
.widget-option.selected.result-incorrect {
  border: 2px solid var(--incorrect-selected-border-color);
  transition: border ease 0.3s;
  color: var(--incorrect-selected-option-text-color);
}
.widget-option.selected.result-incorrect livelike-progress {
  --progress-meter-background: linear-gradient(
    90deg,
    var(--incorrect-selected-progress-secondary-color) 0%,
    var(--incorrect-selected-progress-primary-color) 100%
  );
}
.widget-option.result-correct {
  color: var(--correct-option-text-color);
  border: 2px solid var(--correct-border-color);
  transition: border ease 0.3s;
}
.widget-option.result-correct livelike-progress {
  --progress-meter-background: linear-gradient(
    90deg,
    var(--correct-progress-secondary-color) 0%,
    var(--correct-progress-primary-color) 100%
  );
}
`;
