import { Widget } from '../base/Widget';
import { Voteable } from '../base/Voteable';
import { predictionBaseStyles } from './styles/predictionBaseStyles';
import { predictionWidgetStyles } from './styles/predictionWidgetStyles';
import { textSelectStyles } from '../sharedStyles/textSelectStyles';

export class PredictionBase extends Voteable(Widget) {
  static get styles() {
    return [
      super.styles,
      textSelectStyles,
      predictionBaseStyles,
      predictionWidgetStyles,
    ];
  }
  static get properties() {
    return {
      question: { type: String },
      confirmation_message: { type: String },
      options: { type: Array },
      vote: { type: Object },
    };
  }
  firstUpdated() {
    this.initialTimelineLoad
      ? this.showTimelineVotes()
      : this.waitForVotes(this.widgetPayload)
          .then(() => this.showConfirmation())
          .then(this.shouldExpire);
  }
}
