import { css } from 'lit-element';

export const chatStyles = css`
  :host {
    /* CHAT */

    /* Text Input */
    --input-container-padding: var(--theme-input-container-background);
    --input-container-background: var(--theme-input-container-background, inherit);
    --message-input-placeholder-color: var(--theme-message-input-placeholder-color, rgba(0,0,0,.35));
    --message-input-background: var(--theme-message-input-background, #fff);
    --message-input-border: var(--theme-message-input-border, 1px solid rgba(0,0,0,.2));
    --message-input-border-radius: var(--theme-message-input-border-radius, 0.5rem);
    --message-input-padding: var(--theme-message-input-padding, 0.5rem 2.25rem 0.5rem 0.5rem);
    --message-input-color: var(--theme-message-input-color, inherit);
    --message-input-font-size: var(--theme-message-input-font-size, 1rem);
    --message-input-font-family: var(--theme-message-input-font-family, inherit);
    --message-input-container-width: var(--theme-message-input-container-width, 100%);
    --message-input-container-margin: var(--theme-message-input-container-margin, 0);
    --input-send-icon-color: var(--theme-input-send-icon-color, #777);
    --input-send-icon-hover-color: var(--theme-input-send-icon-color, invert(1));

    /* Message List */
    --message-list-display: var(--theme-message-list-display, block);
    --message-list-overflow: var(--theme-message-list-overflow, auto);
    --message-list-height: var(--theme-message-list-height);
    --message-list-padding: var(--theme-message-list-padding, 1rem 0 0 1rem);
    --message-list-margin: var(--theme-message-list-margin);

    /* Message */
    --message-item-container-position: var(--theme-message-item-container-position, relative);
    --message-item-container-margin: var(--theme-message-item-container-margin, 0 0 0.5rem 0);
    --message-item-container-padding: var(--theme-message-item-container-padding);
    --message-color: var(--theme-message-color, inherit);
    --message-font-size: var(--theme-message-font-size, 1rem);
    --message-font-family: var(--theme-message-font-family);
    --sender-name-color: var(--theme-sender-name-color, inherit);
    --sender-name-font-size: var(--theme-sender-name-font-size, 1rem);
    --sender-name-font-family: var(--theme-sender-name-font-family);
    --sender-name-margin: var(--theme-sender-name-margin, 0 0 0.25rem 0);
    --deleted-message-color: var(--theme-sender-name-color, inherit);

    /* Message Wrapper */
    --message-wrapper-display: var(--theme-message-wrapper-display, inline-grid);
    --message-wrapper-flex-direction: var(--theme-message-wrapper-flex-direction);
    --message-wrapper-flex-wrap: var(--theme-message-wrapper-flex-wrap);
    --message-wrapper-background: var(--theme-message-wrapper-background, rgba(0, 0, 0, 0.4));
    --message-wrapper-border-radius: var(--theme-message-wrapper-border-radius, 0.75rem);
    --message-wrapper-border-bottom: var(--theme-message-wrapper-border-bottom);
    --message-wrapper-padding: var(--theme-message-wrapper-padding, 0.5rem 1rem);
    --message-wrapper-width: var(--theme-message-wrapper-width);
    --message-wrapper-min-height: var(--theme-message-wrapper-min-height);
    --message-wrapper-min-width: var(--theme-message-wrapper-min-width);
    --message-wrapper-max-width: var(--theme-message-wrapper-max-width, 80%);
    --message-wrapper-margin: var(--theme-message-wrapper-margin);
    --message-wrapper-position: var(--theme-message-wrapper-position, relative);

    /* Message Reactions */
    --message-reaction-container-button-margin: var(--theme-message-reaction-button-container-margin);
    --message-reaction-button-position: var(--theme-message-reaction-button-position, absolute);
    --message-reaction-button-transform: var(--theme-message-reaction-button-transform, translate(12px, -2px));
    --message-reaction-button-background: var(--theme-message-reaction-button-background, rgba(255, 255, 255, 0.1));
    --message-reaction-button-hover-background: var(--theme-message-reaction-button-hover-background, rgba(255, 255, 255, 0.2));
    --message-reaction-button-border: var(--theme-message-reaction-button-border);
    --message-reaction-button-border-radius: var(--theme-message-reaction-button-border-radius, 0.5rem);
    --message-reaction-button-padding: var(--theme-message-reaction-button-padding, 0.125rem 0.25rem);
    --message-reaction-button-color: var(--theme-message-reaction-button-color, rgba(150, 150, 150, 0.5));
    --message-reaction-button-hover-color: var(--theme-message-reaction-button-hover-color, rgba(150, 150, 150, 1));
    --message-reaction-wrap-background: var(--theme-message-reaction-wrap-background, #282828);
    --message-reaction-wrap-box-shadow: var(--theme-message-reaction-wrap-box-shadow, -5px 10px 20px rgba(0, 0, 0, 0.4));
    --message-reaction-item-background: var(--theme-message-reaction-item-background, rgba(0, 0, 0, 0));
    --message-reaction-item-hover-background: var(--theme-message-reaction-item-hover-background, rgba(255, 255, 255, 0.2));
    --message-reaction-total-count-color: var(--theme-message-reaction-total-count-color, #fff);

    /* Message Timestamp */
    --message-timestamp-font-family: var(--theme-message-timestamp-font-family);
    --message-timestamp-font-size: var(--theme-message-timestamp-font-size, 0.9rem);
    --message-timestamp-color: var(--theme-message-timestamp-color);
    --message-timestamp-margin: var(--theme-message-timestamp-margin);
    --message-timestamp-padding: var(--theme-message-timestamp-padding);

    /* Message Options Icon Button */
    --message-options-icon-color: var(--theme-down-icon-color, #777);
    --message-options-icon-hover-color: var(--theme-down-icon-color, #555);
    --message-options-icon-opacity: var(--theme-message-options-icon-opacity, 0);
    --message-options-icon-hover-opacity: var(--theme-message-options-icon-hover-opacity, 1);
    --message-options-icon-active-opacity: var(--theme-message-options-icon-active-opacity, 1);
    --message-options-icon-transition: var(--theme-message-options-icon-transition, opacity ease 0.3s);
    --message-options-icon-margin: var(--theme-message-options-icon-margin, 1rem 0);
    --message-options-icon-padding: var(--theme-message-options-icon-padding, 0 1rem);

    /* Message Options */
    --message-options-anchor-transition: var(--theme-message-options-anchor-transition, opacity 0.3s ease);
    --message-options-border: var(--theme-message-options-border);
    --message-options-border-radius: var(--theme-message-options-border-radius);
    --message-options-padding: var(--theme-message-options-padding);
    --message-options-background: var(--theme-message-options-background);
    --message-options-width: var(--theme-message-options-width, 50%);
    --message-options-button-color: var(--theme-message-options-button-color, #000);
    --message-options-button-hover-color: var(--theme-message-options-button-hover-color);
    --message-options-button-background: var(--theme-message-options-button-background, rgba(0, 0, 0, 0.07));
    --message-options-button-hover-background: var(--theme-message-options-button-hover-background, rgba(0, 0, 0, 0.2));
    --message-options-button-border: var(--theme-message-options-button-border);
    --message-options-button-border-radius-top: var(--theme-message-options-button-border-radius-top, 0.25rem 0.25rem 0 0);
    --message-options-button-border-radius-bottom: var(--theme-message-options-button-border-radius-bottom, 0 0 0.25rem 0.25rem);
    --message-options-button-padding: var(--theme-message-options-button-padding, 0.25rem);
    --message-options-button-transition: var(--theme-message-options-button-transition, background ease 0.3s);
    --message-options-font-size: var(--theme-message-options-font-size, 0.75rem);

    /* Message Options Moderation Results Modal */
    --message-moderation-modal-color: var(--theme-message-moderation-modal-color, #000);
    --message-moderation-modal-margin: var(--theme-message-moderation-modal-margin);
    --message-moderation-modal-padding: var(--theme-message-moderation-modal-padding, 1rem);
    --message-moderation-modal-background: var(--theme-message-moderation-modal-background, #fff);
    --message-moderation-modal-border-radius: var(--theme-message-moderation-modal-border-radius, 0.25rem);
    --message-moderation-modal-width: var(--theme-message-moderation-modal-width, 50%);
    --message-moderation-modal-zIndex: var(--theme-message-moderation-modal-zIndex, 15);
    --message-moderation-modal-position: var(--theme-message-moderation-modal-position, absolute);
    --message-moderation-modal-top: var(--theme-message-moderation-modal-top, 50%);
    --message-moderation-modal-left: var(--theme-message-moderation-modal-left, 25%);
    --message-moderation-modal-transform: var(--theme-message-moderation-modal-transform);
    --message-moderation-modal-transform-showing: var(--theme-message-moderation-modal-transform-showing);
    --message-moderation-modal-opacity-showing: var(--theme-message-moderation-modal-opacity-showing, 1);
    --message-moderation-modal-transition: var(--theme-message-moderation-modal-transition, all ease 0.3s);

    /* Sticker Display */
    --sticker-icon-color: var(--theme-sticker-icon-color, #888);
    --sticker-icon-hover-color: var(--theme-sticker-icon-color, invert(1));
    --sticker-container-width: var(--theme-sticker-container-width);
    --sticker-container-max-width: var(--theme-sticker-container-max-width, 22rem);
    --sticker-icon-bottom: var(--theme-sticker-icon-bottom, 0.8rem);

    /* Scroll Down */
    --down-icon-background: var(--theme-down-icon-background, #b0b0b0);
    --down-icon-hover-background: var(--theme-down-icon-hover-background, #999);
    --down-icon-color: var(--theme-down-icon-color, #fff);
    --down-icon-hover-color: var(--theme-down-icon-hover-color, #fff);
    --down-icon-border-radius: var(--theme-down-icon-border-radius, 50%);
    --down-icon-box-shadow: var(--theme-down-icon-box-shadow);

    --scrollbar-background: var(--theme-scrollbar-background, inherit);
    --scrollbar-thumb: var(--theme-scrollbar-thumb, inherit);

    /* PROFILE */
    --profile-color: var(--theme-profile-color);
    --profile-display-padding: var(--theme-profile-display-padding);
    --profile-display-margin: var(--theme-profile-display-margin);
    --profile-display-zIndex: var(--theme-profile-display-zIndex);
    --profile-nickname-display: var(--theme-profile-nickname-display, inline-block);
    --profile-nickname-color: var(--theme-profile-nickname-color, inherit);
    --profile-nickname-font-size: var(--theme-profile-nickname-font-size, 12px);
    --profile-nickname-font-weight: var(--theme-profile-nickname-font-weight, bold);
    --profile-nickname-background: var(--theme-profile-nickname-background, inherit);
    --profile-nickname-border-radius: var(--theme-profile-nickname-border-radius, inherit);
    --profile-nickname-padding: var(--theme-profile-nickname-padding, 3px 16px 4px);
    --profile-points-color: var(--theme-profile-points-color, inherit);
    --profile-rank-color: var(--theme-profile-rank-color, inherit);
    --profile-points-coin-display: var(--theme-profile-points-coin-display);
    --profile-points-coin-vertical-align: var(--theme-profile-points-coin-vertical-align, middle);

    /* GLOBALS */
    display: block;
  }

`;
