import { css } from 'lit-element';

export const imageVotingWidgetStyles = css`
  .widget-figure {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    max-width: 25%;
    width: 25%;
  }
  .widget-option-image {
    max-width: 90px;
    max-height: 60px;
    display: block;
    margin: 0 auto;
  }
  .container {
    display: flex;
    flex-direction: column;
    width: 75%;
    max-width: 75%;
  }
  .widget-option-progress {
    width: 100%;
    height: 22px;
  }
  .image-description-wrapper {
    position: relative;
  }
  .widget-option-percentage {
    padding: .125rem 5.625rem 0.375rem 1rem;
  }
  .widget-image-description {
    flex-grow: 1;
    position: relative;
    z-index: 10;
    padding: 0.625rem 5.625rem 0.25rem 1rem;
  }
`;
