import { connectionBase } from './connectionBase';
import { _$$ } from './globals';
// import { getReactions } from '../../utils';
import { subscribe } from './pubnubHandler';

// This document.contains(c) check after the promise is necessary to ensure the element
// doesn't get added to our list of elements in programs[programId/roomId/elId] during the
// creation of the list in connectionBase. This immedaitely returns a promise so that
// widgetAutoConnect.then() in the WidgetContainer does not error.
//  It either resolves or hangs and never resolves.
export const widgetAutoConnect = (w: any) =>
  new Promise(
    res =>
      document.contains(w) &&
      connectionBase(w, 'widgetEl').then(({ dataId, prog }) => {
        w.widgetTimelineUrl = prog.timeline_url;
        w.timelineLoaded = false;
        w.channel = prog.subscribe_channel;
        if (!_$$.widgetHandlers[dataId]) {
          _$$.widgetHandlers[dataId] = w.channel;
          subscribe(w.channel);
        }
        if (!!_$$.logger) {
          console.log('LIVELIKE Widget Conn. - widget dataId', dataId);
          console.log('LIVELIKE Widget Conn. - handlers', _$$.widgetHandlers);
        }
        res();
      })
  );

