import { LitElement } from 'lit-element';
import { widgetBaseStyles } from './styles/widgetBaseStyles';
import { programs, userProfile } from '../../scripts/internal/globals';
import { phase, widgetState as ws } from '../../constants/WidgetStates';
// import { WidgetLifecycles } from '../../constants/WidgetEvents';
import {
  request,
  eventBus,
  eventDispatcher,
  getMilliseconds,
  appendLocalStorage,
} from '../../utils';
import '../../components/points-display';
import '../../components/badge-display';
import '../../components/widget-reactions';

export class Widget extends LitElement {
  static get properties() {
    return {
      changed: { type: Boolean },
      disabled: { type: Boolean, reflect: true },
      phase: { type: String },
      selectedOption: { type: Object },
      timeout: { type: String },
      timerStarted: { type: Boolean },
      userPoints: { type: Object },
      resultsHolder: { type: Object },
      widgetPayload: { type: Object },
      confirmation_message: { type: String },
      mode: { type: String },
    };
  }

  constructor() {
    super();
    this.changed = false;
    this.phase = phase.VOTING;
    this.selectedOption = {};
    this.timerStarted = false;
    this.userPoints = {};
    this.resultsHolder = {};
    // WidgetLifecycles.forEach(ev => this.addEventListener(ev, (e)=>{console.log('Widget.js -> Widget Event: ', ev, e)}, true))
    this.liveListener = {
      message: msg => {
        // console.log('msg', msg);
        msg.channel === this.widgetPayload.subscribe_channel &&
          msg.message.event === this.resultsEventName &&
          this.onResults(msg.message.payload);
      },
    };
  }

  articleClass = () => (this.changed ? ws.CHANGED : ws.UNCHAGED);

  onResults = payload => {
    this.phase !== 'voting' &&
      payload.average_magnitude &&
      (this.average_magnitude = payload.average_magnitude);
    payload.options
      ? this.optionsResults(payload)
      : (this.resultsHolder = payload);
  };

  optionsResults = payload => {
    const newOptions = [...this.options];
    this.options.map((v, idx) => {
      newOptions[idx].vote_count = payload.options[idx].vote_count;
    });
    this.options = newOptions;
    this.changed = ws.CHANGED;
  };

  choicesResults = payload => {
    if (payload && payload.choices) {
      const newChoices = [...this.choices];
      this.choices.map((v, idx) => {
        newChoices[idx].answer_count = payload.choices[idx].answer_count;
      });
      this.choices = newChoices;
      this.changed = ws.CHANGED;
    }
  };

  static get styles() {
    return widgetBaseStyles;
  }

  selectOption = option => {
    this.changed = true;
    this.selectedOption = option;
  };

  voteAction = option => {
    if (this.disabled) return;
    if (!this.isSelected(option)) {
      this.selectOption(option);
      this.voteOption(option); // In Voteable.js
    }
  };

  removeWidget = evt => {
    eventDispatcher(this, evt, { element: this, widget: this.widgetPayload });
    eventBus.emit('updateProfile', { profile: userProfile });
  };

  expire = () => this.removeWidget('expire');

  dismiss = () => this.removeWidget('dismiss');

  insertReactions = () => {
    if (
      this.widgetPayload.reactions &&
      this.widgetPayload.reactions.length > 0 &&
      this.mode === 'timeline'
    ) {
      setTimeout(() => {
        const widgetSel = this.shadowRoot ? this.shadowRoot : this; // Needed because Social Embed widget does not use a shadowRoot because embedded sdk's weren't working with it.
        const select = widgetSel.querySelector('livelike-select');
        const wrapper = document.createElement('widget-reactions');
        wrapper.dataId = this.parentElement
          ? this.parentElement.dataId
          : this.getRootNode().host.dataId;
        wrapper.widgetPayload = this.widgetPayload;
        wrapper.reactions = this.widgetPayload.reactions;
        select.insertAdjacentElement('afterend', wrapper);
      }, 100);
    }
  };

  showTimelineVotes = () => {
    this.phase = 'show-results';
    this.changed = 'changed';
    this.insertReactions();
  };

  shouldExpire = () => this.mode !== 'timeline' && this.expire();
  // Checks if there is a selectedOption and if it has a correct/incorrect.
  // Because of pred follow ups, must find answered in new array.
  resultEvent = () => {
    let vote = this.selectedOption;
    if (vote && vote.hasOwnProperty('is_correct')) {
      const arr = this.choices || this.options;
      vote = arr.find(v => v.id === vote.id);
      const result = { result: vote.is_correct ? 'correct' : 'incorrect' };
      eventDispatcher(this, 'results', result);
    }
  };

  attachTimer = payload => {
    console.log('this.widgetPaytload', this.widgetPayload);
    console.log('this.timeout', this.timeout);
    console.log('payload', payload);
    const timeout = payload && payload.timeout ? payload.timeout : this.timeout;
    if (timeout) {
      const expireInMs = getMilliseconds(timeout);
      const elSel = this.shadowRoot ? this.shadowRoot : document;
      const timer = elSel.querySelector('#livelike-timer');
      timer.timerStarted = false;
      timer.timeout = expireInMs / 1000;
      timer.timerStarted = true;
      return { timer, expireInMs };
    }
  };
  /**
   * Get milleseconds from widget payload. Starts livelike-timer.
   * Waits until Widget timeout completed and disabled widgets and resolves Promise.
   * @param {object} payload Widget payload object.
   * @returns {Promise}
   */
  waitForVotes = payload =>
    new Promise(resolve => {
      if (this.mode === 'pop-up' || this.mode === 'timeline') {
        const t = this.attachTimer(payload);
        setTimeout(() => {
          this.disabled = true;
          t.timer.remove();
          this.insertReactions();
          resolve();
        }, t.expireInMs);
      }
    });

  /**
   * Changes phase, then makes request to get user points.
   * Appends localStorage with response and resolves promise.
   * @param {number} delay setTimeout delay in ms to reolve promise.
   * @returns {Promise}
   */
  voteResults = (delay = 3000) =>
    new Promise(resolve => {
      this.phase = phase.RESULTS;
      this.choices && this.choicesResults(this.resultsHolder);
      this.resultEvent();
      this.userInteracted() &&
      programs.rewards !== 'none' &&
      this.widgetPayload.rewards_url
        ? request
            .post({ url: this.widgetPayload.rewards_url })
            .then(response => {
              this.userPoints = response;
              userProfile.points = response.total_points;
              if (userProfile.gaming) {
                userProfile.gaming.rank = response.rank;
                userProfile.gaming.total_points = response.total_points; // superflouous. change profile.gaming
                eventBus.emit('updateProfile', { profile: userProfile });
                appendLocalStorage('gaming', response);
              }
              setTimeout(resolve, delay);
            })
        : setTimeout(resolve, delay);
    });

  showConfirmation = () =>
    new Promise(resolve => {
      this.phase = phase.CONFIRMING;
      setTimeout(() => {
        this.phase = 'show-results';
        resolve();
      }, 5000);
    });

  userInteracted = () => {
    const sliderMag = this.selectedMag && this.selectedMag.magnitude;
    const selectedId = this.selectedOption && this.selectedOption.id;
    const followUp =
      this.widgetPayload.kind === 'image-prediction-follow-up' ||
      this.widgetPayload.kind === 'text-prediction-follow-up';
    return selectedId || sliderMag || followUp;
  };

  // Creates and inserts either points-display or badge-display element.
  insertEl = type => {
    const sel = this.shadowRoot ? this.shadowRoot : this;
    const article = sel.querySelector('#widget-article');
    const el = document.createElement(type + '-display');
    el.expire = this.expire;
    el.userPoints = this.userPoints;
    article.insertAdjacentElement('beforeend', el);
    return el;
  };

  // If the program has gaming enabled, and the user has interacted with the widget, show points
  displayPoints = () =>
    new Promise(resolve => {
      if (programs.rewards !== 'none' && this.userInteracted()) {
        const points = this.insertEl('points');
        this.phase = phase.POINTS;
        setTimeout(() => {
          points.remove();
          resolve();
        }, 4000);
      } else {
        resolve();
      }
    });

  // If the program has gaming enabled, and there is a next_badge,
  // and the user has interacted with the widget, show badge overlay.
  displayBadge = () => {
    const hasBadges =
      programs.rewards === 'badges' &&
      this.userPoints &&
      this.userPoints.new_badges &&
      this.userPoints.new_badges[0];
    return new Promise(resolve => {
      if (programs.rewards === 'badges' && hasBadges && this.userInteracted()) {
        const badge = this.insertEl('badge');
        this.phase = phase.BADGE;
        setTimeout(() => {
          badge.remove();
          resolve();
        }, 5000);
      } else {
        resolve();
      }
    });
  };
}
