import { isArray } from '../../utils';

const getStickerPacks = (url: string) => {
  return new Promise(resolve => {
    let stickers = [];
    const fetchStickers = (url: string) => {
      fetch(url)
        .then(r => r.json())
        .then(response => {
          if (
            stickers &&
            response &&
            response.results &&
            isArray(response.results) &&
            isArray(stickers)
          ) {
            stickers = [...stickers, ...response.results];
            response.next ? fetchStickers(response.next) : resolve(stickers);
          } else resolve([]);
        })
        .catch(() => resolve([]));
    };
    fetchStickers(url);
  });
};

export const getStickers = url =>
  new Promise(resolve =>
    getStickerPacks(url).then((stickerPacks: any) =>
      resolve({
        stickerPacks,
        stickers: stickerPacks.reduce((a, c) => a.concat(c.stickers), []),
      })
    )
  );
