import { LitElement, html } from 'lit-element';
import { widgetReactionStyles } from './styles/widgetReactionStyles';
import { emojiIcon } from '../icons';
import { _$$ } from '../scripts/internal/globals';
import {
  request,
  loadLocalStorage,
  appendLocalStorage,
  eventBus,
  outsideClick,
} from '../utils';

class WidgetReactions extends LitElement {
  static get properties() {
    return {
      dataId: { type: String },
      widgetPayload: { type: Object },
      selectedReactions: { type: String },
      reactions: { type: Array },
      animate: { type: Number },
    };
  }

  constructor() {
    super();
    this.dataId = null;
    this.widgetPayload = {};
    this.selectedReactions = null;
    this.reactions = [];
    this.animate = 0;
  }
  createRenderRoot() {
    return this;
  }

  firstUpdated() {
    // setInterval(()=>request.get({url: this.widgetPayload.url}).then(r=>{
    //   console.log('interval widget result', r)
    //   this.widgetPayload = r;
    // }), 10000)
  }
  updated(changedProps) {
    changedProps.forEach((prevProp, name) => {
      name === 'dataId' &&
        this.dataId &&
        eventBus.on(`${this.dataId}-outsideClicked`, () =>
          this.toggleReactions(this.selectedReactions)
        );
    });
  }

  toggleReactions = widgetId => {
    if (this.selectedReactions === widgetId) {
      this.animate = 0;
      setTimeout(() => {
        this.selectedReactions = null;
        outsideClick.remove();
      }, 300);
    } else {
      this.selectedReactions = widgetId;
      setTimeout(() => {
        this.animate = 1;
        outsideClick.create(this.dataId);
      }, 50);
    }
  };

  reactionAction = (item, reaction) => {
    const profile = loadLocalStorage(_$$.lsKey);
    if (!profile.reactions) {
      profile.reactions = {};
      profile.reactions = { [item.id]: [] };
      appendLocalStorage('reactions', profile.reactions);
    } else if (profile.reactions && !profile.reactions[item.id]) {
      profile.reactions[item.id] = [];
      appendLocalStorage('reactions', profile.reactions);
    }

    const idx = profile.reactions[item.id].findIndex(id => id === reaction.id);
    let list = { ...profile.reactions };

    const reactionUpdate = (type, n) => {
      this.toggleReactions(item.id);
      request[type]({ url: reaction.react_url }).then(r => {
        if (reaction.sequence === 0 || r.sequence >= reaction.sequence) {
          type === 'post'
            ? list[item.id].push(reaction.id)
            : list[item.id].splice(idx, 1);
          appendLocalStorage('reactions', list);
          const wid = this.widgetPayload.reactions.findIndex(
            r => r.id === reaction.id
          );
          if (wid !== -1) {
            const payload = { ...this.widgetPayload };
            payload.reactions[wid].count += n;
            this.widgetPayload = payload;
          }
        }
      });
    };

    idx === -1 ? reactionUpdate('post', 1) : reactionUpdate('delete', -1);
  };

  renderReactionButton = (item, fn) => html`
    <div class="reaction-button-container">
      <button
        aria-label="Widget reactions"
        class="reaction-button"
        @click=${() => fn(item.id)}
      >
        ${emojiIcon}
      </button>
    </div>
  `;

  renderMiniReactions = reactions => {
    const totalReactions = reactions =>
      reactions.reduce((a, b) => a + b['count'], 0);
    const total = totalReactions(reactions);
    return html`
      <div class="mini-reactions">
        ${reactions.map(reaction =>
          reaction.count > 0
            ? html`
                <img
                  class="mini-reaction-image"
                  src=${reaction.file}
                  alt="Reaction ${reaction.name}"
                />
              `
            : null
        )}
        ${total > 0
          ? html`
              <span
                aria-label="Reaction total number"
                class="mini-reaction-count"
              >
                ${total}
              </span>
            `
          : null}
      </div>
    `;
  };

  renderReactions = (item, reactionPack, animate, fn) => html`
    <div
      class="reaction-item-container"
      style="opacity: ${animate}; z-index: ${animate > 0 ? 25 : -1} "
    >
      <div class="reaction-item-wrap">
        ${reactionPack.map(reaction => {
          const isSelected = (item, rId) => {
            const profile = loadLocalStorage(_$$.lsKey);
            if (profile.reactions && profile.reactions[item.id]) {
              const idx = profile.reactions[item.id].findIndex(
                id => id === rId
              );
              if (idx !== -1) return true;
            }
          };
          const selected = isSelected(item, reaction.id) ? 'selected' : '';
          return html`
            <button
              aria-label="${reaction.name}"
              class="reaction-item ${selected}"
              @click=${() => fn(item, reaction)}
            >
              <img class="reaction-image" src=${reaction.file} alt=""/>
              ${reaction.count > 0
                ? html`
                    <span class="reaction-count">${reaction.count}</span>
                  `
                : null}
            </button>
          `;
        })}
      </div>
    </div>
  `;

  render() {
    return this.reactions && this.reactions.length
      ? html`
          ${widgetReactionStyles}
          <div class="reactions-container">
            ${this.renderReactions(
              this.widgetPayload,
              this.reactions,
              this.animate,
              this.reactionAction
            )}
            <div class="reactions-button-mini">
              ${this.renderReactionButton(
                this.widgetPayload,
                this.toggleReactions
              )}
              ${this.renderMiniReactions(this.reactions)}
            </div>
          </div>
        `
      : null;
  }
}

customElements.define('widget-reactions', WidgetReactions);
