import { html } from 'lit-element';
import { component, useState, useEffect } from 'haunted';
import { debounce, eventBus } from '../utils';
import { convertMessages } from '../scripts/internal/convertMessages';
import './livelike-message-item';

function MessageList({
  messageList = [],
  deletedMessages,
  timeFormat,
  langCode,
  timestamps,
  stickers,
  blockList,
  channel,
  programId = null,
  elId,
  dataId,
  messagemenus,
  debug,
  messagesLoaded,
  chatReactionPacks,
}) {
  const scrollEvt = `${dataId}-scrollDown`;

  const [userScrolled, setUserScrolled] = useState(false);
  const [messageArr, setMessageArr] = useState([]);
  const [listLoaded, setListLoaded] = useState(false);

  const scrollListener = () => {
    const scrolled = this.scrollTop + this.clientHeight === this.scrollHeight;
    setUserScrolled(!scrolled);
  };

  const scrollToBottom = () =>
    this.shadowRoot.querySelector('#anchor').scrollIntoView();

  let count = 0;
  const forceScroll = () => {
    count < 10 &&
      setTimeout(() => {
        scrollToBottom();
        forceScroll();
        count++;
      }, 50);
  };

  useEffect(() => {
    if (messageArr && messageArr.length > 0) {
      if (listLoaded) {
        !userScrolled && forceScroll();
      } else {
        setListLoaded(true);
      }
    }
  }, [messageArr]);

  useEffect(() => {
    messageList &&
      messageList.length > 0 &&
      messageList[0] !== 'No Messages' &&
      messageList[0] !== 'Waiting' &&
      convertMessages({ messageList, stickers }).then(setMessageArr);
  }, [messageList, deletedMessages]);

  useEffect(() => {
    eventBus.emit(`${dataId}-userScrolled`, userScrolled);
  }, [userScrolled]);

  useEffect(() => {
    listLoaded && forceScroll();
  }, [listLoaded]);

  useEffect(() => {
    scrollToBottom();
    this.addEventListener('scroll', debounce(scrollListener));
    eventBus.on(scrollEvt, scrollToBottom);
    return () => {
      this.removeEventListener('scroll', debounce(scrollListener));
      eventBus.off(scrollEvt, scrollToBottom);
    };
  }, [elId]);

  return html`
    <style>
      :host {
        overflow: auto;
      }
      livelike-chat-message-list {
        overflow: auto;
      }
    </style>
    ${messagesLoaded && listLoaded && messageArr && messageArr.length > 0
      ? messageArr.map(
          item => html`
            <livelike-message-item
              .item=${item}
              .elId=${elId}
              .debug=${debug}
              .dataId=${dataId}
              .channel=${channel}
              .langCode=${langCode}
              .blockList=${blockList}
              .programId=${programId}
              .timestamps=${timestamps}
              .timeFormat=${timeFormat}
              .messagemenus=${messagemenus}
              .deletedMessages=${deletedMessages}
              .chatReactionPacks=${chatReactionPacks}
            ></livelike-message-item>
          `
        )
      : html`
          <slot name="message-list-loading"></slot>
        `}
    <div class="anchor-bottom" id="anchor"></div>
  `;
}

customElements.define('livelike-chat-message-list', component(MessageList), {
  useShadowDOM: false,
});
