import { html } from 'lit-element';
import { component, useState, useEffect } from 'haunted';
import { sendMessage } from '../scripts/sendMessage';
import { eventBus } from '../utils';
import { sendIcon } from '../icons';
import { textInputStyles } from './styles/textInputStyles';
import './sticker-display';
import './scroll-down';

function TextInput({ elId, dataId, stickerPacks, placeHolder, syncStrategy }) {
  /**
   * To be able to separate these components in the future and allow for composition,
   * props can't be passed down. Using rootNode(), this element can get any of the parents properties.
   * this.getRootNode().host
   * Either need to write a function that run this so value changes can be tracked, or livelike-chat
   * needs to emit events for the values changes and a deep compare function must be run.
   */

  const [state, setState] = useState({ message: '' });
  // const [dataId, setDataId] = useState('')
  // const [elId, setElId] = useState('')
  // const [stickerPacks, setStickerPacks] = useState([])
  // console.log('elId', elId)
  // console.log('dataId', dataId)
  // console.log('stickerPacks', stickerPacks)
  useEffect(() => {
    const chat = this.getRootNode().host;
    // setElId(chat.elId)
    // setDataId(chat.dataId)
    // setStickerPacks(chat.stickersPacks)
    eventBus.on(`${dataId}-sendSticker`, e => addSticker(e.event));
    return () => {
      eventBus.off(`${dataId}-sendSticker`, e => addSticker(e.event));
    };
  }, []);

  const addSticker = sticker => {
    setState(prevState => ({
      ...prevState,
      message: prevState.message + sticker,
    }));
    this.shadowRoot.getElementById('textArea').focus();
  };

  useEffect(() => {
    resize();
  }, [state]);

  const handleMessage = e => setState({ message: e.target.value });

  const handleSend = () => {
    if (state.message.match(/([^\n]+\n)*[^\n]+/g)) {
      setTimeout(resetMessage, 0);
      sendMessage({
        message: state.message,
        roomId: elId,
        timestamp:
          syncStrategy && syncStrategy.currentTimecode
            ? syncStrategy.currentTimecode
            : null,
      });
    }
  };

  const keyPress = e => {
    if (e.which === 13 && !e.shiftKey) {
      e.preventDefault();
      state.message && handleSend();
    }
  };

  const resetMessage = () => {
    setState({ message: '' });
    resize('40px');
  };

  const resize = initialHeight => {
    const textArea = this.shadowRoot.getElementById('textArea');
    if (textArea) {
      if (initialHeight) {
        textArea.style.height = initialHeight;
      }
      if (textArea) {
        textArea.style.height = '0';
        textArea.style.height = `${getHeight(textArea)}px`;
      }
    }
  };

  const getHeight = textArea => {
    const { borderBottomWidth, borderTopWidth } = window.getComputedStyle(
      textArea
    );
    const scrollHeight =
      textArea.scrollHeight +
      parseFloat(borderBottomWidth) +
      parseFloat(borderTopWidth);
    return scrollHeight;
  };

  const renderStickerDisplay = () =>
    stickerPacks && stickerPacks.length > 0
      ? html`
          <sticker-display
            .dataId=${dataId}
            .stickerPacks=${stickerPacks}
          ></sticker-display>
        `
      : null;

  return html`
    ${textInputStyles}
    <div class="input-container">
      <textarea
        id="textArea"
        maxlength="200"
        style="height: 40px"
        class="message-input"
        placeholder="${placeHolder}"
        @input=${handleMessage}
        @keydown=${keyPress}
        .value=${state.message}
      ></textarea>
      ${renderStickerDisplay()}
      <scroll-down .dataId=${dataId}></scroll-down>
      <button
        @click="${handleSend}"
        aria-label="Send Message"
        class="send-icon"
      >
        ${sendIcon}
      </button>
    </div>
  `;
}

customElements.define('livelike-chat-composer', component(TextInput));
