import { request } from '../../utils';
import { Widget } from '../base/Widget';
import { Voteable } from '../base/Voteable';
import { getMilliseconds } from '../../utils';
import { baseSliderStyles } from './styles/baseSliderStyles';

export class SliderBase extends Voteable(Widget) {
  static get properties() {
    const props = Object.assign({}, super.properties, {
      widgetPayload: { type: Object },
      question: { type: String },
      options: { type: Array },
      average_magnitude: { type: String },
      initial_magnitude: { type: String },
      selectedMag: { type: Number },
      interacted: { type: String },
      disabled: { type: Boolean },
    });
    return props;
  }

  constructor() {
    super();
    this.average_magnitude = '';
    this.selectedMag = {};
    this.interacted = '';
  }

  firstUpdated() {
    this.initialTimelineLoad
      ? this.showTimeline()
      : this.waitForSliderVotes()
          .then(mag => this.submitResults(mag))
          .then(() => this.voteResults())
          .then(() => this.displayPoints())
          .then(() => this.displayBadge())
          .then(this.shouldExpire);
  }

  showTimeline = () => {
    this.showTimelineVotes();
    this.initial_magnitude = this.widgetPayload.average_magnitude;
    this.average_magnitude = this.widgetPayload.average_magnitude;
    this.waitForSliderVotes();
  };

  waitForSliderVotes = () => {
    return new Promise((resolve, reject) => {
      const imageArr = [];
      this.options.map((item, idx) => {
        imageArr.push({
          img: item.image_url,
          max: (idx / this.options.length) * 100,
        });
      });
      let interacted = false;
      const sRoot = this.shadowRoot;
      const sliderEl = sRoot.getElementById('emoji-slider');
      const formEl = sliderEl.parentNode;
      formEl.style.setProperty('--val', (val = this.initial_magnitude * 100));
      sliderEl.addEventListener('change', () => (interacted = true));
      sliderEl.addEventListener('change', updateVal);
      sliderEl.addEventListener('input', () => (interacted = true));
      sliderEl.addEventListener('input', updateVal);

      let val = null;

      function updateVal() {
        let newVal = +sliderEl.value;
        if (val !== newVal) {
          interacted = true;
          formEl.style.setProperty('--val', (val = newVal));
          for (let item of imageArr) {
            if (val >= item.max) {
              sRoot.getElementById('emoji-output').src = item.img;
            }
          }
        }
      }

      updateVal();

      if (this.mode === 'pop-up' || this.mode === 'timeline') {
        if (this.disabled) {
          sliderEl.disabled = true;
        } else {
          const expireInMs = getMilliseconds(this.widgetPayload.timeout);
          const timer = this.shadowRoot.getElementById('livelike-timer');
          timer.timerStarted = false;
          timer.timeout = expireInMs / 1000;
          setTimeout(() => (timer.timerStarted = true), 0);
          setTimeout(() => {
            // console.log('after timeout interacted', interacted)
            this.disabled = true;
            sliderEl.disabled = true;
            resolve(interacted ? val : null);
            this.insertReactions();
          }, expireInMs);
        }
      }
    });
  };

  submitResults = mag => {
    // console.log('mag',mag);
    return new Promise((resolve, reject) => {
      // console.log('submitResults this.selectedMag', this.selectedMag);
      if (mag) {
        // console.log('this.interacted', this.interacted);
        const url = this.widgetPayload.vote_url;
        const magDec = mag / 100;
        const magStr = magDec.toString();
        this.selectedMag = { magnitude: magStr }; // To allow for points display in Widgets
        return request
          .post({ url, data: { magnitude: magStr } })
          .then(() => resolve());
      } else resolve();
    });
  };

  static get styles() {
    return [super.styles, baseSliderStyles];
  }
}
