import { html } from 'lit-element';
import { component } from 'haunted';
import { badgeDisplayStyles } from './styles/badgeDisplayStyles';

const BadgeDisplay = ({
  userPoints,
  expire,
  description = 'Congrats! You just earned a new badge!',
}) => {
  return html`
    ${badgeDisplayStyles}
    <div class="badge-display">
      <img
        class="overlay-badge"
        src=${userPoints.new_badges[0].file}
        alt="Badge"
      />
      <div class="overlay-title">${userPoints.new_badges[0].name}</div>
      <div>${description}</div>
      <div class="overlay-button-container">
        <button class="overlay-button" @click=${() => expire()}>
          Collect
        </button>
      </div>
    </div>
  `;
};

customElements.define('badge-display', component(BadgeDisplay));
