import { LitElement, html } from 'lit-element';
import { timerStyles } from './styles/timerStyles';
import { eventBus } from '../utils';

export class Timer extends LitElement {
  static get properties() {
    return {
      timeout: { type: Number },
      timeStarted: { type: Number },
      timeLeft: { type: Number },
      pageVisible: { type: Boolean },
      started: { type: Boolean },
      mode: { type: String },
      displayTimer: { type: Boolean },
    };
  }

  constructor() {
    super();
    this.pageVisible = !document.hidden;
    eventBus.on(
      'pageVisibilityChange',
      e => (this.pageVisible = e.pageVisible)
    );
  }

  static get styles() {
    return timerStyles;
  }

  firstUpdated() {
    this.timeStarted = new Date().getTime();
    this.timeLeft = this.timeout;
  }

  updated(changedProps) {
    changedProps.forEach((prevProp, name) => {
      name === 'started' &&
        this.started &&
        this.pageVisible &&
        this.calcTimeLeft();
      name === 'pageVisible' && this.pageVisible && this.calcTimeLeft();
      name === 'displayTimer' && this.displayTimer && this.setupTimer();
    });
  }

  calcTimeLeft = () => {
    const timeDiff = new Date().getTime() - this.timeStarted;
    const diffSec = timeDiff / 1000;
    this.timeLeft = this.timeout - diffSec;
    this.displayTimer = true;
  };

  setWidth = el => {
    const decimal = this.timeLeft / this.timeout;
    const startingWidth = (1 - decimal) * 100;
    el.style.width = `${startingWidth}%`;
    return Promise.resolve();
  };

  setupTimer = () => {
    const sel = this.shadowRoot ? this.shadowRoot : this;
    const el = sel.querySelector('.livelike-timer');
    this.setWidth(el).then(() =>
      setTimeout(() => (el.style.width = '100%'), 10)
    );
  };

  render() {
    return html`
      ${this.mode !== 'timeline' && this.displayTimer
        ? html`
            <div
              class="livelike-timer"
              style="transition-duration: ${this.timeLeft}s"
            ></div>
          `
        : null}
    `;
  }
}

customElements.define('livelike-timer', Timer);