import { loadLocalStorage } from '.';
import { _$$, _config, userProfile } from '../scripts/internal/globals';
import { IRequestOptions } from '../types';

const createPromise = (type: string) => {
  const req = (opts: IRequestOptions): Promise<any> => {
    return new Promise((resolve, reject) => {
      if (!opts.url && !opts.path)
        return console.warn('Need either a url or a path');
      let data: string, endpoint: string;
      const storedProfile = loadLocalStorage(_$$.lsKey);

      const token =
        opts.accessToken ||
        _$$.accessToken ||
        userProfile.access_token ||
        storedProfile.access_token;

      opts.data && (data = opts.data);
      opts.url
        ? (endpoint = opts.url)
        : opts.path && (endpoint = _config.endpoint + opts.path);

      const headers = new Headers();
      !opts.noAuth && headers.append('Authorization', 'Bearer ' + token);

      if (type === 'POST' || type === 'PATCH') {
        headers.append('Content-Type', 'application/json');
        data = JSON.stringify(data);
      }

      const request = new Request(endpoint, {
        method: type,
        body: data,
        headers: headers,
      });

      fetch(request)
        .then(promiseResponse => promiseResponse.json())
        .then(data => resolve(data))
        .catch(err => reject(err));
    });
  };
  return req;
};

export const request = {
  get: createPromise('GET'),
  put: createPromise('PUT'),
  post: createPromise('POST'),
  patch: createPromise('PATCH'),
  delete: createPromise('DELETE'),
};
