import { hasSyncStrategy, isReadySync, getSyncTimecode } from './utils';

export function ContentSession(el: any) {
  let queueArray = [];

  return {
    queueArray,
    hasNextItem: () => queueArray && queueArray.length > 0,
    isItemReady: () =>
      queueArray && isReadySync(el.syncStrategy, queueArray[0]),
    dequeueItem: () => queueArray && queueArray.shift(),
    enqueueItem: (payload: any, cb: Function) => {
      if (queueArray) {
        queueArray.push(payload);
        hasSyncStrategy(el.syncStrategy) &&
          queueArray.sort((a, b) => {
            const atc = getSyncTimecode(el.syncStrategy, a);
            const btc = getSyncTimecode(el.syncStrategy, b);
            return atc > btc ? 1 : btc > atc ? -1 : 0;
          });
        cb && cb();
      }
    },
  };
}
