import { html } from 'lit-element';

export const richPostStyles = html`
  <style>
    .widget {
      color: var(--widget-color);
      width: var(--widget-width);
      background: var(--rich-post-background-color);
      margin: var(--widget-margin);
      position: var(--widget-position);
      border: var(--widget-border);
      border-radius: var(--widget-border-radius);
    }

    .widget-header {
      background-color: black;
      display: flex;
      border-radius: var(--widget-header-border-radius);
    }

    .widget-header {
      background-color: var(--social-embed-primary-color);
      background-image: linear-gradient(
        90deg,
        var(--social-embed-secondary-color) 0%,
        var(--social-embed-primary-color) 100%
      );
      padding-right: 1rem;
    }
    .widget-question {
      padding: 0.5rem 0rem 0.5rem 1rem;
    }

    .rich-post-widget {
      border-radius: var(--widget-border-radius);
      background-color: var(--widget-background-color);
    }
    .rich-post-body {
      margin: var(--rich-post-margin);
      padding: var(--rich-post-padding);
      overflow-wrap: break-word;
    }
    .rich-post-body > *:first-child {
      padding-top: 0;
      margin-top: 0;
    }
    .rich-post-body > *:last-child {
      padding-bottom: 0;
      margin-bottom: 0;
    }
  </style>
`;
