import { html } from 'lit-element';
import { SocialEmbedBase } from './SocialEmbedBase';
import { socialEmbedStyles } from './styles/socialEmbedStyles';
import { widgetBaseStyles } from '../base/styles/widgetBaseStylesHTML';
import '../../components/livelike-timer';
import { rightChevron, leftChevron } from '../../icons';

export class SocialEmbedWidget extends SocialEmbedBase {
  static get styles() {
    return [super.styles];
  }
  createRenderRoot() {
    return this;
  }

  renderButtons = () => html`
    <div class="button-wrapper">
      <button class="left-button slide-button" @click=${this.movePrev}>
        ${leftChevron}
      </button>
      <button class="right-button slide-button" @click=${this.moveNext}>
        ${rightChevron}
      </button>
    </div>
  `;

  renderDots = () =>
    html`
      <form class="dot-nav-container">
        ${this.items.map(
          (v, i) =>
            html`
              <input
                class="dot-input"
                id="dot${i}"
                name="dot"
                type="radio"
                @click=${() => this.moveToSlide(i)}
              />
            `
        )}
      </form>
    `;

  render() {
    const multi = this.items.length > 1;
    return html`
      ${socialEmbedStyles} ${widgetBaseStyles}
      <article
        class="widget ${this.scriptsLoaded && this.finalLoad ? '' : 'loading'}"
      >
        <header id="widgetHeader" class="widget-header">
          <h1 class="widget-question">${this.comment}</h1>
        </header>
        <livelike-timer id="livelike-timer"></livelike-timer>
        <div class="slider-wrapper">
          ${multi ? this.renderButtons() : null}
          <div class="slider">
            ${this.items.length > 0 &&
              this.items.map(
                (item, idx) =>
                  html`
                    <div
                      id="embed${idx}"
                      class="slide ${idx === 0 ? 'initial' : ''}"
                    ></div>
                  `
              )}
          </div>
          ${multi ? this.renderDots() : null}
        </div>
      </article>
    `;
  }
}
// style="display: ${this.scriptsLoaded ? 'block' : 'none'};"

customElements.define('livelike-social-embed', SocialEmbedWidget);
