import { css } from 'lit-element';

export const cheerMeterStyles = css`
  .widget-header {
    color: var(--cheer-meter-header-text-color);
    background-color: var(--widget-background-color);
  }
  .widget-select {
    background-color: var(--widget-background-color);
    border-radius: var(--widget-select-border-radius);
    // transform:translateY(-3px);
  }
  @keyframes image0 {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.2);
    }

    100% {
      transform: scale(1);
    }
  }
  @keyframes image1 {
    0% {
      transform: scale(1.2);
    }
    50% {
      transform: scale(1);
    }

    100% {
      transform: scale(1.2);
    }
  }

  .cheer-meter-body {
    background-color: var(--widget-background-color);
  }

  .cheer-versus {
    position: absolute;
    top: 3.625rem;
    left: 50%;
    margin-left: -20px;
    z-index: 100;
    transition: all 0.3s;
    display: flex;
  }
  .cheer-versus.inactive {
    display: none;
    opacity: 0;
    transition: all 0.3s;
  }

  .cheer-image-body,
  .cheer-description-body {
    display: flex;
  }
  .cheer-image-container,
  .cheer-description-container {
    width: 50%;
    position: relative;
    overflow: hidden;
    display: table-cell;
    opacity: 1;
    transition: all 0.4s;
  }
  .cheer-image-container:hover {
    cursor: pointer;
  }
  .cheer-image-container.disabled:hover {
    cursor: default;
  }
  .cheer-image-container.active {
    width: 100%;
    transition: all 0.4s;
  }
  .cheer-image-container.inactive {
    width: 0%;
    transition: all 0.4s;
  }

  .cheer-description-wrap {
    position: relative;
    padding: 3px 0;
    width: 100%;
    font-size: 14px;
    color: #fff;
    font-weight: 400;
    line-height: 1;
    z-index: 1;
    text-align: center;
    transition: all 0.3s;
  }
  .cheer-description-wrap.description0 {
    color: var(--cheer-meter-side-one-text-color);
    background: var(--cheer-meter-side-one-header-color);
  }
  .cheer-description-wrap.description1 {
    color: var(--cheer-meter-side-two-text-color);
    background: var(--cheer-meter-side-two-header-color);
  }

  .cheer-image-wrap {
    text-align: center;
    padding: 20px 0;
    position: relative;
  }

  .cheer-image {
    position: relative;
    height: 80px;
    z-index: 5;
    top: 0;
    transition: all 0.2s;
    vertical-align: middle;
  }
  .cheer-image.image0:not(.active) {
    animation: image0 2s linear 0s infinite normal;
  }
  .cheer-image.image1:not(.active) {
    animation: image1 2s linear 0s infinite normal;
  }
  .cheer-image.active:active {
    transition: all ease 0.3s;
  }

  .cheer-image-container.active.enabled .cheer-image-wrap.enabled:after {
    content: '';
    position: absolute;
    display: block;
    z-index: 1;
    width: 100px;
    height: 100px;
    border-radius: 100%;
    top: 10px;
    left: 50%;
    margin-left: -50px;
    opacity: 0;
    transition: all 0.2s;
  }
  .cheer-image-container.active.enabled .cheer-image-wrap.enabled:active:after {
    opacity: 1;
    width: 200px;
    height: 200px;
    top: -40px;
    margin-left: -100px;
  }
  .cheer-image-container.active .cheer-image-wrap.enabled:active img {
    -webkit-transform: scale(0.9);
    -webkit-transform: rotate(10deg);
  }
  .cheer-image-container.active .cheer-image-wrap.image0.enabled:after {
    border: 2px solid rgba(80, 160, 250, 0.6);
    background: rgba(80, 160, 250, 0.4);
  }
  .cheer-image-container.active .cheer-image-wrap.image1.enabled:after {
    border: 2px solid rgba(250, 80, 100, 0.6);
    background: rgba(250, 80, 100, 0.4);
  }

  .cheer-score {
    font-size: 10px;
    font-weight: bold;
    color: #fff;
    width: 100%;
    text-align: right;
    position: absolute;
    bottom: 16px;
    padding-right: 16px;
    opacity: 1;
    transition: all 0.3s;
    right: 0;
  }
  .cheer-score.inactive {
    opacity: 0 !important;
  }
  .cheer-score-number {
    font-size: 32px;
    font-weight: 200;
    display: block;
    line-height: 1;
    padding-right: 2px;
  }
`;
