import PubNub from 'pubnub';
import { userProfile, _$$ } from './globals';
import { IMessageObject, IMessageListOptions } from '../../types';

let pubnub: PubNub;
declare global {
  interface Window {
    PTEST: any;
  }
}
export const pubnubConnect = ({
  subscribeKey,
  publishKey,
  origin,
  authKey
}) =>
  new Promise(resolve => {
    _$$.logger && console.table({subscribeKey, publishKey, authKey, profileToken: userProfile.access_token})
    pubnub = new PubNub({
      uuid: userProfile.id,
      subscribeKey,
      authKey,
      publishKey,
      origin,
      restore: true,
    });
    window.PTEST = pubnub;
    _$$.pn = pubnub;
    resolve();
  });

export function pubnubHandler() {
  const generateUUID = () => PubNub.generateUUID();

  const publishMessage = async (args: {
    message: IMessageObject;
    channel: string;
    meta?: any;
  }): Promise<PubNub.PublishResponse> => {
    const timetoken = await pubnub.publish({
      message: args.message,
      channel: args.channel,
      meta: args.meta,
      ttl: 0,
    });
    return timetoken;
  };

  // function params<TRequired>(): <TOptional>(
  //   o: TOptional
  // ) => Partial<TOptional> & TRequired;
  // function params<TOptional>(o: TOptional): Partial<TOptional>;
  // function params<T>(
  //   o?: T
  // ): Partial<T> | (<TOptional>(o: TOptional) => Partial<TOptional> & T) {
  //   if (o != null) {
  //     return o;
  //   } else {
  //     return function(o: any) {
  //       return o;
  //     } as any;
  //   }
  // }

  // Allows for optional object param types
  function params<T>(o: T): Partial<T> {
    return o;
  }
  const getMessages = async (
    channel: any,
    opts = params({ count: 50, end: null, start: null })
  ): Promise<PubNub.FetchMessagesResponse> => {
    const response = await pubnub.fetchMessages({
      channels: [channel],
      count: opts.count,
      stringifiedTimeToken: true,
      start: opts.start,
      end: opts.end,
      includeMessageActions: true,
    });
    return response;
  };

  const messageAction = {
    add: (
      channel: string,
      messageTimetoken: string,
      action: { type: string; value: string },
      cb: any
    ) =>
      pubnub.addMessageAction(
        {
          channel,
          messageTimetoken,
          action,
        },
        function(status, response) {
          status.error
            ? console.error('Error creating reaction')
            : cb(response);
        }
      ),
    remove: (
      channel: string,
      messageTimetoken: string,
      actionTimetoken: string,
      cb: any
    ) =>
      pubnub.removeMessageAction(
        {
          channel,
          messageTimetoken,
          actionTimetoken,
        },
        function(status, response) {
          status.error
            ? console.error('Error creating reaction')
            : cb(response);
        }
      ),
  };

  return {
    publishMessage,
    getMessages,
    generateUUID,
    messageAction,
    subscribe: (channel: string) => pubnub.subscribe({ channels: [channel] }),
    unsubscribe: (channel: string) => pubnub.subscribe({ channels: [channel] }),
    addListener: (listener: PubNub.ListenerParameters) =>
      pubnub.addListener(listener),
    removeListener: (listener: PubNub.ListenerParameters) =>
      pubnub.removeListener(listener),
    connect: ({ channel, listener }) => {
      pubnub.addListener(listener);
      pubnub.subscribe({ channels: [channel] });
    },
    disconnect: ({ channel, listener }) => {
      pubnub.removeListener(listener);
      pubnub.unsubscribe({ channels: [channel] });
    },
  };
}

const {
  publishMessage,
  getMessages,
  messageAction,
  generateUUID,
  subscribe,
  unsubscribe,
  addListener,
  removeListener,
  connect,
  disconnect,
} = pubnubHandler();

export {
  publishMessage,
  getMessages,
  messageAction,
  generateUUID,
  subscribe,
  unsubscribe,
  addListener,
  removeListener,
  connect,
  disconnect,
};

