import { request } from '../../utils';
import { widgetNodeVote } from '../../constants/WidgetNodes';
import { eventDispatcher } from '../../utils';

export const Voteable = superclass =>
  class extends superclass {
    updated(changedProps) {
      changedProps.forEach((prevProp, name) => {
        if (name === 'vote') {
          const evt = widgetNodeVote[this.nodeName];
          eventDispatcher(this, evt, {
            [evt]: this.vote,
            element: this,
            widget: this.widgetPayload,
          });
        }
      });
    }

    isSelected = option =>
      this.selectedOption && this.selectedOption.id === option.id
        ? true
        : false;

    selectedClass = option => (this.isSelected(option) ? 'selected' : '');

    correctClass = item => {
      if (this.phase !== 'voting') {
        return item.is_correct ? 'result-correct' : 'result-incorrect';
      }
    };

    voteOption(option) {
      // console.log('VoteOption option', option);
      if (!this.voteDisable) {
        this.dispatchEvent(
          new CustomEvent('recordPrediction', { detail: { option } })
        );

        this.vote ? this.updateVote(option) : this.createVote(option);
      }
    }

    votePercentage(voteCount, defaultCount = 0) {
      let totalVotes = 0;
      this.options
        ? this.options.map(item => (totalVotes += item.vote_count))
        : this.choices.map(item => (totalVotes += item.answer_count));

      return totalVotes > 0
        ? Math.round((voteCount / totalVotes) * 100)
        : defaultCount;
    }

    voted = payload => {
      // console.log('this.vote', payload);
      this.voteDisable = false;
      this.vote = payload;
    };
    // Must check for vote_url or answer_url, with the latter being of the quiz type.
    createVote = (option, data) => {
      // console.log('createVote', option);
      this.voteDisable = true;
      let url = option.vote_url ? option.vote_url : option.answer_url;
      request.post({ url, data }).then(this.voted);
    };

    updateVote = option => {
      // console.log('updateVote option', option);
      this.voteDisable = true;
      const key = option.answer_url ? 'choice_id' : 'option_id';
      let data = new FormData();
      data.set(key, option.id);
      request.put({ url: this.vote.url, data }).then(this.voted);
    };
  };
