import { css } from 'lit-element';

export const selectStyles = css`
  :host {
    display: block;
  }
  :host([hidden]) {
    display: none;
  }
  :host ::slotted(livelike-option) {
    cursor: pointer;
  }
  :host([disabled]) ::slotted(livelike-option) {
    cursor: default;
  }

  //ie and edge
  livelike-select {
    display: block;
  }
  livelike-select([hidden]) {
    display: none;
  }
  livelike-select ::slotted(livelike-option) {
    cursor: pointer;
  }
  livelike-select([disabled]) ::slotted(livelike-option) {
    cursor: default;
  }
`;
