import { component, useState, html } from 'haunted';
import { loadMoreWidgetsStyles } from './styles/loadMoreWidgetsStyles';

export function LoadMore({ el, nextPage }) {
  const [state, setState] = useState({
    loaded: true,
    nextPage: nextPage,
  });
  const loadPage = () => {
    setState({ ...state, loaded: false });
    el.widgetController
      .getTimeline(state.nextPage)
      .then(({ loaded, nextPage }) => setState({ ...state, loaded, nextPage }));
  };

  return state.nextPage !== null
    ? html`
        ${loadMoreWidgetsStyles}
        <button
          aria-label="Load more widgets"
          @click="${state.loaded ? loadPage : null}"
          class="loadmore-widget-button"
        >
          ${state.loaded
            ? 'Load More'
            : html`
                <div class="loading-spinner"></div>
              `}
        </button>
      `
    : null;
}

customElements.define('loadmore-widgets', component(LoadMore));

