import { css } from 'lit-element';

export const optionStyles = css`
  :host {
    border-radius: var(--widget-option-border-radius);
    display: block;
    overflow: hidden;
    position: relative;
  }

  :host([disabled]) {
    cursor: not-allowed;
  }

  :host:before {
    border: 2px solid;
    border-color: transparent;
    border-radius: var(--widget-option-border-radius);
    content: '';
    opacity: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: all 0.3s;
    z-index: 1;
  }

  :host([selected]):before {
    opacity: 1;
    z-index: 10;
  }

  // ie and edge
  livelike-option {
    border-radius: 6px;
    display: block;
    overflow: hidden;
    position: relative;
  }

  livelike-option([disabled]) {
    cursor: not-allowed;
  }

  livelike-option:before {
    border: 2px solid;
    border-color: transparent;
    border-radius: var(--widget-option-border-radius);
    content: '';
    opacity: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: all 0.3s;
    z-index: 1;
  }

  livelike-option([selected]):before {
    opacity: 1;
    z-index: 10;
  }
`;
