import { SyncStrategy } from '../../videosync/SyncStrategy';
/**
 * If no syncStrategy is passed, default SyncStrategy class is added to LiveLike object.
 * @param {*} [syncStrategy]
 */
export const getSyncStrategy = (strat: any) =>
  new Promise(res => {
    let ss = strat;
    if (!strat) {
      ss = new SyncStrategy();
    } else if (strat.constructor.name === 'Object') {
      function sync() {}
      sync.prototype = strat;
      ss = new sync();
    }
    res(ss);
  });
