/**
 * Custom Widget Lifecycle Adapter
 *
 * The order of the lifecycles are as follows:
 * 1. 'beforeWidgetAttached'
 *    a. Fires the 'beforeWidgetAttached' event on the <livelike-widgets> element.
 *    b. Creates the widget element.
 * 2. adapter 'beforeWidgetAttached'
 * 3. 'widgetAttached'
 *    a. Widget element is attached to DOM.
 *    b. Fires the 'widgetAttached' event on the <livelike-widgets> element.
 * 4. adapter 'widgetAttached'
 * 5. 'beforeWidgetDetached'
 *    a. Fires the 'beforeWidgetDetached' event on the <livelike-widgets> element.
 * 6. adapter 'beforeWidgetDetached'
 * 7. 'widgetDetached'
 *    a. Fires the 'beforeWidgetDetached' event on the <livelike-widgets> element.
 *    b. Removes widget element from DOM.
 * 8. adapter 'widgetDetached'
 *
 * Each method in the custom adapter has access to an object with the properties 'element', the created
 * widget element, and 'widget', the widget data object {element: HTMLElement, widget: Object}
 *
 * Pass in an object with any number of lifecycle methods that resolve a Promise to "plug in" to any of the
 * lifecycle methods.
 *
 * If the custom methods do not return a promise, it is necessary to perform the cleanup yourself.
 *
 * For example:
 *
 * const adapter = {
 *    beforeWidgetDetached: ({widget, element}) => {
 *      return new Promise(resolve => {
 *        const d = document.createElement('div')
 *        d.style = 'height: 100px; width: 200px; background: green';
 *        d.innerText = 'Congratulations!'
 *        element.shadowRoot.append(d)
 *        setTimeout(resolve, 5000)
 *      })
 *    }
 * }
 *
 * Will result in the widget being created and attached like normal, but before
 * the widget gets removed, the custom div containing the "Congratulations" text
 * will be displayed for 5 seconds before the widget is removed. Because this method
 * returns a Promise that gets resolved, nothing else needs to be done, and 'widgetDetached'
 * will fire like normal after the custom 'beforeWidgetDetached' 5 second timer elapses.
 *
 * If a Promise is not returned and resolved, the following defualt lifecycles will not be fired.
 * Normally, only a widget in the 'mode=timeline' will not be removed form the DOM.
 * But this behavior can be overwritten by adding a custom 'beforeWidgetDetached' method
 * that does not return a Promise that resolves.
 *
 * Or if widget removal is desired, but not in the default way, a custom method can be written:
 *
 * const adapter = {
 *    beforeWidgetDetached: ({widget, element}) => {
 *      function createCongrats() {
 *        return Promise(resolve => {
 *          const d = document.createElement('div')
 *          d.style = 'height: 100px; width: 200px; background: green';
 *          d.innerText = 'Congratulations!'
 *          resolve(d);
 *        })
 *      }
 *      createCongrats().then(el => {
 *        element.shadowRoot.append(d);
 *        setTimeout(() => {
 *          element.remove();
 *        }, 5000)
 *      })
 *    }
 * }
 *
 */

const lifecycles = [
  'beforeWidgetAttached',
  'widgetAttached',
  'beforeWidgetDetached',
  'widgetDetached',
];

function AdapterBase() {
  lifecycles.forEach(v => (this[v] = () => Promise.resolve()));
}

export function WidgetAdapter(adapter) {
  AdapterBase.call(this);
  if (adapter) {
    for (let func in adapter) {
      this[func] = adapter[func];
    }
  }
}
