import { html } from 'lit-element';
import { component, useEffect, useState } from 'haunted';
import { eventBus } from '../utils';
import { downIcon } from '../icons';
import { scrollDownStyles } from './styles/scrollDownStyles';

function ScrollDown({ dataId }) {
  const [userScrolled, setUserScrolled] = useState(false);
  useEffect(() => {
    eventBus.on(`${dataId}-userScrolled`, setUserScrolled);
    return () => eventBus.off(`${dataId}-userScrolled`, setUserScrolled);
  });
  return html`
    ${scrollDownStyles}
    <button
      tabindex="0"
      type="button"
      aria-label="Scroll Down"
      class="down-icon ${userScrolled ? 'show' : ''}"
      @click="${() => eventBus.emit(`${dataId}-scrollDown`)}"
    >
      ${downIcon}
    </button>
  `;
}
customElements.define('scroll-down', component(ScrollDown));
