import { css } from 'lit-element';

export const textSelectStyles = css`
  .widget-select {
    background-color: var(--widget-background-color);
    border-radius: var(--widget-select-border-radius);
  }

  .widget-option {
    align-items: center;
    display: flex;
    border-bottom: var(--widget-option-border-bottom);
    border: 2px solid transparent;
  }

  .widget-option.selected {
    background: var(--option-selected-background);
  }

  .widget-option-progress {
    bottom: calc(var(--spacer) / 2);
    left: calc(var(--spacer) / 2);
    position: absolute;
    right: calc(var(--spacer) / 2);
    text-align: right;
    top: calc(var(--spacer) / 2);
  }

  .widget-option-description {
    font-size: var(--theme-widget-option-description-font-size);
    font-weight: var(--theme-widget-option-description-font-weight);
    margin: var(--theme-widget-option-description-margin);
    flex-grow: 1;
    padding: var(--spacer);
    position: relative;
    z-index: 10;
  }
  .image-description {
    padding: 0.65rem 1rem;
  }
  .widget-option-percentage {
    font-size: var(--theme-widget-option-percentage-font-size);
    font-weight: var(--theme-widget-option-percentage-font-weight);
    padding-right: var(--spacer);
    position: relative;
    z-index: 1;
  }
`;
