import { html } from 'lit-element';

export const bylinesStyles = html`
  <style>
    .bylines-wrapper {
      display: var(--bylines-wrapper-display);
      justify-content: var(--bylines-wrapper-justify-content);
      margin: var(--bylines-wrapper-margin);
    }

    .widget-author-container {
    }
    .widget-author-name {
      margin: var(--widget-author-name-margin);
      color: var(--widget-author-name-color);
      font-size: var(--widget-author-name-font-size);
    }
    .widget-author-image {
      height: var(--widget-author-image-height);
      border-radius: var(--widget-author-image-border-radius);
    }
    .widget-timestamp-container {
      display: var(--widget-timestamp-container-display);
      align-items: var(--widget-timestamp-container-align-items);
      height: var(--widget-timestamp-container-height);
    }
    .widget-timestamp-time {
      color: var(--widget-timestamp-time-color);
    }
  </style>
`;
