import { generateProfile } from './generateProfile';
import { getMessageList } from './messageList';
import { getProfile } from './getProfile';
import { init } from './init';
import { getMessageCount } from './messageCount';
import { messageCount } from './messageCount';
import { messageList } from './messageList';
import {
  messageHandler,
  addChannelHandler,
  removeChannelHandler,
} from './messageHandler';
import {
  addMessageReaction,
  removeMessageReaction,
  addWidgetReaction,
  removeWidgetReaction,
} from './reactionActions';
import { sendMessage } from './sendMessage';
import { updateProfile } from './updateProfile';
import { updateProfileImageForChatRoom } from './updateProfileImageForChatRoom';

function focusChat() {
  const el = document
    .querySelector('livelike-chat')
    .shadowRoot.querySelector('livelike-chat-composer').shadowRoot;
  return {
    input: () => el.querySelector('textArea').focus(),
    send: () => el.querySelector('.send-icon').focus(),
  };
}
const focusChatInput = () => focusChat().input();
const focusChatSendButton = () => focusChat().send();

export {
  addChannelHandler,
  generateProfile,
  getMessageList,
  getProfile,
  init,
  messageCount,
  getMessageCount,
  messageHandler,
  messageList,
  addMessageReaction,
  removeMessageReaction,
  addWidgetReaction,
  removeWidgetReaction,
  removeChannelHandler,
  sendMessage,
  updateProfile,
  updateProfileImageForChatRoom,
  focusChatInput,
  focusChatSendButton,
};
