import { html } from 'lit-element';
import { component } from 'haunted';
import { chatModerationStyles } from './styles/chatModerationStyles';

const ChatModeration = ({
  item,
  toggleModeration,
  reportUser,
  findBlockedId,
  blockUser,
  animate,
}) => {
  return html`
    ${chatModerationStyles}
    <style>
      .message-options-anchor {
        display: flex;
        align-items: flex-end;
        position: relative;
        z-index: 18;
        transition: var(--message-options-anchor-transition);
        opacity: ${animate};
      }
    </style>
    <div class="message-options-anchor " id="chat_moderation">
      <div class="message-options">
        <button
          aria-label="Block User"
          @click=${() => blockUser(item)}
          class="button block-user ${findBlockedId(item.sender_id)
            ? 'blocked'
            : ''}"
        >
          Block
        </button>
        <button
          class="button"
          aria-label="Report User"
          @click=${() => reportUser(item)}
        >
          Report
        </button>
        <button
          class="cancel-button"
          aria-label="Close message options"
          @click=${() => toggleModeration(item.id)}
        >
          Close
        </button>
      </div>
    </div>
  `;
};

customElements.define('chat-moderation', component(ChatModeration));
