import { getMessages } from './internal/pubnubHandler';
import {
  fetchData,
  typeErr,
  errDocLink,
  isArray,
  separateMessagesRemoveDeleted
} from '../utils';
import { IMessageCount } from '../types';

const isDate = (val: any) =>
  ({
    number: true,
    string: !isNaN(Date.parse(val)),
    object: val instanceof Date && !isNaN(val.getTime()),
  }[typeof val]);

export const messageCount = async (
  roomId: string,
  time?: { since: string | number | Date }
): Promise<IMessageCount | void> => {
  let end: string | number | Date | null = null;
  if (!roomId) return errDocLink('Missing roomId argument.');
  if (typeof roomId !== 'string') return typeErr('roomId', 'String');
  if (time && time.since) {
    if (isDate(time.since)) {
      // Pubnub takes a 17 digit unix time.
      // Add 4 digits, not all zeroes, to create compatible timetoken.
      const unix = new Date(time.since).getTime();
      end = String(unix) + '0011';
    } else return typeErr('since', 'Date');
  }
  const channel = await fetchData.chatRoom(roomId);

  const fetchAllMessages = (end: string | number | Date): Promise<any> => {
    return new Promise(resolve => {
      let arr = [];
      const fetchMessages = async (
        end: string | number | Date,
        start: number | string
      ) => {
        const opts = { end, limit: 100, start };

        const { channels } = await getMessages(channel, opts);
        const messages = channels[channel];
        if (arr && messages && isArray(arr) && isArray(messages)) {
          arr = [...arr, ...messages];
          messages.length === 100 || messages.length === 25
            ? fetchMessages(end, messages[0].timetoken)
            : resolve(arr);
        } else resolve([]);
      };
      fetchMessages(end, null);
    });
  };

  const messages = await fetchAllMessages(end);

  const cleanedList = separateMessagesRemoveDeleted(messages);

  return {
    roomId,
    count: cleanedList && cleanedList.length,
  };
};

export const getMessageCount = (roomId: string, { since }: any) =>
  messageCount(roomId, { since });
