import { css } from 'lit-element';

export const baseSliderStyles = css`
  .widget-header {
    color: var(--image-silder-header-text-color);
    background-color: var(--image-slider-primary-color);
    background-image: linear-gradient(
      90deg,
      var(--image-slider-secondary-color) 0%,
      var(--image-slider-primary-color) 100%
    );
  }
`;
