import { css } from 'lit-element';

export const progressStyles = css`
  :host {
    display: block;
  }

  :host([hidden]) {
    display: none;
  }
  // ie and edge
  livelike-progress {
    display: block;
  }
  livelike-progress[hidden]) {
    display: none;
  }

  .progress {
    height: var(--progress-height);
    overflow: hidden;
    position: var(--progress-position);
    width: var(--progress-width);
    padding: var(--progress-padding);
    box-sizing: var(--progress-box-sizing);
  }

  .progress .progress-meter {
    background: var(--progress-meter-background);
    border-radius: var(--progress-meter-border-radius);
    bottom: 0;
    height: var(--progress-meter-height);
    position: var(--progress-meter-position);
    top: 0;
    transition: width 0.3s;
    width: var(--progress-meter-width);
    z-index: 0;
  }
`;
