import { messageAction } from './internal/pubnubHandler';
import { getChannel, request } from '../utils';
import { programs, userProfile } from './internal/globals';

const getMessage = messageId =>
  document
    .querySelector('livelike-chat')
    // @ts-ignore
    .messageList.find(m => m.id === messageId);

export const addMessageReaction = (roomId, messageId, reaction, cb) => {
  const message = getMessage(messageId);
  const channel = programs.channels[roomId];
  const add = channel =>
    messageAction.add(
      channel,
      message.timetoken,
      {
        type: 'reaction',
        value: reaction.id,
      },
      cb ? cb() : () => {}
    );
  channel ? add(channel) : getChannel(roomId).then(channel => add(channel));
};

export const removeMessageReaction = (roomId, messageId, reaction, cb) => {
  const message = getMessage(messageId);
  const channel = programs.channels[roomId];
  const found = message.rc[reaction.id].find(r => r.uuid === userProfile.id);
  const remove = channel =>
    messageAction.remove(
      channel,
      message.timetoken,
      found.actionTimetoken,
      cb ? cb() : () => {}
    );

  if (found) {
    channel
      ? remove(channel)
      : getChannel(roomId).then(channel => remove(channel));
  }
};

export const addWidgetReaction = (widget, reactionId, cb) => {
  const reaction = widget.reactions.find(r => r.id === reactionId);
  request
    .post({ url: reaction.react_url })
    .then(response => response && cb && cb(response));
};

export const removeWidgetReaction = (widget, reactionId, cb) => {
  const reaction = widget.reactions.find(r => r.id === reactionId);
  request
    .delete({ url: reaction.react_url })
    .then(response => response && cb && cb(response));
};
