import { html } from 'lit-element';

export const chatModerationStyles = html`
  <style>
    .message-options {
      position: absolute;
      z-index: 10;
      right: 2rem;
      display: flex;
      flex-direction: column;
      padding: var(--message-options-padding);
      background: var(--message-options-background);
      width: var(--message-options-width);
      border: var(--theme-message-options-border);
      border-radius: var(--theme-message-options-border-radius);
    }

    .message-options button {
      font-size: var(--message-options-button-font-size);
      background: var(--message-options-button-background);
      color: var(--message-options-button-color);
      border: var(--message-options-button-border);
      padding: var(--message-options-button-padding);
      transition: var(--message-options-button-transition);
    }

    .message-options > button:first-child {
      border-radius: var(--message-options-button-border-radius-top);
    }

    .message-options > button:last-child {
      border-radius: var(--message-options-button-border-radius-bottom);
    }

    .message-options button:hover {
      background: var(--message-options-button-hover-background);
      color: var(--message-options-button-hover-color);
      border: none;
      transition: var(--message-options-button-transition);
      cursor: pointer;
    }

    .block-user.blocked {
      color: gray;
      cursor: default;
    }
  </style>
`;
