import { html } from 'lit-element';

export const widgetReactionStyles = html`
  <style>
    .reactions-container {
      position: var(--widget-reaction-container-position);
      margin: var(--widget-reaction-container-margin);
    }
    .reactions-button-mini {
      display: flex;
    }
    .reaction-button {
      border: var(--widget-reaction-button-border);
      border-radius: var(--widget-reaction-button-border-radius);
      background: var(--widget-reaction-button-background);
      padding: var(--widget-reaction-button-padding);
      bottom: 0;
    }
    .reaction-button:hover,
    .reaction-button:focus {
      cursor: pointer;
      background: var(--widget-reaction-button-hover-background);
    }
    .reaction-button > svg {
      fill: var(--widget-reaction-button-color);
      height: 13px;
      width: 13px;
    }
    .reaction-button:hover > svg,
    .reaction-button:focus > svg {
      fill: var(--widget-reaction-button-hover-color);
    }
    .reaction-item-container {
      position: absolute;
      display: flex;
      transform: var(--widget-reaction-item-container-transform);
      bottom: 0;
      margin: var(--widget-reaction-item-container-transform);
      transition: opacity ease 0.3s;
      opacity: 0;
      z-index: 25;
    }
    .reaction-item-wrap {
      position: relative;
      display: inline-block;
      padding: 0 8px;
      height: 45px;
      border-radius: 12px;
      min-height: inherit;
      width: inherit;
      background: var(--widget-reaction-wrap-background);
      box-shadow: var(--widget-reaction-wrap-box-shadow);
    }
    .reaction-item {
      display: inline-block;
      width: 40px;
      height: 36px;
      background: var(--widget-reaction-item-background);
      border-radius: 12px;
      border: none;
      margin: 4.5px -2px;
      cursor: pointer;
      padding: 0;
    }
    .reaction-item.selected,
    .reaction-item:hover,
    .reaction-item:focus {
      background: var(--widget-reaction-item-hover-background);
    }
    .reaction-image {
      width: 30px;
      height: 30px;
      position: relative;
      margin: 3px 5px;
      padding: 0;
    }
    .reaction-count {
      color: var(--widget-reaction-count-color);
      transform: translate(12px, 3px);
      position: absolute;
      top: 0;
    }
    .mini-reactions {
      display: flex;
    }
    .mini-reaction-image {
      height: 16px;
      width: 16px;
      vertical-align: middle;
    }
    .mini-reaction-count {
      color: white;
    }
  </style>
`;
