import { _$$, _config, programs } from '../scripts/internal/globals';
import { htmlCloseIcon } from '../icons';
import { request } from './requests';
// import { polyfill } from './polyfill';

/**
 * Converts ISO8601 format duration into an object (PYYYYMMDDThhmmss)
 * Example of duration - P3Y6M4DT12H30M5S
 * @param {string} duration Time duration in ISO8601 format
 * @returns {object} The date object converted into a key value pair of {o: <Number>, y: <Number>, m: <Number>, etc.}
 */
const convertDuration = duration => {
  const regex = /(-)?P(?:([.,\d]+)Y)?(?:([.,\d]+)M)?(?:([.,\d]+)W)?(?:([.,\d]+)D)?(?:T(?:([.,\d]+)H)?(?:([.,\d]+)M)?(?:([.,\d]+)S)?)?/;
  const matches = duration.match(regex);
  const durObj = {
    o: matches[1] === undefined ? '+' : '-',
    y: matches[2] === undefined ? 0 : matches[2],
    m: matches[3] === undefined ? 0 : matches[3],
    w: matches[4] === undefined ? 0 : matches[4],
    d: matches[5] === undefined ? 0 : matches[5],
    h: matches[6] === undefined ? 0 : matches[6],
    mm: matches[7] === undefined ? 0 : matches[7],
    s: matches[8] === undefined ? 0 : matches[8],
  };
  return durObj;
};

export const getMilliseconds = duration => {
  const durObj = convertDuration(duration);
  const { s, mm } = durObj;
  const num = mm > 0 ? mm : s;
  const stripZeros = num.replace(/^0+/, '');
  const numConvert = Number(stripZeros);
  const multi = mm > 0 ? 60000 : 1000;
  return numConvert * multi;
};

export const insertDismissButton = w => {
  // Needed because Social Embed widget does not use a shadowRoot because embedded sdk's weren't working.
  const selector = w.shadowRoot || w;
  const btn = document.createElement('button');
  btn.setAttribute('class', 'widget-dismiss');
  btn.setAttribute('aria-label', 'Dismiss');
  btn.onclick = () => w.dismiss();
  btn.style.background = 'none'; // IE and Edge compat
  btn.style.border = 'none'; // IE and Edge compat
  const icon = document.createElement('div');
  icon.innerHTML = htmlCloseIcon;
  btn.append(icon);
  const header = selector.querySelector('#widgetHeader');
  header && header.insertAdjacentElement('beforeend', btn);
};

export const insertBylines = (w, widgetPayload) => {
  const widgetSel = w.shadowRoot ? w.shadowRoot : w;
  const wrapper = document.createElement('bylines-wrapper');
  if (el.authors) {
    wrapper.hasAuthor = true;
    wrapper.name = widgetPayload.created_by.name;
    wrapper.timeFormat = el.timeFormat;
    wrapper.langCode = el.langCode;
  }
  if (el.timestamps) {
    wrapper.hasTimestamp = true;
    wrapper.publishedTime = widgetPayload.published_at;
  }
  const firstChild = widgetSel.firstElementChild;
  widgetSel.insertBefore(wrapper, firstChild);
};

export const immuteAppend = (arr, v) => {
  const newArr = arr.slice();
  newArr.splice(arr.length, 0, v);
  return newArr;
};

export const isObjectEmpty = obj =>
  Object.entries(obj).length === 0 && obj.constructor === Object;

export const debounce = (callback, time = 250, interval) => (...args) =>
  clearTimeout(interval, (interval = setTimeout(callback, time, ...args)));

export const throttle = (fn, wait) => {
  let previouslyRun, queuedToRun;
  return function invokeFn(...args) {
    const now = Date.now();
    queuedToRun = clearTimeout(queuedToRun);
    if (!previouslyRun || now - previouslyRun >= wait) {
      fn.apply(null, args);
      previouslyRun = now;
    } else {
      queuedToRun = setTimeout(
        invokeFn.bind(null, ...args),
        wait - (now - previouslyRun)
      );
    }
  };
};

export const hasSyncStrategy = syncStrat => {
  if (!syncStrat) return false;
  if (isObjectEmpty(syncStrat)) return false;
  return true;
};

export const outsideClick = {
  create: dataId => {
    const chat = document.querySelector('livelike-chat');
    const oc = document.createElement('div');
    oc.style =
      'height: 100%; width: 100%; position: fixed; top: 0; left: 0; z-index: 20;';
    oc.onclick = function() {
      eventBus.emit(`${dataId}-outsideClicked`);
    };
    oc.setAttribute('class', 'outside-click');
    oc.setAttribute('id', dataId);
    chat && chat.insertAdjacentElement('afterend', oc);
  },
  remove: () => {
    const oc = document.querySelector('.outside-click');
    oc && oc.parentNode.removeChild(oc);
  },
};

export const pageVisHandler = () => {
  _$$.pageVisible = !document.hidden;
  document.addEventListener(
    'visibilitychange',
    () => {
      _$$.pageVisible = !document.hidden;
      eventBus.emit('pageVisibilityChange', { pageVisible: !document.hidden });
    },
    false
  );
};

export const isArray = v =>
  Object.prototype.toString.call(v) === '[object Array]';

export const formatDateTime = (val, format, langCode) =>
  new Date(val).toLocaleDateString(langCode, format);

export const getLang = node =>
  new Promise(res => {
    const checkParents = node => {
      const parent = node.parentNode;
      if (parent) {
        parent.lang ? res(parent.lang) : checkParents(parent);
      } else {
        res(null);
      }
    };
    node.lang ? res(node.lang) : checkParents(node);
  });

export const separateMessagesRemoveDeleted = messages => {
  if (messages && isArray(messages)) {
    const messageList = [];
    const updatedList = [];

    messages.map(m => {
      m.message.payload.timetoken = m.timetoken;
      m.message.payload.reactions = m.data && m.data.rc ? m.data.rc : {};
      m.message.event === 'message-deleted'
        ? updatedList.push(m.message)
        : messageList.push(m.message.payload);
    });

    updatedList.map(u => {
      const idx = messageList.findIndex(m => m.id === u.payload.id);
      idx !== -1 && u.event === 'message-deleted' && messageList.splice(idx, 1);
    });
    return messageList;
  } else return [];
};

const promiseReduce = (iterable, reducer, initialValue) =>
  new Promise((resolve, reject) => {
    const iterator = iterable[Symbol.iterator]();
    let index = 0;

    const next = async total => {
      const element = iterator.next();
      if (element.done) {
        resolve(total);
        return;
      }
      try {
        const value = await Promise.all([total, element.value]);
        next(reducer(value[0], value[1], index++));
      } catch (error) {
        reject(error);
      }
    };
    next(initialValue);
  });

export const promiseSequence = (iterable, initialValue) =>
  promiseReduce(
    iterable,
    (previousValue, fn) => fn(previousValue),
    initialValue
  );

// ============== SYNC ============== //
export const isReadySync2 = (syncStrat, payload) => {
  return syncStrat.currentTimecode
    ? syncStrat.currentTimecode >= getSyncTimecode(syncStrat, payload)
    : true;
};

export const isReadySync = (syncStrat, payload) => {
  if (syncStrat.currentTimecode) {
    const syncTime =
      payload.program_date_time !== null
        ? new Date(payload.program_date_time)
        : syncStrat.currentTimecode;
    _$$.logger === true && console.log('LIVELIKE sync time', syncTime);
    return syncStrat.currentTimecode >= syncTime;
  } else {
    return true;
  }
};

export const getSyncTimecode = (syncStrat, payload) =>
  payload.program_date_time !== null
    ? new Date(payload.program_date_time)
    : syncStrat.currentTimecode;

// ============== FETCH URL ============== //

export const buildUrl = (path, template) =>
  encodeURI(template.replace(/{(.*?)}/g, path));

export const getData = (url, type) =>
  new Promise((resolve, reject) => {
    return fetch(url)
      .then(response => {
        if (response.status === 404) {
          throw new Error(
            `${type} resource not found. Check ${type} Id.\nRead more: https://www.npmjs.com/package/@livelike/engagementsdk`
          );
        }
        return response.json();
      })
      .then(data => resolve(data));
  });

export const fetchData = {
  program: programId =>
    getData(buildUrl(programId, _$$.programUrlTemplate), 'Program'),
  app: clientId =>
    getData(
      encodeURI(`${_config.endpoint}applications/${clientId}/`),
      'Client'
    ),
  chatRoom: roomId =>
    new Promise(resolve =>
      getData(buildUrl(roomId, _$$.chatUrlTemplate), 'Room').then(r =>
        resolve(r.channels.chat.pubnub)
      )
    ),
};

export const getChannel = roomId =>
  new Promise(res =>
    fetchData.chatRoom(roomId).then(channel => {
      programs.channels[roomId] = channel;
      res(channel);
    })
  );

export const getReactions = url =>
  new Promise(res =>
    fetch(url)
      .then(r => r.json())
      .then(r => res(r.results))
      .catch(() => res([]))
  );

// ============== EVENTS ============== //

/**
 * Listen to an event
 * eventBus.on('eventName', e => console.log(e))
 * Listen to all events
 * eventBus.on('*', (type, e) => console.log(type, e))
 * Fire event
 * emitter.emit('eventName', { a: 'test' })
 * @param {*} all
 */
function eventEmitter(all) {
  all = all || Object.create(null);
  return {
    on: (type, handler) => (all[type] || (all[type] = [])).push(handler),
    off: (type, handler) =>
      all[type] && all[type].splice(all[type].indexOf(handler) >>> 0, 1),
    emit: (type, evt) => {
      (all[type] || []).slice().map(handler => handler(evt));
      (all['*'] || []).slice().map(handler => handler(type, evt));
    },
  };
}

export const eventBus = eventEmitter();

export const eventDispatcher = (el, evt, obj) =>
  el.dispatchEvent(
    new CustomEvent(evt, { detail: obj, bubbles: true, composed: true })
  );

export function callbackEvent(message, channel, event) {
  eventBus.emit(channel, { message, event });
}

// ============== ERRORS ============== //

export const typeErr = (v, type) =>
  console.error(`${v} must be of type <${type}>`);

export const errDocLink = (s, path = '') =>
  console.error(
    s,
    ' Read more: https://www.npmjs.com/package/@livelike/engagementsdk#' + path
  );

export const selfChildErr = el =>
  console.error(
    el.localName,
    'is a child element of',
    el.parentElement.localName,
    '\nParent Element: ',
    el.parentElement,
    '\nChild Element: ',
    el
  );
// ============== LOCALSTORAGE ============== //

export const saveLocalStorage = (key, data) =>
  localStorage.setItem(key, JSON.stringify(data));

export const loadLocalStorage = key => JSON.parse(localStorage.getItem(key));

export const appendLSBase = (obj, key, value) => {
  let item = JSON.parse(localStorage.getItem(obj)) || {};
  item[key] = value;
  localStorage.setItem(obj, JSON.stringify(item));
};

export const appendLocalStorage = (key, value) =>
  appendLSBase(_$$.lsKey, key, value);

