import { html } from 'lit-element';

export const loadMoreWidgetsStyles = html`
 
<style>
    .loadmore-widget-button {
      display: var(--load-more-button-display);
      justify-content: var(--load-more-button-justify-content);
      width: var(--load-more-button-width);
      font-size: var(--load-more-button-font-size);
      height: var(--load-more-button-height);
      border: var(--load-more-button-border);
      border-radius: var(--load-more-button-border-radius);
      background: var(--load-more-button-background);
      color: var(--load-more-button-color);
      padding: var(--load-more-button-padding);
      margin: var(--load-more-button-margin);
      /* display: flex;
      justify-content: center;
      width: 100%;
      font-size: 1rem;
      border: none;
      border-radius: 0.25rem;
      background: #086fac;
      color: #fff;
      padding: 0.5rem 2rem;
      margin: 1rem 0 0 0; */
    }

    .loadmore-widget-button:hover {
      cursor: pointer;
    }

    .loading-spinner {
      position: relative;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      border: 4px solid rgba(255, 255, 255, 0.1);
      border-top: 4px solid #fff;
      animation: rotating 1.2s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);
    }

    @keyframes rotating {
      100% {
        transform: rotate(360deg);
      }
    }
  </style>
`;
