import { typeErr, errDocLink } from '../utils';
import { programs } from './internal/globals';

export const updateProfileImageForChatRoom = ({ imageUrl, roomId }) => {
  if (!imageUrl) return errDocLink('Missing imageUrl argument');
  if (!roomId) return errDocLink('Missing roomId argument.');
  if (typeof imageUrl !== 'string') return typeErr('imageUrl', 'String');
  if (typeof roomId !== 'string') return typeErr('roomId', 'String');
  if (!programs[roomId])
    return errDocLink('The app must be initialized before updating the user.');
  programs[roomId].image_url = imageUrl;
};
