import { html } from 'lit-element';
import { component } from 'haunted';
import { modalStyles } from './styles/modalStyles';

const Modal = ({ messageType }) => {
  const getMessage = messageType =>
    ({
      block: 'You will no longer see messages from this user.',
      report: 'The message has been reported to the moderators. Thank you.',
      error: 'There was an error. Please try again.',
    }[messageType]);

  return html`
    ${modalStyles}
    <div class="livelike-modal ${messageType !== '' ? 'showing' : null}">
      <div class="modal-body">${getMessage(messageType)}</div>
    </div>
  `;
};

customElements.define('livelike-modal', component(Modal));
