import { generateProfile } from './generateProfile';
import { _$$, userProfile } from './internal/globals';
import { IProfileAuth, IUserProfile } from '../types';
import {
  request,
  fetchData,
  errDocLink,
  saveLocalStorage,
  loadLocalStorage,
} from '../utils';

/**
 * Gets user profile. If accessToken is passes, user profile is fetched using the accessToken
 * If user profile is saved in localStorage, user profile is returned from localStorage
 * If no accessToken or localStorage, new user profile is generated and returned.
 * @param {string} [accessToken]
 * @returns {handler}
 */
/**
 * @typedef handler
 * @type {Promise}
 * @property {object} profile The user profile object
 */

// accesstoken and clientId are optional, necessary if getProfile used before init() runs.
export const getProfile = (
  args?: IProfileAuth
): Promise<IUserProfile | void> =>
  new Promise(res => {
    const accessToken = (args && args.accessToken) || null;
    const clientId = (args && args.clientId) || null;
    const nickName = args && args.nickName ? { nickName: args.nickName } : null;
    const loaded = _$$.lsKey || _$$.profileUrl;
    if (!loaded && !accessToken && !clientId)
      return errDocLink(
        'Missing accessToken and clientId. Either run LiveLike.init() first, or pass arguments.'
      );

    let profile = loadLocalStorage(_$$.lsKey);
    function p(profile) {
      !profile.access_token && (profile.access_token = accessToken);
      profile.id === userProfile.id && saveLocalStorage(_$$.lsKey, profile);
      profile.detail && console.warn(profile.detail);
      Object.assign(userProfile, profile);
      res(profile);
    }
    if (accessToken) {
      _$$.profileUrl
        ? request.get({ url: _$$.profileUrl, accessToken }).then(p)
        : fetchData
            .app(clientId)
            .then(app =>
              request.get({ url: app.profile_url, accessToken }).then(p)
            );
    } else if (profile) {
      p(profile);
    } else {
      generateProfile(nickName).then(p);
    }
  });
