import { Widget } from '../base/Widget';
import { Voteable } from '../base/Voteable';
import { baseQuizWidgetStyles } from './styles/baseQuizWidgetStyles';
import { votingWidgetStyles } from '../sharedStyles/votingWidgetStyles';
import { textSelectStyles } from '../sharedStyles/textSelectStyles';

export class QuizWidgetBase extends Voteable(Widget) {
  static get styles() {
    return [
      super.styles,
      baseQuizWidgetStyles,
      votingWidgetStyles,
      textSelectStyles,
    ];
  }
  static get properties() {
    return {
      question: { type: String },
      choices: { type: Array },
      vote: { type: Object },
    };
  }
  firstUpdated() {
    this.initialTimelineLoad
      ? this.showTimelineVotes()
      : this.waitForVotes(this.widgetPayload)
          .then(() => this.voteResults())
          .then(() => this.displayPoints())
          .then(() => this.displayBadge())
          .then(this.shouldExpire);
  }
}
