import { html } from 'lit-element';

export const scrollDownStyles = html`
  <style>
    @keyframes slideDown {
      from {
        opacity: 0;
        transform: translate3d(0, -3rem, 0);
      }
      to {
        opacity: 1;
        transform: translate3d(0, 0rem, 0);
      }
    }

    @keyframes slideUp {
      from {
        opacity: 0;
        transform: translate3d(0, 0rem, 0);
      }
      to {
        opacity: 1;
        transform: translate3d(0, -2rem, 0);
      }
    }

    .down-icon {
      z-index: 15;
      position: absolute;
      border: none;
      right: 3.75rem;
      bottom: 5.5rem;
      padding: 0;
      border-radius: var(--down-icon-border-radius);
      height: auto;
      width: auto;
      display: none;
      flex-direction: column;
      animation: slideDown 0.5s ease forwards;
      transition: all 0.3s ease-out;
    }
    .down-icon.show {
      display: flex;
      animation: slideUp 0.5s ease forwards;
    }
    .down-icon:hover {
      cursor: pointer;
      transition: all 0.3s ease-out;
    }
    .down-icon:hover svg > rect {
      fill: var(--down-icon-hover-background);
    }
    .down-icon:hover svg > path {
      fill: var(--down-icon-hover-color);
    }
    .down-icon svg > rect {
      fill: var(--down-icon-background);
    }
    .down-icon svg > path {
      fill: var(--down-icon-color);
    }
  </style>
`;
