import { html } from 'lit-element';

export const messageReactionStyles = html`
  <style>
    /* Reactions*/
    .reaction-button-container {
      margin: var(--message-reaction-button-container-margin);
    }
    .reaction-button {
      border: var(--message-reaction-button-border);
      border-radius: var(--message-reaction-button-border-radius);
      background: var(--message-reaction-button-background);
      position: var(--message-reaction-button-position);
      right: 0;
      padding: var(--message-reaction-button-padding);
      transform: var(--message-reaction-button-transform);
      bottom: 0;
    }
    .reaction-button:hover {
      cursor: pointer;
      background: var(--message-reaction-button-hover-background);
    }
    .reaction-button > svg {
      fill: var(--message-reaction-button-color);
      height: 13px;
      width: 13px;
    }
    .reaction-button:hover > svg {
      fill: var(--message-reaction-button-hover-color);
    }
    .reaction-item-container {
      position: absolute;
      display: flex;
      bottom: 0;
      margin: 0 0 0 0.5rem;
      transition: opacity ease 0.3s;
      opacity: 0;
      z-index: 25;
    }
    .reaction-item-wrap {
      position: relative;
      display: inline-block;
      padding: 0 8px;
      height: 45px;
      border-radius: 12px;
      min-height: inherit;
      width: inherit;
      background: var(--message-reaction-wrap-background);
      box-shadow: var(--message-reaction-wrap-box-shadow);
    }
    .reaction-item {
      display: inline-block;
      width: 40px;
      height: 36px;
      background: var(--message-reaction-item-background);
      border-radius: 12px;
      border: none;
      margin: 4.5px -2px;
      cursor: pointer;
      padding: 0;
    }
    .reaction-item.selected,
    .reaction-item:hover,
    .reaction-item:focus {
      background: var(--message-reaction-item-hover-background);
    }
    .reaction-image {
      width: 30px;
      height: 30px;
      position: relative;
      margin: 3px 5px;
      padding: 0;
    }
    .reaction-count {
      color: var(--message-reaction-count-color);
      transform: translate(12px, 3px);
      position: absolute;
      top: 0;
    }
    .mini-reactions {
      transform: translate(10px, -5px);
      display: flex;
      position: absolute;
      right: 0;
    }
    .mini-reaction-image {
      height: 16px;
      width: 16px;
      margin-left: -5px;
      vertical-align: middle;
    }
    .mini-reaction-count {
      color: var(--message-reaction-total-count-color);
    }
  </style>
`;
