import { html } from 'lit-element';

export const pointsDisplayStyles = html`
  <style>
    .points-display-container {
      display: flex;
      flex-direction: column;
      z-index: 10000;
    }

    .new-points-wrap {
      display: flex;
      justify-content: center;
      margin-bottom: 0.75rem;
    }

    .plus-points {
      font-size: 16px;
      color: #fff;
      text-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
      font-weight: bold;
    }

    .coin-icon {
      display: var(--points-coin-display);
    }

    .points-progress-wrap {
      display: flex;
    }

    .points-progress {
      width: 100px;
      height: 1rem;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      border-top-left-radius: 1.25rem;
      border-bottom-left-radius: 1.25rem;
      font-size: 0.75rem;
      line-height: 0.625rem;
      background-color: #f5f5f6;
      box-shadow: inset 0 0.1rem 0.1rem rgba(90, 97, 105, 0.15);
      display: -ms-flexbox;
      display: flex;
      overflow: hidden;
    }

    .points-progress-bg-primary {
      background-color: #007bff !important;
    }

    .points-number-percent {
      position: absolute;
      transform: translate(26px, 3.5px);
    }
    .points-progress-bar {
      border-radius: 8px;
      height: 1rem;

      line-height: 0.625rem;
      color: #fff;
      background-color: #007bff;
      transition: width 0.6s ease;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
      flex-direction: column;
      -ms-flex-pack: center;
      justify-content: center;
      text-align: center;
      white-space: nowrap;
    }

    .points-badge-image {
      transform: translate(-15px, -8px);
      height: 2rem;
      transition: all ease 0.3s;
    }
  </style>
`;
