import {
  request,
  eventDispatcher,
  insertDismissButton,
  insertBylines,
} from './utils';
import {
  getMessages,
  connect,
  disconnect,
} from './scripts/internal/pubnubHandler';

export function WidgetController(el, adapter) {
  let widgetNodes = new Map();
  let predictionVotes = new Map();
  let widgetShowing = false;
  let updating = false;

  const updateWidget = () => {
    if (!updating) {
      updating = true;
      function update() {
        if (el.widgetSession.hasNextItem()) {
          el.widgetSession.isItemReady() && widgetShowing === false
            ? showWidget(false, false).then(() => update())
            : setTimeout(() => update(), 1000);
        } else {
          updating = false;
        }
      }
      update();
    }
  };

  const loadWidgets = fromCache => {
    if (el.widgetSession.hasNextItem()) {
      showWidget(true, fromCache);
      loadWidgets(fromCache);
    }
    el.timelineLoaded = true;
    return Promise.resolve(true);
  };

  const getTimeline = url =>
    new Promise(resolve => {
      el.timelineLoaded = false;
      fetch(url, { method: 'GET' })
        .then(r => {
          if (r.headers) {
            const age = Number(r.headers.get('age'));
            if (age && age > 0) {
              const end = String(new Date().getTime() - age * 1000) + '0111';
              getMessages(el.channel, { end }).then(r => {
                r.channels &&
                  r.channels[el.channel] &&
                  r.channels[el.channel].map(m =>
                    el.widgetSession.queueArray.push(m.message.payload)
                  );
                loadWidgets(true);
              });
            }
          }
          return r.json();
        })
        .then(response => {
          const widgets = response.results;
          widgets.map(widget => el.widgetSession.queueArray.push(widget));
          loadWidgets(false).then(loaded =>
            resolve({ loaded, nextPage: response.next })
          );
        });
    });
  // Show Widget Lifecycle

  const createWidgetNode = (widgetPayload, initialLoad, fromCache) =>
    new Promise(res => {
      createWidget(widgetPayload, initialLoad).then(node =>
        adapter
          .beforeWidgetAttached({ element: node, widget: widgetPayload })
          .then(() => {
            widgetNodes.set(widgetPayload.id, node);
            if (el.mode === 'timeline') {
              !initialLoad || fromCache
                ? el.insertAdjacentElement('afterbegin', node)
                : el.appendChild(node);
            } else {
              el.appendChild(node);
            }
            res(node);
          })
      );
    });

  const widgetDidShow = (node, widgetPayload, initialLoad) => {
    const obj = { element: node, widget: widgetPayload };
    !initialLoad && eventDispatcher(el, 'widgetattached', obj);
    node &&
      node.liveListener &&
      !initialLoad &&
      connect({
        channel: widgetPayload.subscribe_channel,
        listener: node.liveListener,
      });
    return adapter.widgetAttached(obj);
  };

  const widgetWillShow = (widgetPayload, initialLoad) => {
    !initialLoad &&
      eventDispatcher(el, 'beforewidgetattached', { widget: widgetPayload });
    return Promise.resolve();
  };

  const showWidget = (initialLoad, fromCache) => {
    el.mode !== 'timeline' && (widgetShowing = true);
    const widgetPayload = el.widgetSession.dequeueItem();
    request.post({ url: widgetPayload.impression_url });
    return widgetWillShow(widgetPayload, initialLoad).then(() =>
      createWidgetNode(widgetPayload, initialLoad, fromCache).then(node =>
        widgetDidShow(node, widgetPayload, initialLoad)
      )
    );
  };

  // Hide Widget Lifecycle
  const destroyWidgetNode = (node, widgetPayload) => {
    if (node && node.parentNode) {
      node.parentNode.removeChild(node);
      widgetNodes.delete(widgetPayload.id);
    }
    return Promise.resolve();
  };

  const widgetDidHide = (node, widgetPayload) => {
    const obj = { element: node, widget: widgetPayload };
    eventDispatcher(el, 'widgetdetached', obj);
    node &&
      node.liveListener &&
      disconnect({
        channel: widgetPayload.subscribe_channel,
        listener: node.liveListener,
      });
    return adapter.widgetDetached(obj);
  };

  const widgetWillHide = widgetPayload => {
    const node = widgetNodes.get(widgetPayload.id);
    const obj = { element: node, widget: widgetPayload };
    eventDispatcher(el, 'beforewidgetdetached', obj);
    return adapter.beforeWidgetDetached(obj).then(() => Promise.resolve(node));
  };

  const hideWidget = widgetPayload =>
    widgetWillHide(widgetPayload).then(node =>
      destroyWidgetNode(node, widgetPayload).then(() =>
        widgetDidHide(node, widgetPayload).then(
          () => el.mode !== 'timeline' && (widgetShowing = false)
        )
      )
    );

  const createWidget = (widgetPayload, initialLoad) => {
    const preds = ['text-prediction', 'image-prediction'];
    const followups = [
      'text-prediction-follow-up',
      'image-prediction-follow-up',
    ];
    const contains = arr => arr.some(v => v === widgetPayload.kind);

    let w: any = document.createElement('livelike-' + widgetPayload.kind);
    for (let prop in widgetPayload) {
      w[prop] = widgetPayload[prop];
    }
    w.mode = el.mode;
    w.widgetPayload = widgetPayload;
    w.reactions = el.reactions;
    w.addEventListener('expire', (e: any) => hideWidget(e.detail.widget));
    w.addEventListener('dismiss', (e: any) => hideWidget(e.detail.widget));
    if (initialLoad) {
      w.initialTimelineLoad = true;
      w.disabled = true;
    }
    // Saves the prediction vote to the Map so it can be used when a follow up widget gets posted some time in the future.
    contains(preds) &&
      w.addEventListener('recordPrediction', e =>
        predictionVotes.set(widgetPayload.follow_ups[0].id, e.detail.option)
      );
    // Sets the selectedOption ad the prediction vote that was saved to the map.
    contains(followups) &&
      (w.selectedOption = predictionVotes.get(widgetPayload.id));

    // TODO Removing this timeout causes firstChild to return 'null'
    // and bylines won't get added. Working with a 0 as the time delay is
    // most likely an issue with call stack ordering.
    setTimeout(() => {
      if (el.mode === 'timeline') {
        if (el.authors || el.timestamps) {
          insertBylines(w, widgetPayload);
        }
      } else {
        insertDismissButton(w);
      }
    }, 0);
    return Promise.resolve(w);
  };

  return {
    getTimeline,
    updateWidget,
    showWidget,
    hideWidget,
    widgetNodes,
    predictionVotes,
    widgetShowing,
  };
}
