import { html } from 'lit-element';

export const modalStyles = html`
  <style>
    .livelike-modal {
      color: var(--message-moderation-modal-color);
      padding: var(--message-moderation-modal-padding);
      background: var(--message-moderation-modal-background);
      border-radius: var(--message-moderation-modal-border-radius);
      position: var(--message-moderation-modal-position);
      z-index: var(--message-moderation-modal-zIndex);
      width: var(--message-moderation-modal-width);
      margin: var(--message-moderation-modal-margin);
      top: var(--message-moderation-modal-top);
      left: var(--message-moderation-modal-left);
      transform: var(--message-moderation-modal-transform);
      opacity: var(--message-moderation-modal-opactiy);
    }
    .livelike-modal.showing {
      transition: var(--message-moderation-modal-transition);
      transform: var(--message-moderation-modal-transform-showing);
      opacity: var(--message-moderation-modal-opactiy-showing);
    }
  </style>
`;
