import { html } from 'lit-element';
import { QuizWidgetBase } from './QuizWidgetBase';
import '../../components/livelike-option';
import '../../components/livelike-timer';
import '../../components/livelike-select';
import '../../components/livelike-progress';
import { RESULTS } from '../../constants/WidgetEvents';

export class TextQuizWidget extends QuizWidgetBase {
  resultsEventName = RESULTS.TEXT_QUIZ;

  static get styles() {
    return [super.styles];
  }

  render() {
    return html`
      <article id="widget-article" class="widget ${this.articleClass()}">
        <header id="widgetHeader" class="widget-header">
          <h1 class="widget-question">${this.question}</h1>
        </header>
        <livelike-timer id="livelike-timer"></livelike-timer>
        <livelike-select class="widget-select" .disabled=${this.disabled}>
          ${this.choices.map(
            choice => html`
              <livelike-option
                class="widget-option
                ${this.selectedClass(choice)}
                ${this.correctClass(choice)}"
                .value=${choice.id}
                .selected=${this.isSelected(choice)}
                @click=${() => this.voteAction(choice)}
              >
                <div class="widget-option-description">
                  ${choice.description}
                </div>
                ${(this.phase !== 'voting'
                  ? html`
                      <div class="widget-option-percentage">
                        ${this.votePercentage(choice.answer_count)}%
                      </div>
                    `
                  : null)}
                <livelike-progress
                  class="widget-option-progress"
                  value=${this.votePercentage(choice.answer_count)}
                ></livelike-progress>
              </livelike-option>
            `
          )}
        </livelike-select>
      </article>
    `;
  }
}

customElements.define('livelike-text-quiz', TextQuizWidget);
