import { html } from 'lit-element';

export const stickerDisplayStyles = html`
  <style>
    @keyframes slideIn {
      from {
        opacity: 0;
        transform: translate3d(0, -1rem, 0);
      }
      to {
        opacity: 1;
        transform: translate3d(0, -0.5rem, 0);
      }
    }

    .sticker-icon {
      position: absolute;
      border: none;
      background: none;
      right: 3rem;
      bottom: var(--sticker-icon-bottom);
      transition: all 0.3s ease-out;
    }
    .sticker-icon:hover {
      cursor: pointer;
    }
    .sticker-icon svg {
      fill: var(--sticker-icon-color);
    }
    .sticker-icon:hover svg,
    .sticker-icon:focus svg {
      filter: var(--sticker-icon-hover-color);
      transition: all 0.3s ease-out;
    }
    .sticker-container {
      position: absolute;
      bottom: 4rem;
      opacity: 0;
      transition: all ease 0.3s;
      margin-right: 3rem;
      z-index: 21;
      right: 0;
      width: var(--sticker-container-width);
      max-width: var(--sticker-container-max-width);
    }
    .sticker-container.show {
      opacity: 1;
      transition: all ease 0.3s;
    }
    .sticker-container.hide {
      opacity: 0;
      transition: all ease 0.3s;
    }
    .sticker-container-header {
      position: relative;
      border-radius: 0.5rem 0.5rem 0 0;
      background: rgba(20, 20, 20, 1);
      padding: 0.625rem 1rem;
      color: #fff;
      font-size: 1rem;
      font-weight: bold;
    }
    .sticker-close-icon {
      border: none;
      background: none;
      position: relative;
      float: right;
      top: -1px;
      right: 0px;
      background-size: contain;
      width: 1.25rem;
      height: 1.25rem;
      cursor: pointer;
    }
    .sticker-body {
      position: relative;
      background: rgba(20, 20, 20, 1);
      padding: 0.625rem 0.3rem;
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      overflow-y: auto;
      min-height: 8rem;
      max-height: 8rem;
    }
    .sticker-wrap {
      border: none;
      color: #fff;
      padding-left: 0 0.625rem;
      width: 3.75rem;
      background: none;
      border-radius: 0.75rem;
      transition: all ease 0.3s;
      margin: 0 auto 0.625rem auto;
    }
    .sticker-wrap:hover,
    .sticker-wrap:focus {
      outline: none;
      background: #818592;
      cursor: pointer;
    }
    .sticker-image {
      width: 100%;
      vertical-align: middle;
    }

    .sticker-nav-container {
      position: relative;
      background: rgba(40, 40, 40, 1);
      height: 40px;
      width: 100%;
      border-radius: 0 0 8px 8px;
      overflow: hidden;
      min-width: 15rem;
    }
    .sticker-nav-wrap {
      display: flex;
      list-style-type: none;
      padding: 0;
      margin: 0;
    }
    .sticker-nav-item {
      border: none;
      background: rgba(255, 255, 255, 0);
      transition: all 0.2s;
      padding: 10px;
    }
    .sticker-nav-item.active {
      background: rgba(20, 20, 20, 1);
      border-radius: 0 0 8px 8px;
    }
    .sticker-nav-item:hover {
      cursor: pointer;
    }
    .sticker-nav-item img {
      width: 20px;
      height: 20px;
      vertical-align: middle;
      border: 0;
    }
  </style>
`;
