import { css } from 'lit-element';

export const alertWidgetStyles = css`
  .widget-header {
    color: var(--alert-header-text-color);
    background-color: var(--alert-primary-color);
    background-image: linear-gradient(
      90deg,
      var(--alert-secondary-color) 0%,
      var(--alert-primary-color) 100%
    );
    padding-right: 1rem;
  }

  .widget {
    border-radius: var(--widget-option-border-radius);
  }

  .no-title {
    padding: 0.5rem;
  }

  .widget-dismiss {
    position: absolute;
    right: 1rem;
    top: 0.375rem;
  }

  .widget-alert-body {
    background-color: var(--widget-background-color);
    border-radius: var(--widget-select-border-radius);
    overflow: hidden;
  }

  .widget-captioned-media {
    align-items: center;
    display: flex;
    margin: 0;
  }

  .widget-caption-container {
    padding: 1rem;
  }

  .widget-caption {
    word-wrap: break-word;
    padding: 0;
    width: 100%;
  }
  .widget-caption.media-caption {
    padding: 1rem;
  }
  .widget-media {
    font-size: 0;
    text-align: center;
  }

  .widget-media img {
    max-height: 90px;
    max-width: 100%;
  }

  .widget-captioned-media .widget-caption {
    order: 1;
  }

  .widget-alert-body .widget-caption ~ .widget-media {
    order: 2;
    max-height: 90px;
    max-width: 35%;
  }

  .widget-link {
    word-wrap: break-word;
    background-color: var(--alert-link-background);
    background-image: url('http://shukuanglin.com/livelike/widgets/images/ic_arrow.svg');
    background-position: center right 0.5rem;
    background-size: 0.5rem;
    background-repeat: no-repeat;
    color: var(--alert-link-text-color);
    display: block;
    font-weight: bolder;
    padding: 0.5rem 1rem;
    position: relative;
    text-decoration: none;
  }
`;
