import { html } from 'lit-element';
import { component } from 'haunted';
import { pointsDisplayStyles } from './styles/pointsDisplayStyles';
import { coinIcon } from '../icons';

const PointsDisplay = ({ userPoints }) => {
  const {
    points,
    total_points,
    new_points,
    current_badge,
    next_badge,
    new_badges,
  } = userPoints;

  let barMax = total_points ? total_points : 0;
  let barCurr = new_points ? new_points : 0;
  let pointsDisplay = total_points;
  let saturation = 1;
  let percent;
  let badgeImage;

  const hasCurrBadge = current_badge && current_badge.id;
  const hasNextBadge = next_badge && next_badge.id;
  const hasBadge = hasCurrBadge || hasNextBadge;

  if (hasCurrBadge) {
    barCurr = current_badge.points + new_points;
    badgeImage = current_badge.file;
  }

  if (hasNextBadge) {
    barMax = next_badge.points;
    badgeImage = next_badge.file;
  }

  if (!current_badge && hasNextBadge) {
    percent = (new_points / next_badge.points) * 100;
  } else if (new_badges && new_badges[0] && new_badges[0].id) {
    percent = 100;
  } else if (hasNextBadge) {
    percent = (new_points / (next_badge.points - current_badge.points)) * 100;
  } else {
    percent = 100;
  }

  if (next_badge && next_badge[0] && next_badge[0].id) {
    saturation = new_badges[0] && new_badges[0].id ? 1 : 0;
    pointsDisplay =
      new_badges[0] && new_badges[0].id
        ? points + ` / ` + points
        : total_points + ` / ` + next_badge.points;
  }

  // console.log(' pointsDisplay', pointsDisplay)
  // console.log('percent', percent)

  return new_points > 0
    ? html`
        ${pointsDisplayStyles}

        <div class="points-display-container">
          <div class="new-points-wrap">
            <div class="coin-icon">${coinIcon}</div>
            <div class="plus-points">+${new_points}</div>
          </div>

          ${hasBadge
            ? html`<div class="points-progress-wrap">
        <div
          class="points-progress"
          role="progressbar"
          aria-valuenow=${barCurr}
          aria-valuemin="0"
          aria-valuemax=${barMax}
        >
          <div class="points-number-percent" style="color:orange;">
            ${pointsDisplay}
          </div>
          <div
            class="points-progress-bar points-progress-bg-primary"
            style="width:${percent}%;color:orange;"
          ></div>
        </div>
        <img
          src=${badgeImage}
          class="points-badge-image"
          style="filter: saturate(${saturation})"
          alt="badge"
        />
      </div>
    </div>`
            : null}
        </div>
      `
    : null;
};

customElements.define('points-display', component(PointsDisplay));
