import { css } from 'lit-element';

export const predictionWidgetStyles = css`
  .widget-option {
    border: 2px solid transparent;
    transition: border ease 0.3s;
  }

  .widget-option.selected {
    border: 2px solid var(--prediction-selected-border-color);
    transition: border ease 0.3s;
  }

  .widget-confirmation {
    align-items: center;
    border-radius: var(--prediction-confirmation-border-radius);
    bottom: var(--spacer);
    display: none;
    left: var(--spacer);
    overflow: hidden;
    justify-content: center;
    padding: 0.25rem 0.5rem;
    position: absolute;
    right: var(--spacer);
    text-align: center;
    top: var(--spacer);
  }

  .widget.phase-confirming {
    position: relative;
  }

  .widget.phase-confirming .widget-header,
  .widget.phase-confirming .widget-select {
    opacity: 0.4;
  }

  .widget.phase-confirming .widget-confirmation {
    display: var(--prediction-confirmation-display);
    background: var(--prediction-confirmation-background);
  }
`;
