import { css } from 'lit-element';

export const timerStyles = css`
  livelike-timer {
    display: block;
  }
  .livelike-timer {
    display: block;
    background: var(--timer-background-color);
    height: var(--timer-height);
    width: 0%;
    transition-timing-function: linear;
    transition-property: width;
  }

  .livelike-timer.started {
    width: 100%;
  }
`;
