import { programs } from './internal/globals';
import { getChannel, errDocLink, typeErr } from '../utils';
import {
  addListener,
  removeListener,
  subscribe,
} from './internal/pubnubHandler';
import { MessageEvents, ReactionEvents } from '../constants/ChatEvents.js';
import { IMessageCallbackStruct } from '../types';

// Removing a listener requires passing the exact same "listener" object
// Passing one in as an argument creates a different reference in memory,
// so the original listner never gets removed. Also, it seems like removing
// a listener and unsubscribing doesn't actually remove the listener,
// So when you re-subscribe, you get all the messages you missed. To get around
// this, the "remove" function just removes the listner, and doesnt unsubscribe.
const TEMP_typeCheck = async (id: string, callback: Function) => {
  if (!id) return errDocLink('Id is missing.');
  if (!callback) return errDocLink('Callback is missing.');
  if (typeof id !== 'string') return typeErr('roomId', 'String');
  if (typeof callback !== 'function') return typeErr('callback', 'Function');
  return true;
};

const getIndex = (group, id, callback) =>
  group[id] &&
  group[id].findIndex(
    (item: { callback: any }) => String(item.callback) === String(callback)
  );

export const messageHandler = () => {
  let group = {};

  const add = (roomId: string, callback: Function) => {
    !group[roomId] && (group[roomId] = []);
    TEMP_typeCheck(roomId, callback).then(typeSafe => {
      if (!typeSafe) return;
      // If it already exists, don't add a duplicate
      const idx = getIndex(group, roomId, callback);
      if (idx !== -1) return;
      const channel = programs.channels[roomId];
      const connect = channel => {
        group[roomId].push({
          roomId,
          listener: {
            message: (msg: IMessageCallbackStruct) => {
              msg.message.payload.timetoken = msg.timetoken;
              msg.channel === channel &&
                callback({
                  message: msg.message.payload,
                  roomId,
                  event: MessageEvents[msg.message.event],
                });
            },
            messageAction: (msg: any) =>
              msg.channel === channel &&
              callback({
                message: msg.data,
                roomId,
                event: ReactionEvents[msg.event],
              }),
          },
          callback,
          channel,
        });
        const addedIdx = getIndex(group, roomId, callback);
        addListener(group[roomId][addedIdx].listener);
        subscribe(channel);
      };

      channel
        ? connect(channel)
        : getChannel(roomId).then(channel => {
            connect(channel);
          });
    });
  };

  const remove = (roomId: string, callback: Function) => {
    TEMP_typeCheck(roomId, callback).then(typeSafe => {
      if (!typeSafe) return;
      const idx = getIndex(group, roomId, callback);
      if (idx === -1) return console.warn('Listener not removed.');
      removeListener(group[roomId][idx].listener);
      // unsubscribe(group[id][idx].channel);
      group[roomId].splice(idx, 1);
      if (group[roomId] && group[roomId].length < 1) delete group[roomId];
    });
  };

  return {
    add,
    remove,
  };
};

export const $$messageHandler = messageHandler();

// For backwards compatibility with older versions of app.
const COMPAT_messageHandler = messageHandler();

// id: string | { roomId: string; roomid: string; programid: string; programId: string }
export const addChannelHandler = (
  id: any | { roomId?: string; roomid?: string },
  callback: Function
) => {
  let rid = id;
  if (typeof id === 'object') {
    rid = id.roomid || id.roomId;
  }
  return COMPAT_messageHandler.add(rid, callback);
};

export const removeChannelHandler = (
  id: any | { roomId?: string; roomid?: string },
  callback: Function
) => {
  let rid = id;
  if (typeof id === 'object') {
    rid = id.roomid || id.roomId;
  }
  return COMPAT_messageHandler.remove(rid, callback);
};
