import { getStickers } from './getStickers';
import { connectionBase } from './connectionBase';
import { subscribe } from './pubnubHandler';
import {
  getChannel,
  request,
  appendLocalStorage,
  getReactions,
} from '../../utils';
import { _$$ } from './globals';

// This document.contains(c) check after the promise is necessary to ensure the element
// doesn't get added to our list of elements in programs[programId/roomId/elId] during the
// creation of the list in connectionBase. This immedaitely returns a promise so that
// chatAutoConnect.then() in the ChatContainer does not error.
//  It either resolves or hangs and never resolves.
export const chatAutoConnect = (c, userProfile) =>
  new Promise(
    res =>
      document.contains(c) &&
      connect(
        c,
        userProfile
      ).then(res)
  );

const connect = async (c, userProfile) => {
  const { dataId, roomId, prog } = await connectionBase(c, 'chatEl');
  c.roomId = roomId;

  const channel = await getChannel(c.elId);
  c.channel = channel;

  if (prog) {
    if (prog.reaction_packs_url) {
      c.chatReactionPacks = await getReactions(prog.reaction_packs_url);
    }

    if (prog.sticker_packs_url) {
      const s: any = await getStickers(prog.sticker_packs_url);
      c.stickerPacks = s.stickerPacks;
      c.stickers = s.stickers;
    }

    prog.rewards_type !== 'none' &&
      prog.rank_url &&
      request.get({ url: prog.rank_url }).then(gaming => {
        userProfile.gaming = gaming;
        appendLocalStorage('gaming', gaming);
      });
  }

  if (!_$$.chatHandlers[dataId]) {
    _$$.chatHandlers[dataId] = channel;
    subscribe(channel);
  }
  return;
};
