import { html } from 'lit-element';

export const messageItemStyles = html`
  <style>
    :host {
      overflow: auto;
    }

    .message-item-container {
      position: var(--message-item-container-position);
      margin: var(--message-item-container-margin);
      padding: var(--message-item-container-padding);
    }

    .message-item-container > .more-icon {
      opacity: var(--message-options-icon-opacity);
      transition: opacity ease 0.3s;
    }

    .message-item-container:hover > .more-icon {
      opacity: var(--message-options-icon-hover-opacity);
      transition: opacity ease 0.3s;
    }

    .more-icon:focus {
      opacity: var(--message-options-icon-hover-opacity);
      transition: opacity ease 0.3s;
    }

    .message-item-container > .more-icon.active {
      opacity: var(--message-options-icon-active-opacity);
    }

    .more-icon.active {
      opacity: var(--message-options-icon-active-opacity);
    }

    .message-wrapper {
      display: var(--message-wrapper-display);
      flex-direction: var(--message-wrapper-flex-direction);
      flex-wrap: var(--message-wrapper-flex-wrap);
      opacity: 1;
      transition: all 0.4s;
      background: var(--message-wrapper-background);
      border-radius: var(--message-wrapper-border-radius);
      border-bottom: var(--message-wrapper-border-bottom);
      padding: var(--message-wrapper-padding);
      width: var(--message-wrapper-width);
      min-height: var(--message-wrapper-min-height);
      min-width: var(--message-wrapper-min-width);
      max-width: var(--message-wrapper-max-width);
      margin: var(--message-wrapper-margin);
      position: var(--message-wrapper-position);
    }
    .message-wrapper:focus{
      outline: none;
    }

    .sender-name {
      font-size: var(--sender-name-font-size);
      font-family: var(--sender-name-font-family);
      display: inline-block;
      color: var(--sender-name-color);
      margin-bottom: var(--sender-name-margin);
    }

    .sender-message {
      font-size: var(--message-font-size);
      font-family: var(--message-font-family);
      display: flex;
      word-break: break-word;
      word-wrap: break-word;
      color: var(--message-color);
      line-height: 1.3;
      padding: 0rem 0.25rem 0.25rem 0;
      flex-wrap: wrap;
    }

    .sender-message.deleted {
      font-style: oblique;
      color: var(--deleted-message-color);
    }

    .word-item {
      align-self: flex-end;
    }

    .image-message-container {
      max-width: 480px;
    }

    .image-message {
      max-width: 100%;
      border-radius: 0.5rem;
    }

    .message-timestamp {
      font-family: var(--message-timestamp-font-family);
      font-size: var(--message-timestamp-font-size);
      color: var(--message-timestamp-color);
      padding: var(--message-timestamp-padding);
      margin: var(--message-timestamp-margin);
    }

    .more-icon {
      background: none;
      border: none;
      margin: var(--message-options-icon-margin);
      display: flex;
      align-items: center;
      padding: var(--message-options-icon-padding);
    }
    .more-icon:hover {
      cursor: pointer;
    }
    .more-icon svg {
      fill: var(--message-options-icon-color);
    }
    .more-icon:hover svg {
      fill: var(--message-options-icon-hover-color);
    }
    .no-message-container {
      height: 100%;
      position: relative;
      display: flex;
      justify-content: center;
    }
    .no-messages {
      margin: 0;
      color: #fff;
      position: absolute;
      top: 50%;
    }
  </style>
`;
