import { html } from 'lit-element';

export const badgeDisplayStyles = html`
  <style>
    .badge-display {
      align-items: center;
      background-color: var(--widget-background-color);
      border-radius: var(--badge-display-border-radius);
      bottom: var(--spacer);
      display: flex;
      flex-direction: column;
      opacity: 0.95;
      height: 100%;
      left: var(--spacer);
      justify-content: center;
      padding: 0.25rem 0.5rem;
      position: absolute;
      right: var(--spacer);
      text-align: center;
      top: var(--spacer);
      z-index: 30;
    }

    .badge-display.show {
      position: relative;
    }

    .widget.phase-confirming .widget-header,
    .widget.phase-confirming .widget-select {
      opacity: 0.2;
    }

    .badge-display.show {
      display: flex;
    }

    .overlay-badge {
      height: 5rem;
      position: absolute;
      top: -40px;
    }
    .overlay-button-container {
      position: absolute;
      width: 100%;
      left: 0;
      bottom: -15px;
    }
    .overlay-button {
      background-image: linear-gradient(
        to right,
        rgb(105, 40, 180),
        rgb(75, 40, 180)
      );
      width: 50%;
      border: none;
      color: white;
      padding: 0.5rem;
      border-radius: 0.25rem;
      font-size: 1rem;
    }

    .overlay-button:hover {
      cursor: pointer;
    }

    .overlay-title {
      font-size: 1rem;
      font-weight: bold;
      text-align: center;
      text-transform: uppercase;
      padding: 10px 16px;
      text-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
      margin-bottom: 0.5rem;
    }
  </style>
`;
